import { Line } from 'vue-chartjs'
import theme from '../scss/_theme.scss'

export default {
  extends: Line,
  components: {
    theme
  },
  props: {
    data: {
      type: Array,
      default: () => []
    },
    now: {
      type: String,
      default: 'Now'
    }
  },
  data() {
    return {
    }
  },
  methods: {
    render() {
      this.gradient = this.$refs.canvas
      .getContext("2d")
      .createLinearGradient(0, 10, 0, 175)
      this.gradient.addColorStop(0, theme.primary500main)
      this.gradient.addColorStop(1, '#fafafa')
      this.renderChart(this.chartData, this.options)
    }
  },
  computed: {
    chartData(){
      return {
        datasets: [
          {
            data: this.data,
            backgroundColor: this.gradient,
            pointRadius: 5,
            pointBackgroundColor: theme.neutralWhite,
            pointBorderColor: theme.primary500main,
            pointHoverBackgroundColor: theme.primary500main
          },
        ]
      }
    },
    options(){
      return {
        responsive: true,
        maintainAspectRatio: false,
        legend: {
          display: false
        },
        scales: {
          yAxes: [{
            display: true,
            ticks: {
              min: 0,
              max: 10,    
              beginAtZero: true,   // minimum value will be 0.
              callback: function(value, index) {
                if(index === 0 || index === 10) {
                  return value
                }
              }
            },
            gridLines: {
              display: true,
              drawOnChartArea: false
            },
          }],
          xAxes: [{
            type: 'linear',
            gridLines: {
              display: true,
              drawOnChartArea: false,
              beginAtZero: false,
            },
            ticks: {
              min:(this.data[0]) ? this.data[0].x : 0 ,
              max:(this.data[this.data.length - 1]) ? this.data[this.data.length - 1].x : 0,
              callback: function(value, index, values) {
                if(index === values.length-1) {
                  return this.now
                }
              }.bind(this)
            }
          }]
        },
        tooltips: {
          enabled: false,
          callbacks: {
            beforeBody: function(tooltipItem, data) {
              return data.datasets[0].data[tooltipItem[0].index].tooltipLines

            
            },
            label: function(){
              return
            }
          },
          custom: function(tooltipModel) {
                // Tooltip Element
                var tooltipEl = document.getElementById('chartjs-tooltip');

                // Create element on first render
                if (!tooltipEl) {
                    tooltipEl = document.createElement('div');
                    tooltipEl.id = 'chartjs-tooltip';
                    tooltipEl.innerHTML = '<div></div>';
                    document.body.appendChild(tooltipEl);
                }

                // Hide if no tooltip
                if (tooltipModel.opacity === 0) {
                    tooltipEl.style.opacity = 0;
                    return;
                }

                // Set caret Position
                tooltipEl.classList.remove('above', 'below', 'no-transform');
                if (tooltipModel.yAlign) {
                    tooltipEl.classList.add(tooltipModel.yAlign);
                } else {
                    tooltipEl.classList.add('no-transform');
                }

                var position = this._chart.canvas.getBoundingClientRect();
                // `this` will be the overall tooltip

                let height = 150;

                // Set Text
                if (tooltipModel.body) {

                    var innerHtml = '';

                    tooltipModel.beforeBody.forEach((line, index) => {
                      if(index === tooltipModel.beforeBody.length - 1) {
                        innerHtml += `<div style='border: 1px solid #C7CDD2; height: 0px; width: 90%;'></div>`
                        innerHtml += `<p style='margin:8px; font-weight:bold'>${line}</p>`
                      }
                      else{
                        innerHtml += `<p style='margin:8px;'>${line}</p>`
                      }
                    })
                    let bottom = (position.height - tooltipModel.caretY + 10) * -1 + 'px'
                    let date = new Date(parseInt(tooltipModel.title[0]))
                    date = date.toLocaleDateString()
                    innerHtml += `<div style="position: absolute; bottom:${bottom}; color:${theme.primary500main}; font-weight:bold; left: -35px;">${date}</div>`

                    let lineCursorHeight = `${position.height- tooltipModel.caretY - 33}px`
                    innerHtml += `<div class="lineCursor" style="position: absolute; border:1px ${theme.primary500main} solid; width: 0px; height:${lineCursorHeight};"></div>`
                    var divRoot = tooltipEl.querySelector('div');
                    divRoot.innerHTML = innerHtml;

                }


                // Display, position, and set styles for font
                tooltipEl.style.opacity = 1;
                tooltipEl.style.position = 'absolute';
                tooltipEl.style.left = position.left + window.pageXOffset + tooltipModel.caretX + 'px';
                tooltipEl.style.top = position.top + window.pageYOffset + tooltipModel.caretY - height + 'px';
                tooltipEl.style.fontFamily = tooltipModel._bodyFontFamily;
                tooltipEl.style.fontSize = tooltipModel.bodyFontSize + 'px';
                tooltipEl.style.fontStyle = tooltipModel._bodyFontStyle;
                tooltipEl.style.padding = '0';
                tooltipEl.style.pointerEvents = 'none';
                tooltipEl.style.backgroundColor = '#FAFAFA';
                tooltipEl.style.boxShadow = "0px 2px 4px rgba(0, 0, 0, 0.14), 0px 3px 4px rgba(0, 0, 0, 0.12), 0px 1px 5px rgba(0, 0, 0, 0.2)"
                tooltipEl.style.borderRadius = "4px";
                tooltipEl.style.width = "150px";
            }
        }
      }
    }
  },
  mounted () {
    this.render()
  },
  watch: {
    data() {
      this.render()
    }
  }
}