<script>
import { VueGoodTable } from 'vue-good-table';
// import SvgIcon from '../components/svg-icon';
import utils from "../utils";
import colors from  '@/scss/_theme.scss';
import 'vue-good-table/dist/vue-good-table.css';
import '@/scss/_table.scss' 

export default {
    name: 'bt-exchange-table',
    components: {
        'vue-good-table': VueGoodTable,
        // 'svg-icon': SvgIcon
    },
    props: {
        transactions: {
            type: Array,
            default() {
                return [
                    {
                        id: 1,
                        created: '1579547824',
                        amount: 200,
                        type: 'Deposit',
                        status: 'complete',
                        message: ''
                    }
                ];
            },
            validator(transactions) {
                const requiredKeys = ['id', 'created', 'amount', 'type', 'status'];
                return transactions.every(tx => requiredKeys.every(key => key in tx));
            },
        },
    },
    data() {
        return {
            columns: [
                {
                    label: this.$t('Date & Time'),
                    field: 'created',
                    width: '160px',
                    tdClass: 'table-date-cell'
                },
                {
                    label: this.$t('Amount'),
                    field: 'amount',
                    type: 'number',
                    formatFn: this.formatAmount,
                    tdClass: 'ellipsis'
                },
                {
                    label: this.$t('From'),
                    field: 'from',
                },
                {
                    label: this.$t('To'),
                    field: 'to',
                },
                {
                    label: this.$t('Status'),
                    field: 'status',
                    width: '100px',
                    tdClass: 'status-cell'
                },
            ],
            rows: [],
        };
    },
    methods: {
        statusStyle(status) {
            let color = '';
            if (status.toLowerCase() === 'success') {
                color = colors.accentsGreen500;
            } else if (status.toLowerCase() === 'pending') {
                color = colors.accentsYellow500;
            } else {
                color = colors.accentsRed500;
            }
            return { color };
        },
        formatDate(posixTime) {
            // const d = new Date(posixTime * 1000);
            const d = new Date(posixTime);

            const date = d.toLocaleDateString(undefined, {
                year: 'numeric',
                month: '2-digit',
                day: 'numeric'
            })

            const time = d.toLocaleTimeString(undefined, {
                hour: '2-digit',
                minute: '2-digit',
                hour12: false
            })

            return { date, time };
        },
        formatAmount(amount) {
            const formatOptions = {
                style: 'decimal',
                minimumFractionDigits: 0,
                maximumFractionDigits: 6,
            };
            let amountStr = amount.toString();
            // amountStr = amountStr.slice(0, -6) + '.' + amountStr.slice(-6);
            return Intl.NumberFormat('en-US', formatOptions).format(parseFloat(amountStr));
        },
        capitalize(str) {
            return str[0].toUpperCase() + str.slice(1);
        },
        updateRows() {
            // const BTT = 'BTT Crypto Wallet';
            // const IN_APP = 'BTFS In-App Wallet';

            this.rows = this.transactions
                .map((transaction) => {
                    // if (transaction.type === 'Deposit') {
                    //     transaction.from = BTT;
                    //     transaction.to = IN_APP;
                    // } else {
                    //     transaction.from = IN_APP;
                    //     transaction.to = BTT;
                    // }
                    transaction.statusText = this.$t(transaction.status.toLowerCase())
                    return transaction;
                })
                .sort((a, b) => b.created - a.created)
                .slice(0, 10);
        },
        resizeColumns() {
            Object.values(this.$refs).forEach(ref => ref && utils.ellipsifyText(ref, 5));
        },
        async formatTable() {
            this.updateRows();
            // resolves once entire view has been rendered
            await this.$nextTick();
            this.resizeColumns();
        },
        isBase64(str) {
            // public keys are base64 strings
            const base64Regex = new RegExp("^(?:[A-Za-z0-9+/]{4})*(?:[A-Za-z0-9+/]{2}==|[A-Za-z0-9+/]{3}=)?$");
            return base64Regex.test(str);
        },
    },
    mounted() {
        if (!this.$refs) {
            return;
        }
        this.formatTable();
        window.addEventListener('resize', this.resizeColumns);
    },
    beforeDestroy() {
        window.removeEventListener('resize', this.resizeColumns);
    },
    watch: {
        transactions() {
            this.formatTable();
        },
    },
}
</script>
<template>
    <vue-good-table
        styleClass="vgt-table striped"
        :columns="columns"
        :rows="rows"
        :sort-options="{ enabled: false }"
    >
        <template slot="table-row" slot-scope="props">
            <div v-if="props.column.field === 'created'">
                <span>{{ formatDate(props.row.created).date }}</span>,
                <span class="time">{{ formatDate(props.row.created).time }}</span>
            </div>
            <template v-else-if="props.column.field === 'status'">
                <span :style="statusStyle(props.row.status)">{{ capitalize(props.row.statusText) }}</span>
<!--                 <svg-icon
                    icon="question"
                    class="icon"
                    v-tooltip.right-start="props.row.message"
                    v-if="props.row.message !== 'SUCCESS' && props.row.message !== ''"
                    :hasFill="true"
                /> -->
            </template>
            <template v-else-if="props.column.field === 'to'">
                <div
                    v-if="isBase64(props.row.to)"
                    :ref="'toRef' + props.row.id"
                    :title="props.row.to"
                    class="ellipsis"
                >
                    {{ props.row.to }}
                </div>
                <div v-else class="ellipsis">
                    {{ props.row.to }}
                </div>
            </template>
            <template v-else-if="props.column.field === 'from'">
                <div
                    v-if="isBase64(props.row.from)"
                    :ref="'fromRef' + props.row.id"
                    :title="props.row.from"
                    class="ellipsis"
                >
                    {{ props.row.from }}
                </div>
                <div v-else class="ellipsis">
                    {{ props.row.from }}
                </div>
            </template>
            <span v-else>{{ props.formattedRow[props.column.field] }} <span class="btt-suffix">BTT</span></span>
        </template>
    </vue-good-table>
</template>

<style lang="scss" scoped>
.ellipsis {
    overflow: hidden;
    text-overflow: ellipsis;
}
</style>
