<script>
import { mapActions } from 'vuex';
import Banner from './components/Banner'
import { supportedLanguages } from './utils/constants'

export default {
  name: 'App',
  components: {
    Banner
  },
  data(){
    return {
      isLoading: true,
      showBanner: false
    }
  },
  methods:{
    ...mapActions([
        'saveSettings',
    ]),
    initSetup() {
      let hostConfigAPI = this.$store.dispatch('getHostConfig');
      let hostChallengeAPI = this.$store.dispatch('getHostChallengeEnabled');
      let hostRepairAPI = this.$store.dispatch('getHostRepairEnabled');
      let getStorageHostEnabledAPI = this.$store.dispatch('getStorageHostEnabled');
      let walletConfigAPI = this.$store.dispatch('getWalletConfig');
      let metaStoresAPI = this.initMetaStores();
      this.setupI18n();

      return Promise.all([
        hostConfigAPI,
        hostChallengeAPI,
        hostRepairAPI,
        getStorageHostEnabledAPI,
        walletConfigAPI,
        metaStoresAPI,
      ])
    },
    initSync() {
      this.$store.dispatch('storageInfo')
      let storageStatsSync = this.$store.dispatch('storageStatsSync')
      let storageContractsSync = this.$store.dispatch('storageContractsSync')
      return Promise.all([
        storageStatsSync,
        storageContractsSync
      ]).then((res) => {
        let isSynced =  res.reduce((acc, curr) => acc && curr)
        this.$store.dispatch('updateIsSynced', isSynced)
      })
    },
    initStorageStores() {
      let storageInfoAPI = this.$store.dispatch('storageInfo')
      let storageStatsInfoAPI = this.$store.dispatch('storageStatsInfo')
      let storageContractsAPI = this.$store.dispatch('storageContractsInfo')
      let storageContractsListAPI = this.$store.dispatch('storageContractsList')

      return Promise.all([
        storageInfoAPI,
        storageStatsInfoAPI,
        storageContractsAPI,
        storageContractsListAPI,
      ])
    },
    initMetaStores() {
      let metaRepoStatAPI = this.$store.dispatch('metaRepoStat');
      let metaIdAPI = this.$store.dispatch('metaId');
      let metaVersionAPI = this.$store.dispatch('metaVersion');

      return Promise.all([
        metaRepoStatAPI,
        metaIdAPI,
        metaVersionAPI,
      ])
    },
    initWalletStores() {
      let walletBalanceAPI = this.$store.dispatch('walletBalance');
      let walletTransactionsAPI = this.$store.dispatch('walletTransactions')
      this.$store.dispatch('metaId');

      return Promise.all([
        walletBalanceAPI,
        walletTransactionsAPI
      ])
    },
    createStorageSpaceObj() {
      let storageSpace = {
        used:0,
        cap:32,
        total:1000,
        minimum:32,
        available:32
      }
      if(!this.isLoading){
        storageSpace.used = this.storageSpaceUsed
        storageSpace.available = this.storageSpaceAvailable
        storageSpace.cap = this.storageSpaceCap
        storageSpace.total = this.storageSpaceTotal
      }
      return storageSpace
    },
    isValidRoute(routeName){
      let isValidRoute = this.$router.options.routes.reduce((acc, val) => {
        return (val.name === routeName)||acc
      }, false)
      return isValidRoute
    },
    isSetupRoute(routeName) {
      return routeName.includes('Add') || routeName.includes('Setup') || routeName.includes('Import')
    },
    routeType(routeName) {
      if(routeName.startsWith('Host')) return 'host'
      if(routeName.startsWith('Wallet')) return 'wallet'
      if(routeName.startsWith('Settings')) return 'settings'
      return null
    },
    async setupI18n() {

        let locale = (localStorage.getItem('locale') || window.navigator.language || navigator.languages[0]).toLowerCase();
        let localeShort = locale.slice(0,2)
        locale = supportedLanguages[locale] ? supportedLanguages[locale].value : supportedLanguages[localeShort] ? supportedLanguages[localeShort].value : 'en';

        this.saveSettings({ locale });
        this.$i18n.locale = locale;
    },
  },
  created(){

      this.initSetup()
      .then(() => {
        // ROUTE GUARD
        if(!this.isValidRoute(this.route)){
          this.$router.push('/host')
        }
        let isSetupRoute = this.isSetupRoute(this.route)
        let routeType = this.routeType(this.route)
        switch(routeType){
          case 'host':
            if(!this.isHostSetup && (!isSetupRoute)){

              this.$router.push('/host/add')
            }
            else if(this.isHostSetup && (isSetupRoute)){

              this.$router.push('/host')
            }
            break

          case 'wallet':
            if(!this.isWalletSetup && (!isSetupRoute)){

              this.$router.push('/wallet/add')
            }
            else if(this.isWalletSetup && (isSetupRoute)){

              this.$router.push('/wallet')
            }
            break
        }
        
        // ROUTE GUARD IN APP
        this.$router.beforeEach((to, from, next) => {

          //wallet sign-in page bypass
          if(from.name === "WalletDashboard" || from.name === "WalletImport" && 
            (to.name === "WalletImport" || to.name === "WalletSetup")){
            next()
            return
          }

          if(!this.isValidRoute(to.name)){
            return
          }
          let isSetupRoute = this.isSetupRoute(to.name)
          let routeType = this.routeType(to.name)

          switch(routeType){
            case 'host':
              if(!this.isHostSetup && (!isSetupRoute) ){
                next('/host/add')
              } 
              else if(this.isHostSetup && (isSetupRoute) ){
                next({name: 'HostDashboard'})
              }
              else {
                next()
              }
              break

            case 'wallet':
              if(!this.isWalletSetup && (!isSetupRoute) ){
                next('/wallet/add')
              } 
              else if(this.isWalletSetup && (isSetupRoute) ){
                next({name: 'WalletDashboard'})
              }
              else {
                next()
              }
              break

            default:
              next()
          }
        })
        // ROUTE GUARD END

        if(!this.isHostSetup){
          this.initSync()
          this.initStorageStores()
          this.isLoading = false;
          return Promise.reject('host not setup')
        }
        else{
          this.initSync()
          this.isLoading = false;
        }

      })
      .catch(() => {
        // console.log('[initialization]',e)
      })
  },
  computed:{
    route(){
      return this.$route.name
    },
    isHostSetup(){
      return this.$store.state.config.host.initialized && this.hostEnabled
    },
    isSynced() {
      return this.$store.state.storage.isSynced
    },
    isSyncedLocal() {
      return this.$store.state.storage.isSyncedLocal
    },
    isStorageLoading() {
      return this.$store.state.storage.isStorageLoading
    },
    isWalletSetup(){
      return this.$store.state.config.wallet.initialized
    },
    storagePathList() {
      return this.$store.state.storage.pathList
    },
    storageSpaceAvailable(){
      let storageState = this.$store.state.storage.value
      let gb = storageState.host_stats.storage_disk_available/Math.pow(1000, 3)
      gb = (gb >= 1) ? Math.floor(gb) : Math.floor(gb* 1000) / 1000
      gb = parseFloat(gb)
      return gb
    },
    storageSpaceUsed(){
      let storageState = this.$store.state.storage.value
      let gb = storageState.host_stats.storage_used/Math.pow(1000, 3)
      gb = (gb >= 1) ? Math.floor(gb) : Math.floor(gb* 1000) / 1000
      gb = parseFloat(gb)
      return gb
    },
    storageSpaceCap(){
      let storageState = this.$store.state.storage.value
      return bToGB(storageState.host_stats.storage_cap)
    },
    storageSpaceTotal(){
      let storageState = this.$store.state.storage.value
      let storageDiskAvailable = (this.proposedStorageAvailable === null) ? storageState.host_stats.storage_disk_available : this.proposedStorageAvailable
      let storageUsed = storageState.host_stats.storage_used
      let storageAvailableCap = storageDiskAvailable + storageUsed
      return bToGB(storageAvailableCap)
    },
    proposedStorageAvailable() {
      return this.$store.state.storage.proposedStorageAvailable
    },
    hostDirectoryPath() {
      return this.$store.state.meta.value.RepoPath
    },
    hostEnabled() {
      return this.$store.state.config.hostEnabled.value
    },
    hostOnline() {
      return this.$store.state.storage.value.host_stats.online
    },
    hostScoreHistory() {
      return this.$store.state.storage.statsList
    },
    hostStats() {
      return {
        uptimeScore: (this.$store.state.storage.value.host_stats.uptime_score),
        ageScore: this.$store.state.storage.value.host_stats.age_score,
        versionScore: this.$store.state.storage.value.host_stats.version_score,
        uploadSpeedScore: this.$store.state.storage.value.host_stats.upload_speed_score,
        downloadSpeedScore: this.$store.state.storage.value.host_stats.download_speed_score,
        uptimeWeight: this.$store.state.storage.value.host_stats.uptime_weight,
        ageWeight: this.$store.state.storage.value.host_stats.age_weight,
        versionWeight: this.$store.state.storage.value.host_stats.version_weight,
        uploadSpeedWeight: this.$store.state.storage.value.host_stats.upload_speed_weight,
        downloadSpeedWeight: this.$store.state.storage.value.host_stats.download_speed_weight,
        lastUpdated: this.$store.state.storage.value.host_stats.last_updated
      }
    },
    hostScore() {
      return this.$store.state.storage.value.host_stats.score
    },
    hostUptime() {
      return this.$store.state.storage.value.host_stats.uptime
    },
    hostContractsNum() {
      return this.$store.state.storage.contracts.active_contract_num
    },
    earnings() {
      return this.$store.state.storage.contracts.compensation_paid
    },
    potentialEarnings() {
      return this.$store.state.storage.contracts.compensation_outstanding
    },
    hostPricing() {
      return this.$store.state.storage.info.storage_price_ask
    },
    hostChallengeEnabled() {
      return this.$store.state.config.hostChallengeEnabled
    },
    hostRepairEnabled() {
      return this.$store.state.config.hostRepairEnabled
    },
    hostSetupValues() {
      return this.$store.state.storage.setup
    },
    hostIsImporting() {
      return this.$store.state.storage.isImporting
    },
    nextPayment() {
      let contractsList = this.$store.state.storage.contractsList.contracts
      let contractsListFiltered = contractsList.filter(contract => contract.next_escrow_time !== "0001-01-01T00:00:00Z" && contract.next_escrow_time !== "9999-12-31T23:59:59Z")
      if(contractsListFiltered.length > 0) {
        let date = contractsListFiltered[0]
        date = date.next_escrow_time
        date = new Date(date)
        date = date.toDateString()
        date = date.slice(3)
        date = date.slice(0,7) + ',' + date.slice(7)
        return date
      }
      return null
    },
    nodeVersion() {
      return this.$store.state.meta.version.Version
    },
    notificationState() {
      return this.$store.state.notification
    },
    walletInAppBalance() {
      return this.$store.state.wallet.balance.BtfsWalletBalance
    },
    walletCryptoBalance() {
      return this.$store.state.wallet.balance.BttWalletBalance
    },
    walletRecoveryPhrase() {
      return this.$store.state.wallet.keys.Mnemonic
    },
    walletTransactions() {
      return this.$store.state.wallet.transactions
    },
    nodeID() {
      return this.$store.state.meta.id.ID
    },
    tronAddress() {
      return this.$store.state.meta.id.TronAddress
    },
    routeProps(){
      if(this.route === "HostAdd") {
        let hasEnoughSpace = this.storageSpaceTotal > 32
        return {
          hasEnoughSpace,
          hostEnabled: this.hostEnabled,
          hostDirectoryPath: this.hostDirectoryPath,
          updateHostSetupValues: (options) => this.$store.dispatch('updateHostSetupValues', {...options}),
          checkOtherFolder: (options) => this.$store.dispatch('storagePathCapacity', {...options}),
          getFolders: (path) => {
            return this.$store.dispatch('storagePathList', {path: path})
          },
          getPartitions: () => {
            return this.$store.dispatch('storagePathPartitions')
          },
        }
      }
      else if(this.route === "HostDashboard") {
        let storageSpace = this.createStorageSpaceObj()
        return{
          online: this.hostOnline,
          hostStats: this.hostStats,
          hostChallengeEnabled: this.hostChallengeEnabled,
          hostRepairEnabled: this.hostRepairEnabled,
          score: this.hostScore,
          uptime: this.hostUptime,
          contractsNum: this.hostContractsNum,
          earnings: this.earnings,
          potentialEarnings: this.potentialEarnings,
          hostPricing: this.hostPricing,
          nextPayment: this.nextPayment,
          storageSpace: storageSpace,
          isSynced: this.isSynced,
          isSyncedLocal: this.isSyncedLocal,
          isStorageLoading: this.isStorageLoading,
          hostDirectoryPath: this.hostDirectoryPath,
          chooseOtherFolder: (options) => this.$store.dispatch('storagePath', {...options}),
          checkOtherFolder: (options) => this.$store.dispatch('storagePathCapacity', {...options}),
          notificationState: this.notificationState.host,
          notificationClose: () => this.$store.dispatch('notificationClose', 'host'),
          isImporting: this.hostIsImporting,
          handleRefresh: () => {
            this.initStorageStores()
          },
          getHostScoreHistory(options) {
            return this.$store.dispatch('storageStatsList', {...options})
          },
          hostScoreHistory: this.hostScoreHistory,
          getFolders: (path) => {
            return this.$store.dispatch('storagePathList', {path: path})
          },
          getPartitions: () => {
            return this.$store.dispatch('storagePathPartitions')
          },
        }
      }
      else if(this.route === "HostSetup") {
        let storageSpace = this.createStorageSpaceObj()
        return {
          storageSpace,
          storageAnnounce: (hostStorageMax) => {
            this.$store.dispatch('storageAnnounce', {
              hostStorageMax: hostStorageMax,
            }) 
          },
          setInitializationFlag: () => {

            this.initSync()
            .then(() => {
              return this.initStorageStores()
            })

            return this.$store.dispatch('setHostConfig')
          },
          updateHostSetupValues: (options) => this.$store.dispatch('updateHostSetupValues', {...options}),
          chooseOtherFolder: (options) => this.$store.dispatch('storagePath', {...options}),
          hostSetupValues: this.hostSetupValues
        }
      }
      else if(this.route === "Settings") {
        return {
          version: this.nodeVersion,
          id: this.nodeID,
          notificationState: this.notificationState.settings,
          notificationClose: () => this.$store.dispatch('notificationClose', 'settings'),
          hostPricing: this.hostPricing,
          hostChallengeEnabled: this.hostChallengeEnabled,
          hostRepairEnabled: this.hostRepairEnabled,
        }
      }
      else if(this.route === "WalletDashboard") {
        return {
          id: this.nodeID,
          tronAddress: this.tronAddress,
          transactions: this.walletTransactions,
          handleDeposit: (options) => {
            return this.$store.dispatch('walletDeposit', options)
          },
          handleWithdraw: (options) => {
            return this.$store.dispatch('walletWithdraw', options)
          },
          inAppBalance: this.walletInAppBalance,
          cryptoBalance: this.walletCryptoBalance,
          handleRefresh: () => {
            return this.initWalletStores()
          },
          checkPassword: (options) => {
            return this.$store.dispatch('walletCheckPassword', options)
          },
        }
      }
      else if(this.route === "WalletSetup") {
        return {
          setInitializationFlag: () => {
            return this.$store.dispatch('setWalletConfig')
          },
          getKeys: () => {
            return (this.isWalletSetup) ? this.$store.dispatch('walletGenerateKey') : this.$store.dispatch('walletKeys')
          },
          recoveryPhrases: this.walletRecoveryPhrase,
          walletInit: (options) => {
            return (this.isWalletSetup) ? this.$store.dispatch('walletInit', options) : this.$store.dispatch('walletPassword', options)
          }
        }
      }
      else if(this.route === "WalletImport") {
        return {
          setPassword: (options) => {
            return this.$store.dispatch('walletPassword', options)
          },
          importWallet: (options) => {
            return this.$store.dispatch('walletImport', options)
          },
          setInitializationFlag: () => {
            return this.$store.dispatch('setWalletConfig')
          },
        }
      }
      else if(this.route === "WalletAdd") {
        return {
          checkPassword: (options) => {
            return this.$store.dispatch('walletDiscovery', options)
          },
          importWallet: (options) => {
            return this.$store.dispatch('walletImport', options)
          },
          setInitializationFlag: () => {
            return this.$store.dispatch('setWalletConfig')
          },
          setPassword: (options) => {
            return this.$store.dispatch('walletPassword', options)
          },
        }
      }
      return {}
    },
    sidebarItems() {
      return {
          top: [
            {
              icon: 'dns',
              path:`/host`,
              name:this.$t('Host')
            },
            {
              icon: 'wallet',
              target:'',
              path:`/wallet`,
              name: this.$t('Wallet')
            }
          ],
          bottom: [
            {
              icon: 'dashboard',
              target:'_blank',
              href:`https://scan.btfs.io`,
              name:'BTFS Scan'
            },
            {
              icon: 'mail',
              target:'_blank',
              href:`https://docs.google.com/forms/d/e/1FAIpQLSeH1Vhm4C6tcsI80gLzUQ58cuYtMHj3LEQFx_STAqWNoCP3Ew/viewform?usp=pp_url&entry.875205708=${this.nodeID}`,
              name:this.$t('Contact Us')
            }
          ],
      }
    }
  },

}

function bToGB(b) {
  let gb = b/Math.pow(1000, 3)
  gb = Math.floor(gb)
  return gb
}

</script>

<template>
  <div id="app">
    <Banner @showBanner="(val) => {showBanner=val}"/>
    <div class="header-container">
      <bt-header>
        <template v-slot:left>
          <div class="logo-container">
            <img src="./assets/svg/btfs-logo-2.svg" />
          </div>
        </template>
        <template v-slot:right>
          <router-link to="/settings" class="link">
            <div class="settings-container">
              <svg-icon icon="settings" class="settings-icon" :hasFill="true"/>
            </div>
          </router-link>
        </template>
      </bt-header>
    </div>
    <div class="main" :class="{'banner-present': showBanner}">
      <div class="nav">
        <bt-sidebar :items="sidebarItems">
          <template slot="BTFS Scan">
            <div class="btfs-scan-tab">
              <svg-icon icon="open-in-new" class="new-tab-icon" :hasFill="true"/>
            </div>
          </template>
        </bt-sidebar>
      </div>
      <router-view v-bind="routeProps" v-if="!isLoading" class="view"/>
    </div>
  </div>
</template>

