<script>
import colors from "../scss/_theme.scss"
import Button from '../components/Button.vue'
import SvgIcon from '../components/svg-icon';
import EditHostFolder from '../components/EditHostFolder';

export default {
  name: 'HostAdd',
  props: {
    hasEnoughSpace:{
      type: Boolean,
      required: true,
      default:true
    },
    hostEnabled: {
      type: Boolean
    },
    hostDirectoryPath: {
      type: String,
      default: null
    },
    updateHostSetupValues: {
      type: Function,
      default: () => {}
    },
    checkOtherFolder: {
      type: Function,
      default: () => {}
    },
    getFolders: {
      type: Function,
      default: () => {}
    },
    folderData: {
      type: Object,
      default: () => {}
    },
    getPartitions: {
      type: Function,
      default: () => {}
    },
    partitionsData: {
      type: Array,
      default: () => []
    }
  },
  components: {
    Button,
    'svg-icon': SvgIcon,
    EditHostFolder,
  },
  data() {
    return {
      btfsBlue500 : colors.btfsBlue500,
      showPopup : false,
      showCopyTooltip: false,
      state: 0,
      newHostDirectoryPath: null,
      isEditingFolder: false,
      isFolderValid: true
    }
  },
  methods: {
    handleClose() {
      this.showPopup = !this.showPopup
    },

    handleNext(e) {
      if(!this.hasEnoughSpace || this.hostDirectoryPath === null){
        e.preventDefault();
        this.showPopup = true
        return
      }

      if(this.newHostDirectoryPath !== null && this.isStorageLocationDifferent){
        this.updateHostSetupValues({path: this.newHostDirectoryPath})
      }

      this.$router.push('/host/setup')

    },
    handleCopy() {
      navigator.clipboard.writeText('btfs config profile apply storage-host')
      this.showCopyTooltip = true;
      setTimeout(()=>{this.showCopyTooltip=false},1000)
    },
    nextState() {
      this.state = 1
    },
    backState() {
      this.state = 0
    },
    onEditFolder(val) {
      this.newHostDirectoryPath = val
    }
  },
  computed: {
    isStorageLocationDifferent() {
      return this.hostDirectoryPath !== this.newHostDirectoryPath
    },
  },
  created() {
    //resets proposed storage value if user goes back from host/setup -> host/add
    this.$store.dispatch('updateProposedStorageAvailable', null)
  }

}
</script>

<template>
  <div class="host-add">

    <div v-if="hostEnabled">
      <div class="host-add-container" v-if="state === 0">
        <div class="header-container">
          <p class="header">{{$t('Host setup')}}</p>
          <p class="description">
              {{$t('Commit your spare hard drive space to the BTFS network to earn BTT.')}} 
              <br/><br/>
              {{$t('After clicking ‘Host setup’, select a folder where you would like files to be hosted.')}}
          </p>
          <p class="sub-description">
            <i18n path="Looking for more information? Read our FAQs" tag="span">
              <a href="https://www.bittorrent.com/token/bittorrent-file-system/faq/" target="_blank" rel="noreferrer noopener">{{ $t('FAQs') }}</a>
            </i18n>
          </p>
        </div>
        <div class="button-container">
          <Button :cb="nextState">{{$t('Host setup')}}</Button>
        </div>
      </div>

      <div class="host-add-container" v-if="state === 1">
        <div class="header-container">
          <p class="header">{{$t('Folder destination')}}</p>
          <p class="description">
              {{$t('A default folder destination has been selected for you.')}}
              <br/><br/>
              {{$t('If you would like files hosted somewhere else, please enter a different folder destination with at least 32 GB of available storage space.')}}
          </p>
          <div class="edit-host-folder-container">
            <EditHostFolder
              :hostDirectoryPath="hostDirectoryPath"
              @inputChange = "onEditFolder"
              :checkOtherFolder = "checkOtherFolder"
              @isEditingFolder = "(val) => {isEditingFolder = val}"
              :getFolders = "getFolders"
              :folderData = "folderData"
              @isFolderValid = "(val) => {isFolderValid = val}"
              :getPartitions = "getPartitions"
            />
          </div>
        </div>
        <div class="button-container">
          <Button :isDisabled="isEditingFolder" :cb="handleNext">{{$t('Confirm')}}</Button>
        </div>
      </div>

    </div>

    <div class="popup-bg" v-if="showPopup">
      <div class="popup-container">
        <div class="close-container" @click="handleClose">
          <img src="../assets/svg/close.svg"/>
        </div>
        <p class="header">
          {{$t('Not enough storage space')}}
        </p>
        <p class="description">
          {{$t('32 GB is the minimum available space needed to continue setting up your host. Please use another computer with at least 32 GB of available space.')}} 
        </p>
      </div>
    </div>

    <div v-if="!hostEnabled" class="host-not-enabled host-add-container">
      <div class="container">
        <p class="header">
            Host profile not enabled
          </p>
          <p class="description">
            <ul>
              <li>1. In order to enable your host profile, use command:</li>
              <li class="code-block">
                btfs config profile apply storage-host
                <svg-icon 
                  icon="copy" 
                  class="icon" 
                  :hasFill="true"
                  @click="handleCopy"
                  v-tooltip.bottom="{ 
                              content: 'copied!', 
                              classes: ['tooltip'], 
                              show: showCopyTooltip,
                              trigger: 'manual'
                            }"
                />
              </li>
              <li>2. Restart your daemon</li>
              <li>3. Refresh this page</li>
            </ul>
          </p>
      </div>
    </div>

  </div>
</template>


