import Chart from 'chart.js'
import { generateChart } from 'vue-chartjs'
import theme from '../scss/_theme.scss'

Chart.defaults.RoundedDoughnut = Chart.defaults.doughnut;
Chart.controllers.RoundedDoughnut = Chart.controllers.doughnut.extend({ 
/* custom magic here */
  draw: function(ease) {
      var ctx           = this.chart.ctx;
      var easingDecimal = ease || 1;
      var arcs          = this.getMeta().data;

      var vm         = arcs[1]._view;
      var radius     = (vm.outerRadius + vm.innerRadius) / 2;
      var thickness  = (vm.outerRadius - vm.innerRadius);

      ctx.beginPath();
      ctx.arc(this.chart.width/2, this.chart.height/2+5, radius, 0, 2 * Math.PI);
      ctx.lineWidth = thickness
      ctx.strokeStyle = arcs[1]._view.backgroundColor
      ctx.stroke();

      ctx.font = "bold 36px Lato";
      var txt = this._config.score
      ctx.fillStyle = arcs[0]._view.backgroundColor
      ctx.fillText(this._config.score, (this.chart.width/2) - (ctx.measureText(txt).width/2), this.chart.height/2+7)

      Chart.helpers.each(arcs, function(arc, i) {
        if( i === 0 ){
          arc.transition(easingDecimal).draw();

          // var pArc   = arcs[i === 0 ? arcs.length - 1 : i - 1];
          // var pColor = pArc._view.backgroundColor;
          var vm         = arc._view;
          var radius     = (vm.outerRadius + vm.innerRadius) / 2;
          var thickness  = (vm.outerRadius - vm.innerRadius) / 2;
          var startAngle = Math.PI - vm.startAngle - Math.PI / 2;
          var angle      = Math.PI - vm.endAngle - Math.PI / 2;

          ctx.save();
          ctx.translate(vm.x, vm.y);

          ctx.fillStyle = arc._view.backgroundColor;
          ctx.beginPath();
          ctx.arc(radius * Math.sin(startAngle), radius * Math.cos(startAngle), thickness, 0, 2 * Math.PI);
          ctx.fill();

          ctx.beginPath();
          ctx.arc(radius * Math.sin(angle), radius * Math.cos(angle), thickness, 0, 2 * Math.PI);
          ctx.fill();
        }

          ctx.restore();
      });
  }
})

const RoundedDoughnut = generateChart('RoundedDoughnut', 'RoundedDoughnut')
export default {
  extends: RoundedDoughnut,
  components: {
    theme
  },
  props: {
    score: {
      type: Number,
      required: true
    }
  },
  data() {

    let colors = [theme.accentGreenMain, theme.accentGreenLight]
    if(this.score < 5) {
      colors = [theme.accentRedMain, theme.accentRedLight]
    }
    else if(this.score < 7.5) {
      colors = [theme.accentYellowMain, theme.accentYellowLight]
    }

    let data = [this.score, 10 - this.score]

    return {
      chartdata: {
        datasets: [
          {
            label: 'Data One',
            backgroundColor: colors,
            data: data,
            borderWidth:0, 
            score: this.score
          },
        ]
      },
      options: {
        responsive: true,
        maintainAspectRatio: true,
        tooltips: {
          enabled: false
        },
        rotation: Math.PI,
        cutoutPercentage: 70,
        events: []
        
      }
    }
  },

  mounted () {

    this.renderChart(this.chartdata, this.options)
  }
}