<script>
import InputBox from './InputBox.vue';
import colors from '../scss/_theme.scss';
import Button from './Button.vue'
import TransitionExpand from './TransitionExpand.vue'
import Dropdown from './Dropdown.vue'
import Popup from './Popup.vue';

export default {
    name: 'WalletBalanceTile',
    props: {
        title: {
            type: String,
            required: true
        },
        tooltipText: {
            type: String,
            default: ""
        },
        balance: {
            type: Number,
            required: true,
            default: 0
        },
        defaultTargetAddress: {
            type: String,
            required: true
        },
        dropdownOptions: {
            type: Array,
            required: true,
            default: () => ['Other public address']
        },
        handleWalletTransfer: {
          type: Function,
          required: true,
          default: () => {}
        },
        minTransferAmount: {
            type: Number,
            default: 1000
        },
        publicAddress: {
            type: String,
            default: ''
        },
        publicAddressLabel: {
            type: String,
            default: ''
        }
    },
    components: {
        Button,
        TransitionExpand,
        Dropdown,
        'bt-input-box': InputBox,
        Popup

    },
    data() {
        return {
            isExpanded: false,
            colors,
            amount: '',
            targetAddress: '',
            dropdownSelection: null,
            showPopup: false,
            popupState: 0, //['confirmation', 'processing', 'success']
            transferRes: '',
            showCopyTooltip: false,
            isTargetAddressValid: true
        }
    },
    methods: {
        handleExpand() {
            this.isExpanded = !this.isExpanded
            if(this.isExpanded === false){
                this.dropdownSelection = null
            }
        },
        handleSend() {
            this.showPopup = true
        },
        handlePopoutConfirm() {
            this.popupState = 1

            //If a deposit or withdraw request
            if(this.targetAddress === this.defaultTargetAddress){
                this.handleWalletTransfer({amount: this.amount})
                .then((res) => {
                    setTimeout(() => {
                        this.transferRes = res
                        this.popupState = 2
                        this.amount = ''
                    }, 500)
                })
            }
            //If a transfer request
            else{
                this.$store.dispatch('walletTransfer', {amount: this.amount, address: this.targetAddress})
                .then((res) => {
                    setTimeout(() => {
                        this.transferRes = res

                        if(res === "success") {
                            this.popupState = 2
                            this.amount = ''
                            this.targetAddess = ''
                        }
                        else{
                            this.popupState = 0
                            this.showPopup = false
                            this.isTargetAddressValid = false
                        }
                    }, 500)
                })
            }
        },
        handlePopoutClose() {
            this.showPopup = false
            this.popupState = 0
        },
        handlePublicAddressCopy() {
          navigator.clipboard.writeText(this.publicAddress)
          this.showCopyTooltip = true;
          setTimeout(()=>{this.showCopyTooltip=false},1000)
        },
        checkTargetAddress() {
            //
        }
    },
    computed: {
        isAmountValid() {
            return this.amount >= this.minTransferAmount && this.amount <= this.balance
        },
        isAmountEmpty() {
            return this.amount == null || this.amount === ""
        },
        amountMessage() {
            let message = this.$t("The minimum transfer is {minTransferAmount} BTT", {minTransferAmount: this.minTransferAmount})
            if (this.amount < this.minTransferAmount && !this.isAmountEmpty) {
                message = this.$t("Please enter at least {minTransferAmount} BTT", {minTransferAmount: this.minTransferAmount})
            }
            if (this.amount > this.balance && !this.isAmountEmpty) {
                message = this.$t("You don't have enough balance to create this transfer.")
            }

            return message

        },
        amountMessageColor() {
            return this.isAmountValid ? colors.btfsBlue500 : colors.gray700
        },
        isTargetAddressSame() {
            return this.targetAddress === this.publicAddress;
        },
        isTargetAddressEmpty() {
            return this.targetAddress == null || this.targetAddress === ""
        },
        targetAddressMessage() {
            let message = ""
            if (!this.isTargetAddressValid && !this.isTargetAddressEmpty){
                message = this.$t("The public address doesn't exist.")
            }
            if (this.isTargetAddressSame && !this.isTargetAddressEmpty){
                message = this.$t('You cannot transfer to the same wallet address.')
            }
            return message
        },
        isNumber() {
            return !isNaN(this.balance)
        },
        abbreviatedPublicAddress() {
          return this.publicAddress.slice(0,12) + '...' + this.publicAddress.slice(-10)
        },
    },
    watch: {
        dropdownSelection() {
            if(this.dropdownSelection === this.$t("Transfer to") || this.dropdownSelection === null){
                this.targetAddress = ""
                return
            }
            
            this.targetAddress = (this.dropdownSelection === this.$t("Other public address")) ? "" : this.defaultTargetAddress
        },
        targetAddress() {
            this.isTargetAddressValid = true
        }
    }
};
</script>

<template>
    <div class="wallet-balance-tile">
        <div class="upper">
            <p class="title">
                {{title}} 
                <svg-icon 
                :hasFill="true" 
                icon="help-outline" 
                class="help-icon" 
                v-tooltip.bottom="{ content: tooltipText, classes: ['tooltip'], hideOnTargetClick: false, autoHide: false}"/>
            </p>
            <div v-if="!isNumber" class="balance-loading">
                <img src="../assets/svg/spinner.svg" class="balance-loading-icon"/>
            </div>
            <p v-if="isNumber" class="balance">{{balance.toLocaleString('en-US', {maximumFractionDigits: 6}) }} BTT</p>
            <div class="public-key-container">
                <div>{{publicAddressLabel}}</div>
                <div class="public-key-id">
                    <span class="address">{{publicAddress}}</span>
                    <span class="address-abbreviate">{{abbreviatedPublicAddress}}</span>

                    <svg-icon
                        :hasFill="true" 
                        icon="copy" 
                        class="copy-icon" 
                        @click="handlePublicAddressCopy" 
                        v-tooltip.bottom="{ 
                          content: 'copied!', 
                          classes: ['tooltip'], 
                          show: showCopyTooltip,
                          trigger: 'manual'
                        }"
                      />
              </div>
            </div>
            <div v-if="isNumber" class="transfer-button-container">
                <Button type="text" :cb="handleExpand" :isOpen="isExpanded">{{$t('Transfer')}}</Button>
            </div>
        </div>
        <TransitionExpand>
            <div v-if="isExpanded" class="lower">
                <div class="lower-inner-container">
                    <bt-input-box 
                        v-model="amount"
                        inputType="number"
                        :placeholder="this.$t('Amount')"
                        :showMessage="true"
                        :messageColor="amountMessageColor"
                        :borderColor="colors.btfsBlue500"
                        :placeholderColor="colors.gray700"
                        :message="amountMessage"
                        :delayEmit="true"
                        :isInvalid="!isAmountValid && !isAmountEmpty"
                        v-if="(dropdownSelection !== this.$t('Other public address')) || !this.$slots.default"
                    />
    
                    <Dropdown :parentId="title" :placeholder="this.$t('Transfer to')" :options="dropdownOptions" @selection="(val) => dropdownSelection = val"/>
                    
                    <div 
                        class="coming-soon"
                        v-if="dropdownSelection === this.$t('Other public address') && this.$slots.default"
                        >
                            <!-- temporary under development text slot -->
                            <slot/>
                    </div>
                    <bt-input-box 
                        v-model="targetAddress"
                        inputType="text"
                        :placeholder="this.$t('Public address')"
                        :showMessage="true"
                        :borderColor="colors.btfsBlue500"
                        :placeholderColor="colors.gray700"
                        :message="targetAddressMessage"
                        v-if="dropdownSelection === this.$t('Other public address') && !this.$slots.default"
                        :delayEmit="true"
                        :isInvalid="(!isTargetAddressValid || isTargetAddressSame ) && !isTargetAddressEmpty"
                    />
                    <div class="button-container">
                        <Button :disabled="!this.isTargetAddressValid || this.isTargetAddressSame || this.isTargetAddressEmpty || !this.isAmountValid" :cb="handleSend">{{$t('Send')}}</Button>
                    </div>
                </div>
            </div>
        </TransitionExpand>
        <Popup :isOpen="showPopup" :closeable="popupState === 0" @onClose="handlePopoutClose" :blur="true">
            <div v-if="popupState === 0" class="text-center">
                <p class="popup-header">{{$t('Transfer confirmation')}}</p>
                <p class="popup-description">{{$t('You are about to send {amount} BTT.', {amount:this.amount})}}</p>
                <div class="popup-confirmation-buttons">
                    <Button :cb="handlePopoutClose" type="text">{{$t('Cancel')}}</Button>
                    <Button :cb="handlePopoutConfirm ">{{$t('Continue')}}</Button>
                </div>
            </div>
            <div v-if="popupState === 1" class="text-center">
                <p class="popup-header">{{$t('Transfer processing')}}</p>
                <p class="popup-description">{{$t('The transfer may take ~1 min before the balance change is shown.')}}</p>

                <img src="../assets/svg/spinner.svg" class="spinner-icon"/>
            </div> 
            <div v-if="popupState === 2" class="text-center">
                <p class="popup-header">{{$t(`transfer ${this.transferRes}`)}}</p>
                <p class="popup-description hide"></p>

                <div class="">
                    <Button :cb="handlePopoutClose">{{$t('Done')}}</Button>
                </div>
            </div> 
        </Popup>
    </div>
</template>

<style lang="scss" scoped>
.wallet-balance-tile {
    background: $gray-050;
    box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.2), 0px 3px 4px rgba(0, 0, 0, 0.12), 0px 2px 4px rgba(0, 0, 0, 0.14);
    border-radius: 4px;
    margin: 24px 0;

    .upper {
        display: flex;
        flex-direction: column;
        position: relative;
        padding: 24px;
        border-bottom: 1px solid $gray-300;

        p {
            display: flex;
            align-items: center;
        }

        .title {
            font-family: Lato;
            font-style: normal;
            font-weight: bold;
            font-size: 14px;
            line-height: 24px;
            letter-spacing: 2px;
            text-transform: uppercase;

            .help-icon{ 
                fill: $neutral-main;
                margin: 0 0 0 8px;
            }
        }

        .balance {
            font-family: Lato;
            font-style: normal;
            font-weight: bold;
            font-size: 34px;
            line-height: 40px;
            letter-spacing: 0.25px;
            color: $btfs-blue-500-main;
            margin: 24px 0 0 0;
        }

        .transfer-button-container {
            position: absolute;
            bottom: 24px;
            right: 24px;
        }

        .public-key-container {
            margin-top: 32px;
            font-size: 14px;
            color: $neutral-medium;
            font-weight: bold;

            .public-key-id {
                color: $neutral-main;
                line-height: 24px;
                margin: 8px 0 0 0;
                display: flex;
                align-items: center;

                .copy-icon {
                    margin: 0 0 0 10px;
                    fill: $neutral-main;
                    font-size: 1.25em;
                    cursor: pointer;
                }

                .address {
                    display: none;
                }

                .address-abbreviate {
                    display: block;
                }

            }
        }
    }

    .lower {
        display: flex;
        flex-direction: column;
        position: relative;

        .lower-inner-container {
            padding: 24px;

                .button-container {
                    width:100%;
                    display: flex;
                    justify-content: flex-end;
                }

                .coming-soon {
                    margin-bottom: 24px;
                }
        }
    }

    .balance-loading {
        height: 40px;
        margin: 24px 0 0 0;
        display: flex;
        align-items: center;

        .balance-loading-icon {
            width: 34px;
            position:relative;
            -webkit-animation:spin 1s linear infinite;
            -moz-animation:spin 1s linear infinite;
            animation:spin 1s linear infinite;
        }
    }
    @-moz-keyframes spin { 100% { -moz-transform: rotate(360deg); } }
    @-webkit-keyframes spin { 100% { -webkit-transform: rotate(360deg); } }
    @keyframes spin { 100% { -webkit-transform: rotate(360deg); transform:rotate(360deg); } }
}
@media (min-width: 850px) {
    .address {
        display:block !important;
    }
    .address-abbreviate {
        display:none !important;
    }
}
</style>