<script>
export default {
	name: 'Textbox',
	props: {
	},
    data() {
        return {
        }
    },
	methods: {
		handleScroll(){
			if(this.$el.scrollHeight === this.$el.scrollTop + this.$el.clientHeight){
				this.$emit('scrolled', true)
			}
		}
	}
};
</script>

<template>
	<div class="textbox-container" @scroll="handleScroll">
		<slot></slot>
	</div>
</template>

<style lang="scss" scoped>


.textbox-container{
	background: $gray-050;
	border: 2px solid $gray-300;
	box-sizing: border-box;
	border-radius: 4px;
	max-height:200px;
	margin:24px 0 24px 0;
	padding:16px;
	max-width:800px;
	overflow-x:scroll;

	p{
		font-size: 14px;
		line-height: 20px;
	}
}


</style>