<script>
import { mapActions, mapGetters } from 'vuex';
import { supportedLanguages } from '../utils/constants';
export default {
    name: 'LanguageSelector',
    computed: {
        ...mapGetters(['locale']),
    },
    created() {
        this.languages = supportedLanguages;
    },
    methods: {
        ...mapActions(['saveSettings']),
        async updateLanguage(language) {
            this.$root.$i18n.locale = language;
            await this.saveSettings({ locale: language });
        },
        getFlagClass(flagCode) {
            return `flag-icon flag-icon-${flagCode}`;
        },
    },
};
</script>

<template>
    <div class="language-selector-container">
        <bt-select :options="languages" :valueProp="locale" @option-updated="updateLanguage">
            <div slot="option-selected" slot-scope="optionSelected" class="option-selected">
                <div class="flag">
                    <!-- <span :class="getFlagClass(optionSelected.flagCode)" /> -->
                    <!-- BTC-1304: Replace img with above span once style.css is loaded the same way as bundle.js, remove flag-img class as well -->
                    <img :src="`https://www.countryflags.io/${optionSelected.flagCode}/flat/64.png`" class="flag-img" />
                </div>
                <div class="text">
                    {{ optionSelected.nativeName }}
                </div>
            </div>
            <div slot="option-item" slot-scope="option" class="option">
                <div class="flag">
                    <!-- <span :class="getFlagClass(option.flagCode)" /> -->
                    <!-- BTC-1304: Replace img with above span once style.css is loaded the same way as bundle.js, remove flag-img class as well -->
                    <img :src="`https://www.countryflags.io/${option.flagCode}/flat/64.png`" class="flag-img" />
                </div>
                <div class="text">
                    {{ option.nativeName }}
                </div>
            </div>
        </bt-select>
    </div>
</template>

