<script>
import Button from '../components/Button.vue'
import SvgIcon from '../components/svg-icon';
import EditHostFolder from '../components/EditHostFolder';
import Modal from './Modal';


export default {
	name: 'EditStorageSize',
	components: {
		Button,
		'svg-icon': SvgIcon,
		EditHostFolder,
		Modal

	},
	props: {
		show: {
			type: Boolean,
			default: false
		},
		storageSpace: {
			type:Object,
			default:function() {
				return {
					minimum: {
						type: Number,
						required: false,
						default:0
					},
					used: {
						type:Number,
						required:true,
						default:0
					},
					cap: {
						type:Number,
						required:true,
						default:0
					},
					total: {
						type:Number,
						required:true,
						default:1000
					}
				}
			}
		},
		hostDirectoryPath: {
			type: String,
			default: null
		},
		chooseOtherFolder: {
			type: Function,
			default: () => {}
		},
		//TODO: remove if unused
		checkOtherFolder: {
			type: Function,
			default: () => {}
		},
		getFolders: {
            type: Function,
            default: () => {}
        },
		getPartitions: {
			type: Function,
			default: () => {}
		},
	},
	data() {
		let data = this.initData()

		return {
			noSlideInput: this.storageSpace.cap,
			current: data,
			noSlideState:'',
			newHostDirectoryPath: this.hostDirectoryPath,
			isEditingFolder: false,
			showFolderSelector: false,
			isFolderValid: true

		}
	},
	methods: {
		handleCommitClick() {
			
			this.newFolderPromise()
			.then(() => {
				if(this.isCommitSizeDifferent) {
					this.$store.dispatch('storageAnnounce', {
						hostStorageMax: this.noSlideInput,
						local: false
					})
				}
			})

			this.$emit('close')

		},
		newFolderPromise() {
			return new Promise((resolve) => {

				if(this.newHostDirectoryPath === null || !this.isStorageLocationDifferent) {
					return resolve()
				}
				else {
					return this.chooseOtherFolder({path: this.newHostDirectoryPath, size: this.noSlideInput})
					.then(() => {
						return resolve()
					})
				}

			})
		},
		commitSizeCheck() {
			let inputBelowMin = this.noSlideInput < this.storageSpace.minimum
			let inputTooLow = this.noSlideInput < this.storageSpace.used 
			let inputTooHigh = this.noSlideInput > this.storageSpace.total
			if( inputBelowMin ) {
				this.noSlideState = "commit-invalid-below"
				return false
			}
			else if ( inputTooLow ) {
				this.noSlideState = "commit-invalid-low"
				return false
			}
			else if ( inputTooHigh ) {
				this.noSlideState = "commit-invalid-high"
				return false
			}
			else if ( this.noSlideInput === this.storageSpace.cap ) {
				return true
			}
			else {
				this.noSlideState = "commit-valid"
				this.current.used = this.noSlideInput
				this.current.available = this.storageSpace.total - this.noSlideInput
				this.noSlideInput = this.current.used
				return true
			}
		},
		handleInputFocus() {
			this.noSlideState = ""
			// this.noSlideInput = 0
		},
		handleInputBlur() {
			//round storage decimal values
			if(this.noSlideInput === ""){
				this.noSlideInput = this.storageSpace.cap
			}
			this.noSlideInput = Math.round(this.noSlideInput)
			this.commitSizeCheck()
		},
		initData(){
			if(this.storageSpace.total < this.storageSpace.minimum){
				this.isDisabled = true
			}
			let initialSpaceUsedPercent = ((this.storageSpace.cap - this.storageSpace.minimum) / (this.storageSpace.total - this.storageSpace.minimum)).toFixed(3)
			initialSpaceUsedPercent = parseFloat(initialSpaceUsedPercent)
			if(initialSpaceUsedPercent < 0){
				initialSpaceUsedPercent = 0
			}
			else if(initialSpaceUsedPercent > 1){
				initialSpaceUsedPercent = 1
			}
			let space = getStorageUsedAndRemaining(this.storageSpace.minimum, this.storageSpace.total, initialSpaceUsedPercent)

			return {
				used:space.used,
				available:space.available,
				initialPercent:initialSpaceUsedPercent
			}
		},
		onEditFolder(val) {
			this.newHostDirectoryPath = val
		}
	},
	computed: {
		canCommit() {
			return (this.isCommitSizeDifferent || this.isStorageLocationDifferent) && (this.noSlideState === '' || this.noSlideState === 'commit-valid')
		},
		earnings(){
			return gbToBTT(this.noSlideInput)
		},
		isCommitSizeDifferent() {
			return this.storageSpace.cap !== this.noSlideInput 
		},
		isStorageLocationDifferent() {
			return this.hostDirectoryPath !== this.newHostDirectoryPath
		},
		commitSizePercent() {
			return ((this.noSlideInput / this.storageSpace.total)*100).toFixed(0)
		},
		hostStoragePercent() {
			return ((this.storageSpace.used / this.storageSpace.cap)*100).toFixed(0)
		}
	},
	watch:{
		storageSpace : function(){
			let newData = this.initData()
			this.current = {
				...this.current,
				...newData
			}
		},
		noSlideInput : function() {
			this.commitSizeCheck()
		}
	},
	created() {
		//resets proposed storage value if user backs out without saving changes
		this.$store.dispatch('updateProposedStorageAvailable', null)
	}

};
function getStorageUsedAndRemaining(min, max, percent){
	let spaceUsed = (max - min) * percent + min
	let spaceAvailable = max - spaceUsed
	return {
		used:parseInt(spaceUsed.toFixed(3)),
		available:parseInt(spaceAvailable.toFixed(0))
	}
}

function gbToBTT(gb){
	let btt = gb * 8
	btt = parseInt(btt)
	return btt.toLocaleString()
}
</script>

<template>

<div class="edit-storage-size-bg" :class="{hidden: !show}">
	<Modal :show="show" @close="()=>{this.$emit('close')}" >
		<div class="edit-storage-size-container">
			<p class="header">{{$t('Edit host storage size')}}</p>
			<p class="description">
				{{$t('Change your storage commitment size in the input field below to see how much more you can earn.')}}
			</p>
			<div 
			class="no-slider-container">

				<div class="storage-commit-size">
					<p class="subtitle">
						{{$t('Storage commitment size')}}
					</p>
					<div class="storage-commit-size-container">
						<div>
							<input v-model="noSlideInput" @focus="handleInputFocus" @blur="handleInputBlur" type="number" :class="{unchanged: !isCommitSizeDifferent, invalid: !(noSlideState === '' || noSlideState === 'commit-valid')  }"> <b>{{$t("GB of {total} GB used", {total:this.storageSpace.total})}} ({{this.commitSizePercent}}%)</b>
						</div>
					</div>
					<div class="input-message-container">
						<div
						v-if="this.noSlideState === 'commit-invalid-low' || this.noSlideState === 'commit-invalid-below'"
						class="input-message invalid">
							<svg-icon icon="info" class="icon invalid" :hasFill="true" />
							<span v-if="this.noSlideState === 'commit-invalid-low'">
								{{$t('You cannot commit less than the current used storage size by BTFS')}}
							</span>
							<span v-if="this.noSlideState === 'commit-invalid-below'">
								{{$t('You cannot commit less than 32 GB')}}
							</span>
						</div>
						<div
						v-if="this.noSlideState === 'commit-invalid-high'"
						class="input-message invalid">
							<svg-icon icon="info" class="icon invalid" :hasFill="true" />
							{{$t('You do not have enough space on your hard drive')}}
						</div>
						<div
						v-if="this.noSlideState === '' || this.noSlideState === 'commit-valid'"
						class="input-message earnings">
							{{$t('Monthly potential earnings:')}} {{this.earnings}} BTT
						</div>
					</div>
					<p class="subtitle">

							{{$t('Host storage')}}
					</p>
					<p>
						<b> 
							{{this.storageSpace.used}} {{$t("GB of {total} GB used", {total:this.storageSpace.cap})}} ({{this.hostStoragePercent}}%)
						</b>
					</p>
				</div>

				<EditHostFolder
					:hostDirectoryPath="hostDirectoryPath"
					@inputChange = "onEditFolder"
					:checkOtherFolder = "checkOtherFolder"
					@isEditingFolder = "(val) => {isEditingFolder = val}"
					:getFolders = "getFolders"
					@isFolderValid = "(val) => {isFolderValid = val}"
					:getPartitions = "getPartitions"
				/>

			</div>

			<div class="button-container">
				<Button :isDisabled="!canCommit || !isFolderValid" :cb="handleCommitClick">{{$t('Save changes')}}</Button>
			</div>
		</div>
	</Modal>

</div>
</template>

