<script>
import Modal from './Modal';

export default {
    name: 'folder-selector-modal',
    props: {
        show: {
            type: Boolean,
            default: false
        },
        defaultFolder: {
            type: String,
            required: true
        },
        showFiles: {
            type: Boolean,
            default: false,
        },
        // Only allow users to select writeable folders
        writeOnly: {
            type: Boolean,
            default: false,
        },
        getFolders: {
            type: Function,
            required: true,
            default: () => {
                return {
                  subfolders: [{
                    name: 'folder1',
                    path: '/Users/testuser/Documents/folder1',
                    read: 'true',
                    write: 'true'
                  }],
                  subfiles: [{
                    name: 'file1',
                    path: '/Users/testuser/Documents/file1',
                    read: 'true',
                    write: 'true'
                  }],
                  meta: {
                    parent: "/Users/testuser",
                    drive: {
                      name: "Macintosh HD",
                      root: "/"
                    },
                    top: false
                  }  
                }
            }
        },
        getDrives: {
            type: Function,
            required: true,
            default: () => {
                return {
                    drives: [
                        { name: "Macintosh HD", path: "/Users/testuser", root: "/" },
                        { name: "External", path: "/Volumes/External", root:"/Volumes/External"}
                      ]
                }
            }
        },
        title: {
            type: String,
            default: function() { return 'Select folder'; }
        },
        folderDescription: {
            type: String,
            default: function() {
                return this.allowFiles ? 'Folder or file path' : 'Folder path';
            }
        },
    },
    data() {
        return {
            folderPathValue: this.defaultFolder,
            errorStr: '',
            isLoading: false,
            folderData: {},
            drivesData: {}
        };
    },
    components: {
        'modal': Modal
    },
    methods: {
        close() {
            this.$emit('close');
        },
        selectPath() {
            this.$emit('selectPath', this.folderPathValue);
        },
        handleGetFolders(path, read = true) {
            // NOTE: We could just disable clicks when a folder is disabled by setting pointer-events
            // to none, but this would cause the title attribute on the folder-title to not work since
            // it is a pointer-event on hover
            if (!read) {
                return;
            }
            this.showLoader();
            this.getFolders(path).then((response) => {
                if (response) {
                    this.folderData = response;
                    this.folderPathValue = this.getObjectNestedValue(this.folderData, 'meta', 'item_path');
                    // Scroll back to top when changing folders
                    this.$refs.foldersScrollContainer.scrollTop = 0;
                }
            }).catch(() => {
                // error
            }).then(() => {
                this.hideLoader();
            });
        },
        handleGetDrives() {
            this.getDrives().then((response) => {
                if (response) {
                    this.drivesData = response;
                }
            });
        },
        isDriveSelected(root) {
            const folderRoot = this.getObjectNestedValue(this.folderData, 'meta', 'drive', 'root');
            return folderRoot === root;
        },
        selectFile(filePath, read = true) {
            if (!this.allowFiles || !read) {
                return;
            }
            // This gets the folder data for the file parent folder (which should have been previously loaded),
            // but it will set the proper meta field values.
            this.showLoader();
            this.getFolders(filePath).then((response) => {
                if (response) {
                    this.folderData = response;
                    this.folderPathValue = this.getObjectNestedValue(this.folderData, 'meta', 'item_path');
                }
            }).catch(() => {
                // error
            }).then(() => {
                this.hideLoader();
            });
        },
        isFileSelected(filePath) {
            return this.folderPathValue === filePath;
        },
        showLoader() {
            this.loadingTimer = setTimeout(() => {
                this.isLoading = true;
            }, 250);
        },
        hideLoader() {
            clearTimeout(this.loadingTimer);
            this.isLoading = false;
        },
        getObjectNestedValue(obj, ...args) {
            return args.reduce((obj, level) => obj && obj[level], obj)
        }  
    },
    computed: {
        parentPath() {
            const parent = this.getObjectNestedValue(this.folderData, 'meta', 'parent');
            return parent || '';
        },
        hasParent() {
            const top = this.getObjectNestedValue(this.folderData, 'meta', 'top');
            return top !== true;
        },
        hasWrite() {
            // If undefined that means we were not able to get the write value, assume write permissions
            // so no error shows. An error would briefly show when the component is first loaded until
            // we get results from initial handleGetFolders call.
            const write = this.getObjectNestedValue(this.folderData, 'meta', 'write');
            return write === true || write === undefined;
        },
        hasError() {
            if (this.writeOnly && !this.hasWrite) {
                // this.errorStr = 'This folder is read-only, you cannot select it';
                return true;
            }
            // this.errorStr = '';
            return false;
        }
    },
    mounted() {
        this.handleGetDrives();
        this.handleGetFolders(this.folderPathValue);
    },
};
</script>

<template>
    <modal :show="show" @close="close">
        <div id="folder-selector-modal">
            <div class="folder-selector-header">{{title}}</div>
            <div class="modal-body">
                <div class="folder-input-title">{{folderDescription}}</div>
                <input class="folder-input"
                    v-model="folderPathValue"
                    disabled
                    @paste.stop
                    @keydown.stop>
                <div class="folder-error">{{errorStr}}</div>
                <div class="main-container">
                    <div class="drives-container">
                        <div v-bar="{ 'draggerStylerClass': 'vb-dragger-styler-dark' }">
                            <div>
                                <div class="drives-list">
                                    <div class="drive-item"
                                        :class="{ selected: isDriveSelected(drive.root) }"
                                        v-for="(drive, index) in drivesData.drives"
                                        :key="index"
                                        @click="handleGetFolders(drive.path)">
                                        <svg-icon icon="dns" class="drive-icon" :hasFill="true" />
                                        <div class="drive-title" :title="drive.name">{{ drive.name }}</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="folders-container">
                        <div v-bar="{ 'draggerStylerClass': 'vb-dragger-styler-dark' }">
                            <div ref="foldersScrollContainer">
                                <div class="loader-container" v-if="isLoading">
                                    <div class="loader"></div>
                                </div>
                                <div  class="folders-list">
                                    <div v-if="hasParent" class="back-item" @click="handleGetFolders(parentPath)">
                                        <svg-icon icon="arrow-back" class="arrow-back-icon" :hasFill="true" />
                                        <div class="back-title">{{$t('Back')}}</div>
                                    </div>
                                    <div class="folder-item"
                                        :class="{ disabled: !folder.read }"
                                        v-for="folder in folderData.subfolders"
                                        :key="folder.path"
                                        @click="handleGetFolders(folder.path)">
                                        <svg-icon icon="folder-open" class="folder-icon" :hasFill="true" />
                                        <div class="folder-title" :title="folder.name">{{ folder.name }}</div>
                                    </div>
                                    <template v-if="showFiles">
                                        <div class="file-item disabled" v-for="file in folderData.subfiles" :key="file.path">
                                            <svg-icon icon="insert-drive-file" class="folder-icon" :hasFill="true" />
                                            <div class="file-title" :title="file.name">{{ file.name }}</div>
                                        </div>
                                    </template>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="modal-two-button-footer">
                <bt-button btnType="secondary" @click="close">
                    {{$t('Cancel')}}
                </bt-button>
                
                <bt-button class="select-button" btnType="primary" :class="{ disabled: hasError }" @click="selectPath">
                    {{$t('Select')}}
                </bt-button>
            </div>
        </div>
    </modal>
</template>

