<script>
import SvgIcon from './svg-icon';

export default {
	name: 'Button',
	components: {
		'svg-icon': SvgIcon,
	},
	props: {
		isDisabled: {
			type: Boolean,
			default: false
		},
		cb: {
			type: Function,
			default: () => {}
		},
		type: {
			type: String,
			default: 'primary'
		},
		copyValue: {
			type: String,
			default: null
		},
		isOpen: {
			type: Boolean,
			required: false,
			default: undefined
		},
		size: {
			type: String,
			default: 'big'
		}
	},
	data() {
		return {
			hasCopied: false
		}
	},
	methods:{
		handleClick(e){
			if(this.isDisabled){
				e.preventDefault()
			}
			if(this.type === "copy" && this.copyValue){
				navigator.clipboard.writeText(this.copyValue)
				this.hasCopied = true
			}
		}
	},
};
</script>

<template>
	<button :disabled="isDisabled" @click="this.cb" :class="[type, {hasCopied: hasCopied}, size]" class="button">
		<span @click="handleClick">
			<slot v-if="!hasCopied"/>
			<span v-if="hasCopied"> {{$t('Copied!')}} </span>
			<svg-icon v-if="type==='text' && isOpen !== undefined" icon="down-arrow" class="icon" :hasFill="true" :class="{'up-arrow': isOpen}"/>
			<svg-icon v-if="type==='copy'" icon="copy" class="icon" :hasFill="true" />
		</span>
	</button>
</template>

<style lang="scss" scoped>


button{
	border-radius: 24px;
	font-weight: bold;
	font-size: 14px;
	line-height: 24px;
	cursor:pointer;
	outline: none;
	transition:.25s;
	position:relative;
	border:none;

	> span{
		position:relative;
		display: inline-flex;
	}

	&.primary {
		box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.2), 0px 2px 2px rgba(0, 0, 0, 0.12), 0px 0px 2px rgba(0, 0, 0, 0.14);
		background: $btfs-blue-500-main;
		color: $gray-050;
		padding: 8px 24px;


			&:hover{
				background: $btfs-blue-400;
			}

			&:before {
				background: $btfs-blue-600;
			}

			&:active {
				background: $btfs-blue-500-main;
			}

			&:disabled{
				opacity: 0.4;
			}
	}

	&.outline {
		background: white;
		border: 1px solid $btfs-blue-500-main;
		padding: 4px 16px;
		color: $btfs-blue-500-main;

		&:hover {
			background: $btfs-blue-100;
		}

		&:disabled {
			opacity: 0.3;
			cursor: default;

			&:hover {
				background: white;
			}
		}
	}

	&.text{
		background: $gray-050;
		color: $btfs-blue-500-main;
		padding: 8px 0px;
		font-size: 16px;

		.icon {
			margin: 0 0 0 14px;
			font-size: 0.5em;
			align-items: center;
			fill: $btfs-blue-500-main;

			&.up-arrow {
				transform: scale(-1);
			}
		}

		&:hover{
			color: $btfs-blue-300;

			.icon {
				fill: $btfs-blue-300;
			}
		}
	}

	&.copy {
		background: white;
		color: $btfs-blue-900;
		padding: 4px 16px;
		font-weight: 400;
		border: 1px solid $btfs-blue-500-main;

		&.hasCopied {
			background: $btfs-blue-050;
		}

		.icon {
			margin: 0 0 0 8px;
			font-size: 1em;
			align-items: center;
			fill: $btfs-blue-500-main;
		}

		&:hover {
			background: $btfs-blue-100;
		}

	}

	&.small {
		padding: 4px 16px;
	}

	&:before {
		content: "";
		position: absolute;
		width: 0;
		height: 0;
		top: 50%;
		left: 50%;
		transform-style: flat;
		transform: translate3d(-50%,-50%,0);
		border-radius: 24px;
		transition: width .2s ease, height .2s ease;
	}

	&:active {

		&:before {
			width: 100%;
			height: 100%;
		}
	}

	&:disabled{
		cursor:default;

		&:before {
			width:0;
			height:0;
		}
	}
}


</style>