<script>
import colors from '../scss/_theme.scss'
export default {
	name: 'SlideBar',
	props: {
		isDisabled: {
			type: Boolean,
			required: false,
			default:false
		},
		initialPercent: {
			type: Number,
			required: false,
			default:0
		},
		slideable: {
			type: Boolean,
			required: false,
			default:true
		},
		height: {
			type: Number,
			required: false,
			default: 8
		},
		fillColor: {
			type: String,
			required: false,
			default: colors.btfsBlue500
		},
		bgColor: {
			type: String,
			required: false,
			default: colors.btfsBlue100
		}
		//TODO: make knob size style customizable
	},
	data() {
		return {
			isDragging:false,
			knob:{
				x:0,
			},
			parent:{
				width:0,
				left:0
			},
			slidebarPercent:this.initialPercent
		}
	},
	methods: {
		handleMouseMove(e){
			let cursorX = e.x;

			let x = cursorX - this.parent.left
			if(x < 0){
				x = 0
			}
			else if(x > this.parent.width){
				x = this.parent.width
			}
			this.knob.x = x

			let percent = (this.knob.x/this.parent.width) || 0
			this.slidebarPercent = percent.toFixed(4)

			this.$emit('slideChange',this.slidebarPercent)

		},
		handleMouseUp(){
			window.removeEventListener('mousemove', this.handleMouseMove)
			window.removeEventListener('mouseup', this.handleMouseUp)
		},
		startDrag(){
			this.isDragging = true
			let { left, width } = this.$refs.slidebarBg.getBoundingClientRect()
			this.parent.width = width
			this.parent.left = left
			window.addEventListener('mousemove', this.handleMouseMove)
			window.addEventListener('mouseup', this.handleMouseUp)
		},
		handleResize(){
			let { left, width } = this.$refs.slidebarBg.getBoundingClientRect()
			this.parent.width = width
			this.parent.left = left
			this.knob.x = (this.parent.width * this.slidebarPercent).toFixed(0)
		}
	},
	computed: {
		bgStyle() {
			return {
				height:`${this.height}px`,
				backgroundColor:this.bgColor
			}
		},
		fillStyle() {
			let width = (this.slidebarPercent*100).toFixed(0);
			width = `${width}%`
			return {
				width:width,
				backgroundColor:this.fillColor
			}
		}
	},
	watch:{
		initialPercent(){
			this.slidebarPercent = this.initialPercent
			this.handleResize()
		}
	},
	mounted(){
		this.handleResize()
		window.addEventListener('resize', this.handleResize)
	},
	beforeDestroy(){
		window.removeEventListener('resize', this.handleResize)
	},
};

</script>

<template>
		<div 
			class="slidebar-bg" 
			:class="{'slidebar-disabled':this.isDisabled}" 
			:style="bgStyle"
			ref="slidebarBg">
			<div class="slidebar-fill" :class="{'slidebar-disabled':this.isDisabled}" :style="fillStyle"/>
			<div 
				v-if="!this.isDisabled && this.slideable"
				class="slidebar-knob" 
				:style="{left: `${this.knob.x-12}px`}" 
				@mousedown="startDrag">
			</div>
		</div>
</template>

<style lang="scss" scoped>


.slidebar{
	&-bg{
		position:relative;
		width:auto;
		border-radius: 8px;
	}

	&-fill{
		height:100%;
		border-radius: 8px;
	}

	&-knob{
		position:absolute;
		width:24px;
		height:24px;
		border-radius: 8px;
		background-color:$btfs-blue-500-main;
		top:-8px;
		cursor:grab;

		&:active{
			cursor:grabbing;
		}
	}

	&-disabled{
		background-color:$gray-500;
	}
}
</style>