<script>
import colors from '../scss/_theme.scss';
import StepIndicator from '../components/StepIndicator.vue'
import Button from '../components/Button.vue'
import PasswordCreate from '../components/PasswordCreate.vue'
import RecoveryPhrase from '../components/RecoveryPhrase.vue'
import Popup from '../components/Popup.vue';
import QRCode from 'qrcode'

export default {
	name: 'WalletSetup',
	props: {
		recoveryPhrases: {
			type: Array,
			default() {
				return ['Animated', 'Useful', 'Aunt', 'Grab', 'Visit', 'Sales', 'Brisk', 'Glory', 'Blind', 'Exotic', 'upset', 'valve'];
			}
		},
		setInitializationFlag: {
			type: Function,
			default: () => {}
		},
		getKeys: {
			type: Function,
			default: () => {}
		},
		walletInit: {
			type: Function,
			deafult: () => {}
		}
	},
	components: {
		StepIndicator,
		Button,
		PasswordCreate,
		RecoveryPhrase,
		Popup
	},
	data() {
		
		return {
			steps: ['1', '2', '3', '4'],
			current: 1,
			colors,
			passwordsMatch: false,
			recoveryPhraseMatches: null,
			password: '',
			showPopup: false,
			popupState: 0,
			qrCode: ''
		}
	},
	computed: {
		
	},
	methods: {
		handleRecoveryPhraseMatches(val) {
			this.recoveryPhraseMatches = val
		},
		nextStep() {
			this.current += 1
			if(this.current === 4){
				this.recoveryPhraseMatches = null;
			}
		},
		prevStep() {
			this.current -= 1
		},
		handleSetupComplete() {
			this.showPopup = true
            this.popupState = 0
			this.walletInit({password: this.password})
			.then(() => {
				return this.setInitializationFlag()
			})
			.then(() => {
				this.handlePopoutClose()
				this.$router.push('/wallet')
			})
			.catch((e) => {
				console.log(e)
				this.popupState = 1
			})
		},
		handlePasswordSet() {
			this.getKeys()
			.then (() => {
				if(this.recoveryPhrases === undefined || this.recoveryPhrases.length == 0) {
					this.handleSetupComplete()
				}
				else{
					this.nextStep()
				}

			})
		},
		handlePopoutClose() {
            this.showPopup = false
            this.popupState = 0
        },
        generateQrCode() {
			QRCode.toCanvas(this.recoveryPhrases.join(' '), {width: 140, margin: 0, color:{light:colors.neutralExtraLight}})
			.then((res) => {
				this.$refs.qrCode.appendChild(res)
			})
			.catch((err) => {
				console.log(err)
			})
        }
	},
	watch: {
		current(){
			if(this.current === 4){
				this.generateQrCode()
			}
		}
	},
}
</script>

<template>
	<div class="wallet-setup">
			<StepIndicator class="step-container" :steps="steps" :current="current"></StepIndicator>

			<div class="wallet-setup-1" v-if="current === 1">
				<div class="header-container">
					<p class="header">{{$t('Wallet password')}}</p>
					<p class="description">
						{{$t('Create a strong password and save it in a safe place. We do not have access to your password and will not be able to restore it. Losing your password may result in losing access to your funds.')}}
					</p>
				</div>

				<div class="password-container">
					<PasswordCreate @passwordsMatch="(val)=>{passwordsMatch = val}" @password="(val) => {password = val}"/>
				</div>

				<div class="text-container">
					<p class="text-sm">
						<i18n path="By clicking “Continue” you agree to the Terms of Service and Privacy Policy" tag="span">
							<a href="https://www.bittorrent.com/legal/terms-of-use/" target="_blank" rel="noreferrer noopener">{{$t('Terms of Service')}}</a>
							<a href="https://www.bittorrent.com/legal/privacy-policy/" target="_blank" rel="noreferrer noopener">{{$t('Privacy Policy')}}</a>
						</i18n>
					</p>
				</div>
				<div class="button-container">
					<div>
						<router-link to="/wallet/add" class="router-link">
							<bt-button btnType="secondary">{{$t('Back')}}</bt-button>
						</router-link>
					</div>
					<div>
						<Button :disabled="!passwordsMatch" :cb="handlePasswordSet">{{$t('Continue')}}</Button>
					</div>
				</div>
			</div>
			
			<div class="wallet-setup-2" v-if="current === 2">

				<div class="header-container">
					<p class="header">{{$t('Wallet recovery phrase')}}</p>
					<p class="description">
						{{$t('This is your wallet recovery phrase. Please copy this phrase and store it somewhere safe as there’s no way for us to help you recover this if it’s lost. ')}}
					</p>
				</div>
				<div class="recovery-right-container">
					<div class="recovery-answer-key-container" >
						<div v-for="(phrase, index) in recoveryPhrases"
							class="recovery-answer-key"
							:key="index">{{phrase}}
						</div>
					</div>
				</div>
				<div class="button-container copy-container">
					<Button type="copy" :copyValue="recoveryPhrases.join(' ')">{{$t('Copy recovery phrase to clipboard')}}</Button>
				</div>
				<div class="button-container">
					<div>
						<bt-button btnType="secondary" @click="prevStep">{{$t('Back')}}</bt-button>
					</div>
					<div>
						<bt-button btnType="primary" @click="nextStep">{{$t('Continue')}}</bt-button>
					</div>
				</div>
			</div>
			<div class="wallet-setup-3" v-if="current === 3">
				<div class="header-container">
					<p class="header">{{$t('Confirm recovery phrase')}}</p>
					<p class="description">
						{{$t('Confirm your recovery phrase by clicking on the words below and placing them in sequence in their corresponding tiles, starting from 1 and ending with 12.')}}
					</p>
				</div>
				<div class="recovery-match-container">
					<RecoveryPhrase :choices="recoveryPhrases" @recoveryPhraseMatches="handleRecoveryPhraseMatches"/>
					<p :class="{'hidden': recoveryPhraseMatches !== false }" class="error-message">The recovery phrase sequence is incorrect.</p>
				</div>
				<div class="button-container">
					<div>
						<bt-button btnType="secondary" @click="prevStep">{{$t('Back')}}</bt-button>
					</div>
					<div>
						<bt-button btnType="primary" :disabled="recoveryPhraseMatches !== true"  @click="nextStep">{{$t('Continue')}}</bt-button>
					</div>
				</div>
			</div>

			<div class="wallet-setup-4" v-if="current === 4">
				<div class="header-container">
					<p class="header">{{$t('Backup wallet recovery phrase with TronLink')}}</p>
					<p class="description">
						{{$t('If you don’t have it already, we recommend installing TronLink so you can scan the QR code to back up your wallet recovery phrase to your TronLink wallet.')}}
					</p>
				</div>

				<div class="container">
					<bt-card class="card">
						<div class="card-item">
							<div class="tronlink-container">
								<div>
									<img src="../assets/svg/tronlink.svg"/>
								</div>
								<div class="learn-tronlink"> 
									<a href="https://www.tronlink.org/" target="_blank" ref="noopener noreferrer">{{$t('Click here to learn about TronLink')}}</a>
								</div>
								<div class="app-badges-container">
									<a href="https://itunes.apple.com/us/app/tronlink/id1453530188" target="_blank" ref="noopener noreferrer">
										<img src="../assets/svg/app-store-badge.svg"/>
									</a>
									<a href="https://play.google.com/store/apps/details?id=com.tronlinkpro.wallet" target="_blank" ref="noopener noreferrer">
										<img src="../assets/svg/google-play-badge.svg"/>
									</a>
								</div>
							</div>
						</div>
						<div class="card-item">
							<div class="qr-container">
								<div class="qr-code" ref="qrCode" ></div>
								<div class="qr-text-1">{{$t('Scan with Tronlink')}}</div>
								<div class="qr-text-2">{{$t('Check your surroundings and make sure no one is around!')}}</div>
							</div>
						</div>
					</bt-card>
				</div>

				<div class="button-container">
					<div class="btn-1">
						<bt-button btnType="secondary" @click="handleSetupComplete">{{$t('Skip')}}</bt-button>
					</div>
					<div>
						<bt-button btnType="primary" @click="handleSetupComplete">{{$t('Done')}}</bt-button>
					</div>
				</div>
			</div>

			<Popup :isOpen="showPopup" @onClose="handlePopoutClose">
				<div class="text-center" v-if="this.popupState === 0">
					<p class="popup-header">{{$t('Creating wallet...')}}</p>
					<p class="popup-description">{{$t('This may take up to a minute.')}}</p>
					<img src="../assets/svg/spinner.svg" class="spinner-icon"/>
				</div>
				<div class="text-center" v-if="this.popupState === 1">
					<p class="popup-header">{{$t('System Error')}}</p>
					<p class="popup-description">{{$t('Please try again later')}}</p>
					<div class="button-container center">
						<Button :cb="handlePopoutClose">{{$t('Continue')}}</Button>
					</div>
				</div>
			</Popup>

	</div>
</template>

