import _ from 'lodash';
let iv = [0x02, 0x00, 0x01, 0x06, 0x00, 0x08, 0x01, 0x04, 0x02, 0x00, 0x01, 0x06, 0x00, 0x08, 0x01, 0x04];
var crypto = require("crypto")

function encrypt_with_aes(key, message) {
    let md5 = crypto.createHash('md5').update(key).digest('hex');
    const cipher = crypto.createCipheriv(
        'aes-128-cbc',
        new Buffer(md5, 'hex'),
        new Buffer(iv)
    );
    // cipher.setAutoPadding(true);
    var encrypted = cipher.update(message, 'utf8', 'base64');
    encrypted += cipher.final('base64');
    return encrypted;
}
function decrypt_with_aes(key, message) {
    let md5 = crypto.createHash('md5').update(key).digest('hex');
    const decipher = crypto.createDecipheriv(
        'aes-128-cbc',
        new Buffer(md5, 'hex'),
        new Buffer(iv)
    );
    var decrypted = decipher.update(message, 'base64', 'utf8');
    decrypted += decipher.final('utf8');
    return decrypted;
}
function ellipsifyText(container, numBackChars) {
    console.log('ellipsify', container)
    // reset container value to original value as long as original value is set in
    // title attribute. Allows ellipsify function to work on a window resize event.
    if (container.innerText < container.title) {
        container.innerText = container.title;
    }
    if (container.offsetWidth < container.scrollWidth) {
        let front = '';
        const back = container.innerText.slice(-1 * numBackChars);
        while (container.offsetWidth < container.scrollWidth) {
            front = container.innerText.slice(0, -1);
            container.innerText = front;
        }
        // make room for ellipsis and `back`.
        front = front.slice(0, -1 * (numBackChars + 1));
        container.innerText = `${front}...${back}`;
    }
    return container;
}

function copyToClipboard(value) {
    //TODO: verify browser compatibiliy of execCommand
    const ta = document.createElement('textarea');
    ta.value = value;
    ta.setAttribute('readonly', '');
    ta.style.position = 'absolute';
    ta.style.left = '-9999px';
    document.body.appendChild(ta);
    ta.select();
    document.execCommand('copy');
    document.body.removeChild(ta);
}

/**
 * returns an object with a uri-encoded subject and body
 * @param {string} subject - email subject
 * @param {object} environmentInfo - environment info object to be appended to footer of email
 */
function composeEmail(subject, environmentInfo) {
    let footer = '='.repeat(25);
    footer += _.reduce(environmentInfo, (acc, val, key) => {
        acc += `${key}: ${val}\n`;
        return acc;
    },'\n',);
    footer += '='.repeat(25);

    return {
        subject: encodeURI(subject),
        body: encodeURI(`\n\n${footer}`),
    };
}
/**
 * Creates an properly encoded string to pass to an href attribute for sending an email
 * @param {string} recipient - email url of the recipient
 * @param {string} title - email title/subject
 * @param {object} environmentInfo - object that has environment info to be appended to bottom of email
 */
function createEmailHref(recipient, title, environmentInfo) {
    const { subject, body } = composeEmail(title, environmentInfo);
    return `mailto:${recipient}?subject=${subject}&body=${body}`;
}
export default {
    ellipsifyText,
    copyToClipboard,
    createEmailHref,
    encrypt_with_aes,
    decrypt_with_aes
};
