<script>
import Button from '../components/Button.vue'
import Popup from '../components/Popup.vue';

export default {
	name: 'WalletAdd',
	props: {
		checkPassword: {
			type: Function,
			default: () => {}
		},
		importWallet: {
			type: Function,
			default: () => {}
		},
		setInitializationFlag: {
			type: Function,
			default: () => {}
		},
		setPassword: {
			type: Function,
			default: () => {}
		},
	},
	components: {
		Button,
		Popup
	},
	methods: {
		handleSignIn() {
			this.checkPassword({p: this.password})
			.then(() => {
				this.showPopup = true
				return this.importWallet({type: 'p', value: this.$store.state.wallet.speedWalletKey})
			})
			.then(() => {
				return this.setPassword({password: this.password})
			})
			.then(() => {
				return this.setInitializationFlag()
			})
			.then(() => {
				this.$store.dispatch('walletAuth', this.password)
				this.handlePopoutClose()
				this.$router.push('/wallet')
			})
			.catch((e) => {
				if(e === "invalid speed password") {
					this.passwordInvalid = true
				}
				else{
					this.popupState = 1
				}
			})
		},
		handlePopoutClose() {
			this.showPopup = false
			this.popupState = 0
		},
	},
	data() {
		return {
			password: null,
			passwordInvalid: false,
			showPopup: false,
			popupState: 0,

		}
	},
	computed: {
		isSpeedWalletRunning() {
			return this.$store.state.wallet.isSpeedWalletRunning
		},
		passwordMessage() {
			return (this.passwordInvalid) ? "Incorrect password" : ""
		},
	},
	mounted() {
		//check if Speed is running
		this.checkPassword({p:''})
	},
	watch: {
		password() {
			if(this.passwordInvalid){
				this.passwordInvalid = false
			}
		}
	}
}
</script>

<template>
	<div class="wallet-add">
		<div class="wallet-add-inner" v-if="!isSpeedWalletRunning">
			<div class="header-container">
				<p class="header">{{$t('Wallet setup')}}</p>
				<p class="description">
					{{$t('Please follow the guide below to properly set up and protect your wallet. The wallet will allow you to manage your funds by transferring digital tokens. ')}}
				</p>
				<p class="sub-description">
					<i18n path="Looking for more information? Read our FAQs" tag="span">
						<a href="https://www.bittorrent.com/token/bittorrent-file-system/faq/" target="_blank" rel="noreferrer noopener">{{ $t('FAQs') }}</a>
					</i18n>
				</p>
			</div>

			<div class="main-container">
				<div class="container card left">
					<p class="text-lg">{{$t('Create Wallet')}}</p>
					<p class="text-md">{{$t('Click here if you don’t have an ID on BitTorrent Wallet.')}}</p>
					<div class="button-container">
						<router-link to="/wallet/setup/">
							<Button>{{$t('Create')}}</Button>
						</router-link>
					</div>
				</div>

				<div class="container card">
					<p class="text-lg">{{$t('Import Wallet')}}</p>
					<p class="text-md">{{$t('Click here if you already have a BitTorrent Wallet ID.')}}</p>
					<div class="button-container">
						<router-link to="/wallet/import/">
							<Button>{{$t('Import')}}</Button>
						</router-link>
					</div>
				</div>
			</div>
		</div>
		<div class="wallet-add-inner" v-if="isSpeedWalletRunning">
			<div class="wallet-sign-in">
				<div class="header-container">
					<p class="header">{{$t('Sign in to your BitTorrent Wallet')}}</p>
					<p class="description">
						<i18n path="It looks like you have BitTorrent Speed Wallet setup already. Please enter your password to continue." tag="span">
							<svg-icon icon="speed" class="speed-icon"/>
						</i18n>
					</p>
					<bt-input-box
					:label="this.$t('Password')"
					inputType="password" 
					@input="(val) => password = val" 
					:isInvalid="passwordInvalid"
					:message="passwordMessage"
					:placeholder="this.$t('Enter password')"
					:value="password"
					/>
					<div class="button-container">
					<Button :cb="handleSignIn" :isDisabled="password === null || passwordInvalid">Continue</Button>
					</div>
					<div class="line"/>
					<p class="sub-description">
					<i18n path="Don't remember you password? You can import your wallet using your recovery phrase and private key or create a new one here" tag="span">
						<router-link to="/wallet/import" class="link">{{$t('import')}}</router-link>
						<router-link to="/wallet/setup" class="link">{{$t('here')}}</router-link>
					</i18n>
					</p>
				</div>
			</div>
		</div>
		<Popup :isOpen="showPopup" @onClose="handlePopoutClose">
			<div class="text-center" v-if="this.popupState === 0">
				<p class="popup-header">{{$t('Importing...')}}</p>
				<p class="popup-description">{{$t('This transfer may take up to a minute.')}}</p>
				<img src="../assets/svg/spinner.svg" class="spinner-icon"/>
			</div>
			<div class="text-center" v-if="this.popupState === 1">
				<p class="popup-header">{{$t('System Error')}}</p>
				<p class="popup-description">{{$t('Please try again later')}}</p>
				<div class="button-container center">
					<Button :cb="handlePopoutClose">{{$t('Continue')}}</Button>
				</div>
			</div>
		</Popup>
	</div>
</template>

