import Vue from 'vue';
import routes from './routes';
const { request } = routes;

const state = {
    host: {
        initialized: false,
    },
    wallet: {
        initialized: false
    },
    hostEnabled: {
        value: true
    },
    hostRepairEnabled: false,
    hostChallengeEnabled: false
};
const mutations = {
    setHostConfig(s, res) {
        Vue.set(s, 'host', {initialized: res.Value});
    },
    setHostChallengeEnabled(s, res) {
        Vue.set(s, 'hostChallengeEnabled', res.Value);
    },
    setHostRepairEnabled(s, res) {
        Vue.set(s, 'hostRepairEnabled', res.Value);
    },
    setWalletConfig(s, res) {
        Vue.set(s, 'wallet', {initialized: res.Value});
    },
    setStorageHostEnabled(s, res) {
        Vue.set(s, 'hostEnabled', {value: res.Value});
    },
};
const getters = {
    config(s) {
        return s;
    }
};
const actions = {
    btfsRestart() {
        return request({
            endpoint: 'v1/restart',
            method: 'POST',
        })
        .catch(() => {
            // hack to handle btfs empty response
            // btfs should return 200 before it resets
            return Promise.resolve()
        })
    },
    getHostConfig(context) {
        return request({
            endpoint: 'v1/config',
            method: 'POST',
            params: {
                arg:'UI.Host.Initialized'
            }
        }).then((res) => {
            context.commit('setHostConfig', JSON.parse(res));
        });
    },
    setHostConfig(context) {
        return request({
            endpoint: 'v1/config',
            method: 'POST',
            params: {
                arg:'UI.Host.Initialized&arg=true'
            }
        }).then(() => {
            return context.dispatch('getHostConfig')
        })
    },
    getHostChallengeEnabled(context) {
        return request({
            endpoint: 'v1/config',
            method: 'POST',
            params: {
                arg:'Experimental.HostChallengeEnabled'
            }
        }).then((res) => {
            const data = JSON.parse(res)
            context.commit('setHostChallengeEnabled', data);
        });
    },
    setHostChallengeEnabled(context, val) {
        return request({
            endpoint: 'v1/config',
            method: 'POST',
            params: {
                arg:`Experimental.HostChallengeEnabled&arg=${val}`
            }
        }).then(() => {
            return context.dispatch('btfsRestart')
        }).then(() => {
            return context.dispatch('appReset')
        }).then(() => {
            return context.dispatch('getHostChallengeEnabled')
        })
    },
    getHostRepairEnabled(context) {
        return request({
            endpoint: 'v1/config',
            method: 'POST',
            params: {
                arg:'Experimental.HostRepairEnabled'
            }
        }).then((res) => {
            const data = JSON.parse(res)
            context.commit('setHostRepairEnabled', data);
        });
    },
    setHostRepairEnabled(context, val) {
        return request({
            endpoint: 'v1/config',
            method: 'POST',
            params: {
                arg:`Experimental.HostRepairEnabled&arg=${val}`
            }
        }).then(() => {
            return context.dispatch('btfsRestart')
        }).then(() => {
            return context.dispatch('appReset')
        }).then(() => {
            return context.dispatch('getHostRepairEnabled')
        })
    },
    getWalletConfig(context) {
        return request({
            endpoint: 'v1/config',
            method: 'POST',
            params: {
                arg:'UI.Wallet.Initialized'
            }
        }).then((res) => {
            context.commit('setWalletConfig', JSON.parse(res));
        });
    },
    setWalletConfig(context) {
        return request({
            endpoint: 'v1/config',
            method: 'POST',
            params: {
                arg:'UI.Wallet.Initialized&arg=true'
            }
        }).then(() => {
            return context.dispatch('getWalletConfig')
        })
    },
    getStorageHostEnabled(context) {
        return request({
            endpoint: 'v1/config',
            method: 'POST',
            params: {
                arg:'Experimental.StorageHostEnabled'
            }
        }).then((res) => {
            context.commit('setStorageHostEnabled', JSON.parse(res));
        });
    },

};

export default {
    state,
    getters,
    mutations,
    actions,
};