<script>
import { mapActions, mapGetters } from 'vuex';
import Button from '../components/Button.vue'
import Notification from '../components/Notification.vue'
import colors from '../scss/_theme.scss';
import Popup from '../components/Popup.vue';
import LanguageSelector from '../components/LanguageSelector.vue';

export default {
	name: 'Dashboard',
	props: {
		version: {
			type: String,
			required: true
		},
		hostChallengeEnabled: {
			type:Boolean,
			required:false,
			default:false
		},
		hostRepairEnabled: {
			type:Boolean,
			required:false,
			default:false
		},
		id: {
			type: String,
			required: true
		},
		notificationState: {
			type: String,
			default: ''
		},
		notificationClose: {
			type: Function,
			default: () => {}
		},
		hostPricing: {
			type: Number,
		},
	},
	components: {
		Button,
		Notification,
		Popup,
		LanguageSelector
	},
	methods: {
		...mapActions(['storageAnnounceDisableCustomizedPricing', 'storageAnnouncePrice']),
		handleNavClick(navItem) {
			this.selectedNavItem = navItem
		},
		toggleAdvanced() {
			if(this.isCustomizedPricing){
				this.storageAnnounceDisableCustomizedPricing()
			}
			this.showAdvanced = !this.showAdvanced
		},
		notificationDone() {
			return this.$store.dispatch('notificationComplete', 'settings')
		},
		handlePopupClose() {
			this.showPopup = false
		},
		handlePopupOpen(val) {
			this.popupState = val
			this.showPopup = true
		},
		handlePriceUpdate() {

			this.storageAnnouncePrice({hostStoragePrice : this.newHostPricing})
			.then(() => {
				return this.notificationDone()
			})
		},
		toggleChallengeMode() {
			this.popupState = 2
			this.$store.dispatch('setHostChallengeEnabled', !this.hostChallengeEnabled)
			.then(() => {
				this.showPopup = false
			})
		},
		toggleRepairMode() {
			this.popupState = 2
			this.$store.dispatch('setHostRepairEnabled', !this.hostRepairEnabled)
			.then(() => {
				this.showPopup = false
			})
		}
	},
	data() {
		return {
			navList: [
				'host',
			],
			selectedNavItem: 'host',
			showCopyTooltip: false,
			showAdvanced: false,
			newHostPricing: this.hostPricing,
			colors,
			showPopup: false,
			popupState: 0 //popup dialogue repair mode = 0, challenge mode = 1
		}
	},
	computed: {
		...mapGetters(['isCustomizedPricing', 'storagePriceDefault']),
		showNotification() {
			return this.notificationState !== ''
		},
		isPricingValid() {
			return this.newHostPricing > 0 && this.newHostPricing <= 30412800000
		}
	},
	mounted() {
		this.showAdvanced = this.isCustomizedPricing
		this.newHostPricing = this.hostPricing
	},
	beforeDestroy() {
	},
	watch: {
		hostPricing() {
			this.showAdvanced = this.isCustomizedPricing
			this.newHostPricing = this.hostPricing
		}
	}
}
</script>

<template>
	<div class="settings">
		<p class="title">{{$t('Settings')}}</p>
		<div class="settings-nav">
			<div
				v-for="navItem in navList" 
				:key="navItem"
				class="settings-nav-item"
				:class="{'settings-nav-item-selected': navItem === selectedNavItem}"
				@click="()=> {handleNavClick(navItem)}"
			>
				<!-- {{ navItem }} -->
			</div>
		</div>
		<div 
			v-if="selectedNavItem === 'host'"
			class="settings-host"
		>

			<div class="subtitle-container">
				<p class="subtitle">
					{{$t('Pricing tb/month')}}
				</p>
				<div>
					<div class="toggle-container">
						<div class="toggle-wrapper">
							<bt-toggle
								:checked="showAdvanced" 
								:backgroundColor='colors.primary500main'
							/>
							<div class="toggle-overlay" @click="toggleAdvanced"/> 
						</div>
						<div class="subtext">
							<span v-if="!showAdvanced" class="disabled">{{$t('Default Pricing')}}</span>
							<span v-else class="enabled">{{$t('Custom Pricing')}}</span>
						</div>
					</div>
					<!-- <Button type="outline" :isDisabled="showAdvanced" :cb="toggleAdvanced"> 
						<div 
						class="button" 
						v-tooltip.bottom="{ 
							content: 'Edit monthly host pricing', 
							classes: ['tooltip'], 
						}">
							Edit 
							<svg-icon icon="edit" :hasFill="true" class="icon"/>
						</div>
					</Button> -->
				</div>
			</div>
			<div class="content">
				{{hostPricing.toLocaleString()}} BTT

				<div v-if="showAdvanced">
					<div class="pricing">
						<div class="">
							{{$t('Edit Pricing')}}
						</div>
						<div class="pricing-input">
							<input v-model="newHostPricing" type="number" :class="{invalid: !isPricingValid}"/> <span class="subtitle">BTT</span>
						</div>
					</div>
					<p v-if="!isPricingValid" class="assistive-text invalid">{{$t('Please enter a valid amount')}}</p>
					<p v-if="isPricingValid" class="assistive-text">{{$t('Suggested price:')}} {{this.storagePriceDefault.toLocaleString()}} BTT</p>
					<div class="pricing-description">
						{{$t('Changes to advanced settings can impact your host earnings. Please make sure you understand the effects before making any changes.')}}
						<br/><br/>
						<i18n path="Learn more on our FAQs page" tag="span">
							<a href="https://www.bittorrent.com/token/bittorrent-file-system/faq/" target="_blank" rel="noreferrer noopener">{{ $t('FAQs page') }}</a>
						</i18n>.
					</div>
					<div class="button-container">
						<Button :cb="handlePriceUpdate" :isDisabled="!isPricingValid || newHostPricing == hostPricing">{{$t('Save changes')}}</Button>
					</div>
				</div>
			</div>

			<div class="subtitle-container">
				<p class="subtitle">
					{{$t('Challenge Mode')}}
				</p>
				<div class="toggle-container">
					<div class="toggle-wrapper">
						<bt-toggle
							:checked="hostChallengeEnabled" 
							:backgroundColor='colors.primary500main'
						/>
						<div class="toggle-overlay" @click="handlePopupOpen(1)"/> 
					</div>
					<div class="subtext" :class="{'disabled': !hostChallengeEnabled}">
						<span v-if="!hostChallengeEnabled" class="disabled">{{$t('Off')}}</span>
						<span v-else class="enabled">{{$t('On')}}</span>
					</div>
				</div>
			</div>
			<div class="content">
				<p>
					{{$t('Check the integrity and validity of files stored by Host nodes')}}
				</p>
			</div>

			<div class="subtitle-container">
				<p class="subtitle">
					{{$t('Repair Mode')}}
				</p>
				<div class="toggle-container">
					<div class="toggle-wrapper">
						<bt-toggle
							:checked="hostRepairEnabled" 
							:backgroundColor='colors.primary500main'
						/>
						<div class="toggle-overlay" @click="handlePopupOpen(0)"/> 
					</div>
					<div class="subtext" :class="{'disabled': !hostRepairEnabled}">
						<span v-if="!hostRepairEnabled" class="disabled">{{$t('Off')}}</span>
						<span v-else class="enabled">{{$t('On')}}</span>
					</div>
				</div>
			</div>
			<div class="content">
				<p>
					{{$t('Turn on to help repair broken file shards')}}
				</p>
			</div>

			<div class="subtitle-container">
				<p class="subtitle">
					{{$t('Language')}}
				</p>
				<div class="language-selector-container">
					<LanguageSelector/>
				</div>
			</div>
			<p class="content">
				{{$t('Choose languages')}}
			</p>

			<div class="subtitle-container">
				<p class="subtitle">
					{{$t('host node id')}}
				</p>
				<div>
					<Button type="copy" :copyValue="id"> 
							{{$t('Copy')}}
					</Button>
				</div>
			</div>
			<div class="content node-id">
				<p ref="nodeId" id="node-id">
					{{ id }}
				</p>
			</div>

			<div class="subtitle-container">
				<p class="subtitle">
					{{$t('current version')}}
				</p>
			</div>
			<p class="content">
				{{ version }}
			</p>

		</div>
		<div
			v-if="selectedNavItem === 'wallet'"
			class="settings-wallet"
		>

		</div>
		<Notification v-if="showNotification" :state="notificationState" :handleClose="notificationClose">
			<template #complete>
			Pricing updated
			</template>
		</Notification>
		<Popup :isOpen="showPopup" :closeable="popupState === 0 || popupState === 1" @onClose="handlePopupClose" :blur="true">
            <div v-if="popupState === 0" class="text-left">
                <p class="popup-header">{{$t('BTFS needs to restart')}}</p>
                <p class="popup-description">{{$t(`BTFS needs to restart in order to turn ${this.hostRepairEnabled ? 'off' : 'on'} Repair Mode. This usually only takes a couple of minutes.`)}} <br/><br/> {{$t('Would you like to continue?')}}</p>
                <div class="popup-confirmation-buttons ">
                    <Button type="text" :cb="handlePopupClose">{{$t('Cancel')}}</Button>
                    <Button :cb="toggleRepairMode">{{$t('Restart')}}</Button>
                </div>
            </div>
            <div v-if="popupState === 1" class="text-left">
                <p class="popup-header">{{$t(`Restart BTFS to turn ${this.hostChallengeEnabled ? 'off' : 'on'} Challenge Mode`)}}</p>
                <p class="popup-description">{{$t(`This usually takes a couple of minutes.`)}} <br/><br/> {{$t('Would you like to continue?')}}</p>
                <div class="popup-confirmation-buttons ">
                    <Button type="text" :cb="handlePopupClose">{{$t('Cancel')}}</Button>
                    <Button :cb="toggleChallengeMode">{{$t('Restart')}}</Button>
                </div>
            </div>
            <div v-if="popupState === 2" class="text-center">
                <p class="popup-header">{{$t('Restarting BTFS')}}</p>
                <p class="popup-description">{{$t('This may take up to a minute.')}}</p>
                <img src="../assets/svg/spinner.svg" class="spinner-icon"/>
            </div> 
        </Popup>
	</div>
</template>

