		font-weight: bold;
		font-size: 34px;
		line-height: 40px;
		letter-spacing: 0.25px;
		color: $btfs-blue-800;
	}

	.settings-nav {
		display:flex;
		border-bottom:1px solid #E0E0E0;
		width: 100%;

		&-item {
			font-weight: bold;
			font-size: 20px;
			line-height: 24px;
			letter-spacing: 0.25px;
