			cursor: pointer;

			&-selected {
				color: $btfs-blue-500-main;
			}
		}
	}

	.settings-host {
		width:100%;
		box-sizing: border-box;

		.assistive-text {
			font-size: 12px;
			line-height: 1.33;
			letter-spacing: 0.4px;
			color: $btfs-blue-500-main;
			margin: 8px 0 0 0;

			&.invalid {
				color: $accents-red-main;
			}
		}

		.subtitle-container {
			display: flex;
			justify-content: space-between;
			margin: 24px 0 0 0;

			.language-selector-container {
				position: relative;
			}

			.subtitle {
				font-weight: bold;
				font-size: 14px;
				line-height: 24px;
				letter-spacing: 2px;
				text-transform: uppercase;
				color: $gray-500;
				margin: 0 0 16px 0;
			}

			.toggle-container {
				position: relative;

				.subtext {
					white-space: nowrap;
					right:0;
					margin: 8px 0 0 0;
					color: $neutral-main;
					display: flex;
					position: absolute;

					&.disabled {
						left:0;
					}

					.enabled {
						color: $primary-500-main;
						width: 100%;
					}
