						height: 100%;
						z-index: 2;
					}
				}
			}

			.button {
				font-size: 14px;
				display: flex;
				align-items: center;
				line-height: 22px;

				.icon {
					margin: 0 0 0 8px;
					fill: $btfs-blue-500-main;
				}
			}
		}

		.content {
			font-weight: bold;
			font-size: 14px;
			line-height: 24px;
			color:$neutral-main;
			position:relative;
			padding: 0 0 24px 0;
			border-bottom: 1px solid #E0E0E0;

		}

		.pricing{
			margin: 24px 0 4px 0;


			.pricing-input {
				margin: 8px 0 0 0;

				input {
					width: 102px;
					height: 32px;
					border-radius: 4px;
					font-size: 14px;
					font-weight: bold;
					line-height: 1.71;
					border: solid 1px $gray-500;
					box-sizing: border-box;
					padding: 4px 8px;
					text-align: center;
					outline: none !important;

					&:focus {
						border-color: $primary-blue-main;
					}

					&.invalid {
						border-color: $accents-red-main;

					}
				}
			}
		}

		.pricing-description {
			margin:24px 0 0 0;
		}

		.button-container {
			display: flex;
			justify-content: flex-end;
			margin: 24px 0px 0 0;

				.cancel {
					margin: 0 24px 0 0;
				}
		}

		.node-id {
			display: flex;
