<script>
import DashboardStat from '../components/DashboardStat.vue'
import ViewStorageSize from '../components/ViewStorageSize.vue'
import EditStorageSize from '../components/EditStorageSize.vue'
import Notification from '../components/Notification.vue'
import HostScoreCard from '../components/HostScoreCard'
import HostScoreHistory from '../components/HostScoreHistory'

export default {
  name: 'HostDashboard',
  props: {
    online: {
      type:Boolean,
      required:false,
      default:false
    },
    hostChallengeEnabled: {
      type:Boolean,
      required:false,
      default:false
    },
    hostRepairEnabled: {
      type:Boolean,
      required:false,
      default:false
    },
    score: {
      type:Number,
      required:false,
      default:0
    },
    uptime: {
      type:Number,
      required:false,
      default:0
    },
    contractsNum: {
      type:Number,
      required:false,
      default:0
    },
    earnings: {
      type:Number,
      required:false,
      default:0
    },
    potentialEarnings: {
      type:Number,
      required:false,
      default:0
    },
    hostPricing: {
      type:Number,
      required:false,
      default:0
    },
    nextPayment: {
      type:String,
      required:false,
      default:""
    },
    storageSpace: {
      type:Object,
      default:function() {
        return {
          minimum: {
            type: Number,
            required: false,
            default:0
          },
          used: {
            type:Number,
            required:true,
            default:0
          },
          total: {
            type:Number,
            required:true,
            default:1000
          }
        }
      }
    },
    isSynced: {
      type:Boolean,
      required:true,
      default:false
    },
    isSyncedLocal: {
      type:Boolean,
      required:true,
      default:false
    },
    isStorageLoading: {
      type:Boolean,
      required:true,
      default:true
    },
    isImporting: {
      type:Boolean,
      required:true,
      default:false
    },
    hostDirectoryPath: {
      type: String,
      default: null
    },
    chooseOtherFolder: {
      type: Function,
      default: () => {}
    },
    checkOtherFolder: {
      type: Function,
      default: () => {}
    },
    notificationState: {
      type: String,
      default: ''
    },
    notificationClose: {
      type: Function,
      default: () => {}
    },
    handleRefresh: {
      type: Function,
      default: () => {}
    },
    hostScoreHistory: {
      type: Array,
      default: () => []
    },
    getHostScoreHistory: {
      type: Function,
      default: () => {}
    },
    hostStats: {
      type:Object,
      default:function() {
        return {
          uptimeScore: {
            type:Number,
            required:true,
            default:0
          },
          ageScore: {
            type:Number,
            required:true,
            default:0
          },
            versionScore: {
            type:Number,
            required:true,
            default:0
          },
          uploadSpeedScore: {
            type:Number,
            required:true,
            default:0
          },
          downloadSpeedScore: {
            type:Number,
            required:true,
            default:0
          },
          uptimeWeight: {
            type:Number,
            required:true,
            default:0
          },
          ageWeight: {
            type:Number,
            required:true,
            default:0
          },
          versionWeight: {
            type:Number,
            required:true,
            default:0
          },
          uploadSpeedWeight: {
            type:Number,
            required:true,
            default:0
          },
          downloadSpeedWeight: {
            type:Number,
            required:true,
            default:0
          },
          lastUpdated: {
            type: String,
            required: true,
            default:''
          }
        }
      }
    },
    getFolders: {
      type: Function,
      default: () => {}
    },
    getPartitions: {
      type: Function,
      default: () => {}
    },
  },
  components: {
    DashboardStat,
    ViewStorageSize,
    EditStorageSize,
    Notification,
    HostScoreCard,
    HostScoreHistory,

  },
  methods: {
    onRefresh() {
      this.refresh()
    },
    toggleEditStorageSize() {
      this.showEditStorageSize = !this.showEditStorageSize
    },
  },
  data() {
    return {
      showEditStorageSize: false,
    }
  },
  computed: {
    challengeModeTooltip() {
      if(this.hostChallengeEnabled){
        return this.$t('Go to settings if you want to turn Challenge Mode off.', {settings:`<a  href='#/settings'>${this.$t("settings")}</a>`})
      }
      return this.$t('Go to settings to turn Challenge Mode on to assist in checking the integrity and validity of files already stored by Host nodes.', {settings: `<a  href='#/settings'>${this.$t('settings')}</a>`})
    },
    challengeModeText() {
      return (this.hostChallengeEnabled) ? this.$t('On') : this.$t('Off')
    },
    onlineStatusText() {
      return (this.online) ? this.$t('Online') : this.$t('Offline')
    },
    onlineStatusTooltip() {
      if(this.online){
        return this.$t('You are connected to BTFS network and hosting.')
      }
      return `${this.$t('You are not connected to BTFS network, this could be due to many reasons, please check FAQs:')} <a href='https://docs.btfs.io/docs/faq' target='_blank' rel='noreferrer noopener'>https://docs.btfs.io/docs/faq</a>`
    },
    repairModeTooltip() {
      if(this.hostRepairEnabled){
        return this.$t('Go to settings if you want to turn Repair Mode off.', {settings:`<a  href='#/settings'>${this.$t("settings")}</a>`})
      }
      return this.$t('Go to settings and turn Repair Mode on to help repair broken file shards.', {settings: `<a  href='#/settings'>${this.$t('settings')}</a>`})
    },
    repairModeText() {
      return (this.hostRepairEnabled) ? this.$t('On') : this.$t('Off')
    },
    showNotification() {
      return this.notificationState !== ''
    },
    isSyncing() {
      return (!this.isSynced && !this.isSyncedLocal) || this.isImporting
    },
  },
  mounted() {
    this.handleRefresh()

  }
}


</script>

<template>
  <div class="dashboard" v-if="!isStorageLoading">
      <div class="dashboard-header">
        <DashboardStat 
          :title="this.$t('Status')"
          :tooltipText="onlineStatusTooltip">
          <span class="online-status" :class="{offline: !online, hidden: isSyncing}">{{onlineStatusText}}</span>
        </DashboardStat>
        <DashboardStat 
          :title="this.$t('Repair Mode')"
          :tooltipText="repairModeTooltip">
          <span class="online-status" :class="{offline: !hostRepairEnabled, hidden: isSyncing}">{{repairModeText}}</span>
        </DashboardStat>
        <DashboardStat 
          :title="this.$t('Challenge Mode')"
          :tooltipText="challengeModeTooltip">
          <span class="online-status" :class="{offline: !hostChallengeEnabled, hidden: isSyncing}">{{challengeModeText}}</span>
        </DashboardStat>
        <DashboardStat 
          :title="this.$t('Uptime')"
          :tooltipText="this.$t('Percentage of time when a host is online and can prove the file replication.')">
          <span :class="{hidden: isSyncing}">{{(uptime*100).toFixed(0)}}%</span>
        </DashboardStat>
        <DashboardStat 
          :title="this.$t('Contracts')"
          :tooltipText="this.$t('Each contract represents a host’s commitment to a certain file shard’s storage length, size, and price.')">
          <span :class="{hidden: isSyncing}">{{contractsNum}}</span>
        </DashboardStat>
      </div>
      <div class="dashboard-container"> 

        <div class="host-score-grid">
          <div class="host-score-factor-card">
            <HostScoreCard :hostStats = "hostStats" :score = "score"/>
          </div>
          <div class="host-score-history-card">
            <HostScoreHistory :getHostScoreHistory="getHostScoreHistory" :hostScoreHistory="hostScoreHistory"/>
          </div>
        </div>
        
        <div class="earnings-container">
          <div class="earnings">
            <p class="title">
              {{$t('Earnings to-date')}}
            </p>
            <p class="amount">
              <span :class="{hidden: isSyncing}">{{earnings.toLocaleString('en-US', {maximumFractionDigits: 6})}} BTT </span>
            </p>
          </div>
          <div class="future-earnings">
            <div class="future-potential-earnings">
              <p class="secondary-title">{{$t('Future potential earnings')}}</p>
              <p class="secondary-content">
                {{potentialEarnings.toLocaleString('en-US', {maximumFractionDigits: 6})}} BTT
              </p>
            </div>
            <div class="next-payment-date">
              <p class="secondary-title">{{$t('Next payment date')}}</p>
              <p class="secondary-content">
                {{nextPayment}}
              </p>
            </div>
            <div class="pricing">
              <p class="secondary-title">{{$t('Current host pricing')}}</p>
              <p class="secondary-content">
                {{hostPricing.toLocaleString()}} BTT (TB/Month)
              </p>
            </div>
          </div>
        </div>

        <ViewStorageSize
        :storageSpace="storageSpace" 
        :isDisabled="false"
        :handleEdit="toggleEditStorageSize"
        :canEdit="notificationState !== 'in-progress'"
        v-if="!isSyncing"
        />
      </div>
      <div class="syncing-bg" v-if="isSyncing">
        <div class="syncing-container">
          <p class="syncing-header">{{$t('Validating...')}}</p>
          <p class="syncing-description">{{$t('Currently syncing the BTFS network, which may last several hours depending on network conditions.')}}
          </p>
          <img src="../assets/svg/spinner.svg" class="spinner-icon"/>
        </div>
      </div>
    <EditStorageSize
      :show = "showEditStorageSize"
      :storageSpace = "storageSpace"
      @close = "toggleEditStorageSize"
      :hostDirectoryPath = "hostDirectoryPath"
      :chooseOtherFolder = "chooseOtherFolder"
      :checkOtherFolder = "checkOtherFolder"
      :getFolders = "getFolders"
      :getPartitions = "getPartitions"
      >
    </EditStorageSize>
    <Notification v-if="showNotification" :state="notificationState" :handleClose="notificationClose">
      <template #in-progress>
        {{$t('Transferring files')}} ({{storageSpace.used}} GB)..
      </template>
      <template #complete>
        {{$t('Transfer Complete')}}
      </template>

    </Notification>
  </div>
</template>

<style lang="scss" scoped>

