  visibility: hidden;
}

.dashboard-header {
  border-bottom: 1px solid $gray-300;
  padding: 16px;
  display:flex;
  flex-direction: row;
  justify-content:space-between;
  position: relative;
  width:100%;
  box-sizing: border-box;

  .online-status {
    display:inline-flex;
    align-items:center;

    &:before {
      content: '';
      width: 8px;
      height: 8px;
      border-radius: 50%;
      display: block;
      background: #27AE60;
      margin-right:5px;
    }

  }
  .offline {
    &:before{
      background:red;
    }
  }
}

.dashboard-container {
  padding:24px 24px;
  box-sizing: border-box;
  width:100%;
  //max-width: 1000px;

  .host-score-grid {
    display: flex;
    width: 100%;
    margin: 0 0 24px 0;
    flex-direction: column;

      .host-score-history-card {
        height: 312px;
        margin: 24px 0 0 0;
      }
  }
}

.earnings-container {
  display:flex;
  flex-direction:column;
  background: $gray-050;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  padding:24px 24px 0 24px;
  margin:24px 0;
  position:relative;

  .earnings {
    .title {
      font-weight: bold;
      font-size: 14px;
      line-height: 24px;
      letter-spacing: 2px;
      text-transform: uppercase;
    }
    .amount {
      font-weight: bold;
      font-size: 24px;
      line-height: 32px;
      color:$btfs-blue-500-main;
      margin:8px 0;
    }
  }

  .future-earnings {
    display:flex;
    margin:24px 0;
    .future-potential-earnings {
      width:100%;
      max-width: 355px;
    }
    .pricing {
      width:100%;
      max-width: 355px;
    }
    .next-payment-date {
      width:100%;
      max-width: 355px;
    }
  }

  .secondary-title {
    font-weight: bold;
    font-size: 14px;
    line-height: 24px;
    color: $gray-600;
  }
  .secondary-content {
    margin:8px 0 0 0;
    font-size: 18px;
    line-height: 24px;
    color: $gray-900;
  }
  .refresh-button {
    position:absolute;
    top:28px;
    right:28px;
    cursor:pointer;
    fill:$gray-700;
  }
}

.syncing-bg {
  position: absolute;
  background: rgba(5, 5, 5, 0.32);
  backdrop-filter: blur(6px);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;

  .syncing-container {
    background: $gray-050;
    box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.2), 0px 3px 4px rgba(0, 0, 0, 0.12), 0px 2px 4px rgba(0, 0, 0, 0.14);
    border-radius: 4px;
    margin: 24px;
    padding: 48px 24px 48px 24px;
    text-align: center;
    max-width: 504px;

    .syncing-header {
      font-weight: bold;
      font-size: 34px;
      line-height: 40px;
      letter-spacing: 0.25px;
      color: $btfs-blue-800;
    }

    .syncing-description{
      font-size: 18px;
      line-height: 24px;
      color: $btfs-blue-900;
      margin: 24px 0 24px 0;
    }
  }
}

.spinner-icon {
    -webkit-animation:spin 1s linear infinite;
    -moz-animation:spin 1s linear infinite;
    animation:spin 1s linear infinite;
}
@-moz-keyframes spin { 100% { -moz-transform: rotate(360deg); } }
@-webkit-keyframes spin { 100% { -webkit-transform: rotate(360deg); } }
@keyframes spin { 100% { -webkit-transform: rotate(360deg); transform:rotate(360deg); } }

@media (min-width: 1152px) {
  .dashboard-header {
    padding:16px 140px;
  }
  .dashboard-container {
    padding:24px 116px;
    //box-sizing: content-box;
  }
}

@media (min-width: 1300px) {

.dashboard-container {

  .host-score-grid {
    display: grid;
    height: 312px;
    grid-template-columns: 490px 32px auto;
    margin: 0 0 24px 0;

      .host-score-history-card {
        grid-column-start: 3;
        margin: 0
      }
  }
}
}
</style>
