<script>
import Button from '../components/Button.vue'
import WalletBalanceTile from '../components/WalletBalanceTile';
import ExchangeTable from '../components/ExchangeTable'
import RecoveryInformation from '../components/RecoveryInformation'

export default {
  name: 'WalletDashboard',
  props: {
    id: {
      type: String,
      required: true
    },
    tronAddress: {
      type: String,
      required: true
    },
    handleRefresh: {
      type: Function,
      default: () => {}
    },
    transactions: {
      type: Array,
      default: () => {
        return [
          {
          id: 1,
          created: '1579547824',
          amount: 200,
          type: 'Deposit',
          status: 'complete',
          }
        ]
      }
    },
    handleDeposit: {
      type: Function,
      default: () => {}
    },
    handleWithdraw: {
      type: Function,
      default: () => {}
    },
    inAppBalance: {
      type: Number,
      required: true,
      default: 0
    },
    cryptoBalance: {
      type: Number,
      required: true,
      default: 0
    },
    checkPassword: {
      type: Function,
      default: () => {}
    },
  },
  components: {
    Button,
    WalletBalanceTile,
    'bt-exchange-table': ExchangeTable,
    RecoveryInformation,
  },
  methods: {
    paginationLeft() {
      let newIndex = this.pageIndex - 1
      if(newIndex >= 0){
        this.pageIndex = newIndex
      }
    },
    paginationRight() {
      let newIndex = this.pageIndex + 1
      if(newIndex < this.transactionsLength / this.pageSize){
        this.pageIndex = newIndex
      }
    },
    onRefresh() {
      this.isRefreshing = true
      let isRefreshComplete = false

      let interval = setInterval(() => {
        if(isRefreshComplete) {
          clearInterval(interval)
          this.isRefreshing = false
        }
      }, 750)

      this.handleRefresh()
      .then(() => {
        isRefreshComplete = true
      })
      .catch(() => {
          clearInterval(interval)
          this.isRefreshing = false
      })
    },
    toggleRecoveryInformation() {
      this.showRecoveryInformation = !this.showRecoveryInformation
    },
    handleSignIn() {
      this.checkPassword({password: this.password})
      .then(() => {
        this.handleRefresh()
        this.$store.dispatch('walletAuth', this.password)
      })
      .catch(() => {
        this.passwordInvalid = true
      })
    }
  },
  data() {
    return {
      pageIndex: 0,
      pageSize: 10,
      showRecoveryInformation: false,
      password: null,
      passwordInvalid: false,
      isRefreshing: false
    }
  },
  computed: {
    transactionsLength() {
      return this.transactions.length
    },
    pageItemStart() {
      return this.pageIndex * this.pageSize
    }, 
    pageItemEnd() {
      let end = this.pageItemStart + (this.pageSize)
      return (end > this.transactionsLength) ? this.transactionsLength : end
    },
    visibleTransactions() {
      return this.transactions.slice(this.pageItemStart, this.pageItemEnd)
    },
    isAuth() {
      return this.$store.state.wallet.auth !== null
    },
    passwordMessage() {
      return (this.passwordInvalid) ? this.$t('Incorrect password') : ""
    },

  },
  watch: {
    password() {
      this.passwordInvalid = false
    }
  },
  mounted() {
    this.handleRefresh()
  }
}
</script>

<template>
  <div class="wallet-dashboard">
    <div v-if="!isAuth" class="wallet-sign-in">
      <div class="header-container">
      <p class="header">{{$t('Sign in to your BitTorrent Wallet')}}</p>
      <p class="description">
        {{$t('You are about to input some sensitive information, please DO NOT expose the password to strangers.')}}
      </p>
      <p class="description">
        {{$t('We don’t have access to your password, and WILL NOT be able to restore it.')}}
      </p>
      <bt-input-box
        :label="this.$t('Password')"
        inputType="password" 
        @input="(val) => password = val" 
        :isInvalid="passwordInvalid"
        :message="passwordMessage"
        :placeholder="this.$t('Enter password')"
        :value="password"
      />
      <div class="button-container">
        <Button :cb="handleSignIn" :isDisabled="password === null || passwordInvalid">{{$t('Continue')}}</Button>
      </div>
      <div class="line"/>
      <p class="sub-description">
        <i18n path="Don't remember you password? You can import your wallet using your recovery phrase and private key or create a new one here" tag="span">
          <router-link to="/wallet/import" class="link">{{$t('import')}}</router-link>
          <router-link to="/wallet/setup" class="link">{{$t('here')}}</router-link>
        </i18n>
      </p>
    </div>
    </div>
    <div v-if="isAuth" >
        <div class="wallet-dashboard-header">
          <div class="recovery-info">
              <Button :cb="toggleRecoveryInformation" type="text">{{$t('Recovery information')}}</Button>
          </div>
        </div>
        <div class="wallet-dashboard-container">
          <WalletBalanceTile 
            :title="$t('BTFS In-app Wallet Balance')" 
            :balance="inAppBalance"
            :defaultTargetAddress="'cryptoWalletAddress'"
            :dropdownOptions='[this.$t("BTT Crypto Wallet"), this.$t("Other public address")]'
            :tooltipText='this.$t(`This is the balance of your BTT in BTFS network, if you would like to deposit or withdraw using a crypto wallet or exchange, please use below "BTT Crypto Wallet" instead.`)'
            :handleWalletTransfer="handleWithdraw"
            :minTransferAmount="1000"
            :publicAddress = "id"
            :publicAddressLabel = "this.$t('Peer ID')"
          >
            <span>{{$t('Transferring to another public address is currently under development.')}}</span>
          </WalletBalanceTile>
          <WalletBalanceTile 
            :title="this.$t('BTT Crypto Wallet Balance')" 
            :balance="cryptoBalance"
            :defaultTargetAddress="'inAppWalletAddress'"
            :dropdownOptions='[this.$t("BTFS In-App Wallet"), this.$t("Other public address")]'
            :tooltipText="this.$t('BTT crypto wallet balance shows your BTT securely saved in an online wallet. You can exchange them with other tokens; transfer them to your In-app balance for use with BTFS; or send them to others')"
            :handleWalletTransfer="handleDeposit"
            :minTransferAmount="10"
            :publicAddress = "tronAddress"
            :publicAddressLabel = "this.$t('TRON Address')"
          >
          </WalletBalanceTile>
          <div class="transaction-tile">
            <div class="title">
              {{$t('transactions')}}
            </div>
            <svg-icon
                :hasFill="true" 
                icon="refresh" 
                class="refresh-icon" 
                :class="{'rotate':isRefreshing}"
                @click="onRefresh" 
                
            />
            <bt-exchange-table :transactions="visibleTransactions"/>
            <div class="pagination-container">
              {{transactionsLength === 0 ? 0 : pageItemStart+1}}-{{pageItemEnd}} of {{transactionsLength}}
              <svg-icon 
                :hasFill="true" 
                icon="chevron-left" 
                class="arrow-icon left"
                @click="paginationLeft" 
              />
              <svg-icon 
                :hasFill="true" 
                icon="chevron-right" 
                class="arrow-icon"
                @click="paginationRight" 
              />
            </div>
          </div>
        </div>

        <RecoveryInformation 
        v-if="showRecoveryInformation" 
        :close="toggleRecoveryInformation"
        :checkPassword="checkPassword"
        />
    </div>
  </div>
</template>

