<script>
import InputBox from './InputBox.vue';
import colors from '../scss/_theme.scss';
import zxcvbn from 'zxcvbn';
import SvgIcon  from  './svg-icon.vue';

export default {
    name: 'PasswordCreate',
    props: {
    },
    components: {
        'svg-icon': SvgIcon,
        'bt-input-box': InputBox
    },
    data() {
        return {
            togglePasswordCheck: false,
            password: '',
            confirmPassword: '',
            colors,
            tooltip: `<div>${this.$t('We recommend including:')} <br/><br/>${this.$t('- One lower case letter')}<br/>${this.$t('- One upper case letter')}<br/>${this.$t('- One special character')}<br/>${this.$t('- One number')}<br/>${this.$t('- No spaces')}</div>`
        }
    },
    methods: {
    },
    computed: {
        passwordState() {
            switch(this.passwordStrength){
                case 1:
                    return {
                        message:this.$t('Very weak password'),
                        invalidColor: colors.btfsRed
                    }
                case 2:
                    return {
                        message:this.$t('Weak password'),
                        invalidColor: colors.btfsOrange
                    }
                case 3:
                    return {
                        message:this.$t('Strong password'),
                        invalidColor: colors.btfsRed,
                    }
                default:
                    return ''
            }
        },
        confirmPasswordState() {
            if( !this.passwordStrong || this.confirmPassword === ''){
                return {
                    message:"",
                    borderColor:colors.gray500
                }
            }
            return this.passwordsMatch ? 
                {
                    message:this.$t('Passwords match'),
                }
                : 
                {
                    message:this.$t("Passwords don't match"),
                    borderColor:colors.btfsRed
                }
        },
        passwordStrong() {
            return (this.passwordStrength) ? this.passwordStrength > 2 : true
        },
        passwordStrength() {
            // If any user input, set range of password strengh from 1-3 (since we only have 3 bars)
            return this.password ? Math.max(Math.min(zxcvbn(this.password).score, 3), 1) : null;
        },
        passwordsMatch() {
            this.$emit('passwordsMatch', this.password === this.confirmPassword && this.passwordStrong && this.password !== "")
            this.$emit('password', this.password)
            return this.password === this.confirmPassword
        }
    },
};
</script>

<template>
<div>
    <div class="password-container">
        <bt-input-box v-model="password"
            inputType="password"
            :placeholder="this.$t('Wallet password')"
            :isInvalid="!passwordStrong"
            :showMessage="true"
            :borderColor="colors.btfsBlue500"
            :passwordPlaceholderColor="colors.gray500"
            :messageColor="colors.btfsGreen"
            :message="passwordState.message"
            :iconColor="colors.gray500"
            :invalidColor="passwordState.invalidColor"
        />
        <svg-icon icon="help" class="icon" :hasFill="false" v-tooltip.left="{ content: tooltip, classes: ['tooltip'], hideOnTargetClick: false, autoHide: false}"/>
    </div>
    <div class="password-container">
        <bt-input-box v-model="confirmPassword"
            inputType="password"
            :placeholder="this.$t('Confirm password')"
            :isInvalid="!passwordsMatch && confirmPassword !== ''"
            :showMessage="true"
            :borderColor="colors.btfsBlue500"
            :passwordPlaceholderColor="colors.gray500"
            :messageColor="colors.btfsGreen"
            :message="confirmPasswordState.message"
            :iconColor="colors.gray500"
            :invalidColor="colors.btfsRed"
        />
    </div>
</div>

</template>

