<script>
import colors from '@/scss/_theme.scss';
import SvgIcon from './svg-icon';

export default {
    name: 'bt-input-box',
    props: {
        value: [String, Number],
        inputType: {
            type: String,
            default: 'text'
        },
        handleChange: {
            type: Function,
            default() {}
        },
        placeholder: {
            type: String,
            default: ''
        },
        disabled: {
            type: Boolean,
            default: false
        },
        step: {
            type: String,
            default: '0',
        },
        message: {
            type: String,
            default: ''
        },
        isInvalid: {
            type: Boolean,
            default: false
        },
        unit: {
            type: String,
            default: ''
        },
        width: {
            type: Number,
            default: null
        },
        showMessage: {
            type: Boolean,
            default: true
        },
        borderColor: {
            type: String,
            default: colors.primary500Main
        },
        passwordPlaceholderColor: {
            type: String,
            default: colors.primary200
        },
        placeholderColor: {
            type: String,
            default: colors.gray400
        },
        disabledColor: {
            type: String,
            default: colors.gray400
        },
        invalidColor: {
            type: String,
            default: colors.accentsRed500
        },
        textareaColor: {
            type: String,
            default: colors.gray800main
        },
        messageColor: {
            type: String,
            default: colors.gray500
        },
        iconColor: {
            type: String,
            default: null
        },
        delayEmit: {
            type: Boolean,
            default: false
        }
    },
    components: {
        'svg-icon': SvgIcon,
    },
    data() {
        return {
            inputVal: this.value ? this.inputType === 'number' ? Number(this.value) : this.value : null,
            showPassword: false,
            intervalHandler: null
        };
    },
    computed: {
        isPassword() {
            return this.inputType === 'password';
        },
        invalid() {
            return false;
        },
        inputStyle() {
            return {
                '--borderColor': this.borderColor,
                '--passwordPlaceholderColor':this.passwordPlaceholderColor,
                '--placeholderColor':this.placeholderColor,
                '--disabledColor':this.disabledColor,
                '--invalidColor':this.invalidColor,
                '--textareaColor':this.textareaColor,
                '--messageColor':this.messageColor,
                '--iconColor':this.iconColor,
            }
        },
        isInactive() {
            return this.inputVal === null || this.inputVal === ""
        }
    },
    methods: {
        handleShowPasswordChange() {
            this.showPassword = !this.showPassword;
        },
        startInterval(val) {
            this.intervalHandler = setInterval(() => {
                this.$emit('input', val)
                clearInterval(this.intervalHandler)
            }, 500)
        },
        noNegativeKeystroke(e) {
            if(this.inputType === "number") {
                var charCode = (e.which) ? e.which : e.keyCode;
                if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
                    e.preventDefault();
                }
                else {
                    return true;
                }
            }
        }
    },
    watch: {
        inputVal(val) {
            if(this.inputType === "number" && val < 0 ) {
                val = val * -1
            }
            if(this.delayEmit){

                if(this.intervalHandler === null) {
                    this.startInterval(val)
                }
                else {
                    clearInterval(this.intervalHandler)
                    this.startInterval(val)
                }

            }
            else{
                this.$emit('input', val); // Usage: <wallet-input-box v-model="value" /> to get two-way binding to work
            }
        },
        value(val) {
            this.inputVal = val;
        },
    },
    mounted() {
        if (this.width) {
            this.$refs.inputboxContainer.style.width = this.width + 'px';
        }
    },
};
</script>

<template>
    <div ref="inputboxContainer" class="input-box-container" :class="[inputType, { 'invalid': isInvalid }, {'inactive': isInactive}]" :style="inputStyle">
        <div class="input-box">
            <textarea v-if="inputType === 'textarea'"
                v-model="inputVal"
                @change="handleChange"
                @keydown.stop
                :placeholder="placeholder"
                :disabled="disabled"
            ></textarea>
            <input v-else-if="inputType === 'password'"
                :type="showPassword ? 'text' : 'password'"
                v-model.trim="inputVal"
                @change="handleChange"
                :placeholder="placeholder"
                :class="inputType"
                :disabled="disabled"
            >
            <input v-else
                :type="inputType"
                v-model="inputVal"
                @change="handleChange"
                @keydown.stop
                :placeholder="placeholder"
                :step="step"
                :disabled="disabled"
                @keypress="noNegativeKeystroke($event)"
                min="0"
            >
            <div @click="handleShowPasswordChange" class="eye-wink-icon"  v-if="isPassword && !disabled">
                <svg-icon icon="eye-invisible" v-if="showPassword" key="eye-invisible" :hasFill="iconColor !== null"/>
                <svg-icon icon="eye-visible" key="eye-visible" v-else :hasFill="iconColor !== null"/>
            </div>
            <div v-if="unit" class="unit">{{unit}}</div>
        </div>
        <div class="message" v-if="showMessage">{{ message }}</div>
    </div>
</template>

<style lang="scss" scoped>
@import '~@/scss/_theme.scss';

.input-box-container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;

    .input-box {
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        width: 100%;

        input,textarea {
            outline: none;
            border: 0px;
            width: 100%;
            border-radius: 6px;
            border: 1px solid var(--disabledColor);
            padding: 16px;
            font-size: 16px;
            line-height: 24px;
            letter-spacing: 0.15px;
            text-overflow: ellipsis;
            box-sizing: border-box; 
            height: 40px;
            background: $gray-050;

            &.password {
                padding: 10px 16px;
                padding-right: 60px;
                text-overflow: clip;
                border-radius: 6px;
                border: 1px solid #707577;
                height: 40px;

                &::placeholder {
                    color: var(--passwordPlaceholderColor);
                    font-weight: lighter;
                }
            }

            &:focus {
                border: 2px solid var(--borderColor);
            }

            &::placeholder {
                color: var(--placeholderColor);
            }

            &:disabled {
                border-color: var(--disabledColor);
            }

            &.invalid {
                border-color: var(--invalidColor);
            }


        }

        textarea {
            min-width: 300px;
            height: 90px;
            resize: none;
            padding: 10px 15px;
            color: var(--textareaColor);
            font-weight: 300;
            font-size: 14px;
            border-radius: 4px;
            font-family: lato;

        }

        .eye-wink-icon {
            font-size: 24px;
            position: absolute;
            right: 1.75em;
            cursor: pointer;
            display: flex;
            fill: var(--iconColor);
        }
        .unit {
            margin-left: 10px;
            font-size: 14px;
        }
    }

    .message {
        font-size: 13px;
        margin: 4px 10px 0px 14px;
        line-height: 16px;
        color: var(--messageColor);
        font-weight: bold;
        height: 16px;
    }

    &.textarea{
        .message {
            margin: 4px 10px 0px 0px;
        }
    }

    &.text {
        .input-box {
            input {
                border-radius: 6px;
                padding: 10px 16px;
                height: 40px;
            }
        }
        .message {
            margin: 4px 10px 0px 0px;
        }
    }

    &.password {
    }

    &.inactive {
        input, textarea {
            border: 1px solid var(--disabledColor);
            color: var(--disabledColor);
        }
    }

    &.invalid {
        input, textarea {
            border: 2px solid var(--invalidColor);

            &:focus {
                border: 2px solid var(--invalidColor);
            }
        }
        .message {
            color: var(--invalidColor);
        }
    }

}

</style>