import Vue from 'vue';

const state = {
    host: '',
    settings: ''
};
const mutations = {
    setHost(s, options) {
        Vue.set(s, options.page, options.state);
    }
};
const getters = {
    notification(s) {
        return s;
    }
};
const actions = {
    notificationInProgress(context, page) {
        context.commit('setHost', {page:page, state:'in-progress'});
    },
    notificationComplete(context, page) {
        context.commit('setHost', {page:page, state:'complete'});
    },
    notificationClose(context, page) {
        context.commit('setHost', {page:page, state:''});
    }

};

export default {
    state,
    getters,
    mutations,
    actions,
};