<script>
import Vue from 'vue';
import _ from 'lodash';

export default {
    name: 'RecoveryPhrase',
    props: {
        choices: {
            type: Array,
            default() {
                return ['test1', 'test2', 'test3', 'test4', 'test5', 'test6', 'test7', 'test8'];
            }
        },
        show: {
            type: Boolean,
            default: true
        }
    },
    components: {
    },
    data() {
        return {
            shuffledChoices: [],
            shuffled: false,
            answers: new Array(this.choices.length).fill(undefined),
            answerToShuffleIndexMapper: {},
        };
    },
    methods: {
        addAnswer(value, index) {
            this.answerToShuffleIndexMapper[this.indexNextAnswer] = index;
            Vue.set(this.answers, this.indexNextAnswer, value);
        },
        removeAnswer(index) {
            delete this.answerToShuffleIndexMapper[index];
            Vue.set(this.answers, index, undefined);
        },
        isAnswerEmpty(index) {
            return !this.answers[index];
        },
        isChoiceDisabled(index) {
            return _.includes(this.answerToShuffleIndexMapper, index);
        },
        reset() {
            this.answers = new Array(this.choices.length).fill(undefined);
            this.$emit('reset');
        }
    },
    computed: {
        isError() {
            return this.validate && !this.isCorrect;
        },
        isCorrect() {
            return _.isEqual(this.choices, this.answers);
        },
        indexNextAnswer() {
            return _.findIndex(this.answers, (answer) => answer === undefined);
        },
        validate() {
            return this.answers[this.answers.length-1] !== undefined
        }
    },
    watch: {
        validate(value) {
            let emitValue = value ? this.isCorrect : null
            this.$emit('recoveryPhraseMatches', emitValue);
        },
    },
    created() {
        // Need to keep track of original idx to use for :key values in transistion group.
        // Needed in case recovery phrase contains duplicate words.
        this.shuffledChoices = _.reduce(this.choices, (acc, choice, originalIndex) => {
            acc.push({
                originalIndex,
                choice,
            });
            return acc;
        }, []);
    },
    mounted() {
        setTimeout(() => {
            this.shuffledChoices = _.shuffle(this.shuffledChoices);
            this.shuffled = true;
        }, 2000);
    }
};
</script>

<template>
    <div class="recovery-phrase-container">
        <div class="recovery-phrase-answer-container"
            :class="{ 'error': isError }">
            <div v-for="({ choice, originalIndex }, index) in shuffledChoices"
                class="recovery-phrase-answer"
                :class="{ 'empty': isAnswerEmpty(index), 'next': indexNextAnswer === index}"
                :key="originalIndex"
                @click="removeAnswer(index)">{{answers[index]}}</div>
        </div>
        <transition-group class="recovery-phrase-choice-container" tag="div" name="choice-container">
            <div v-for="({ choice, originalIndex }, index) in shuffledChoices"
                class="recovery-phrase-choice"
                :class="{ 'disabled': isChoiceDisabled(index), 'shuffled': shuffled }"
                :key="originalIndex"
                @click="addAnswer(choice, index)">{{choice}}</div>
        </transition-group >
    </div>
</template>

