<script>
import Popup from './Popup'
import InputBox from './InputBox'
import Button from './Button'
import colors from '../scss/_theme.scss';
import QRCode from 'qrcode'

export default {
	name: 'RecoveryInformation',
	components: {
		Popup,
		InputBox,
		Button
	},
	props: {
		close: {
			type: Function,
			deafult: () => {}
		},
		checkPassword: {
			type: Function,
			default: () => {}
		},
	},
    data() {
        return {
			state:0,
			colors,
			password: null,
			passwordInvalid: false,
			mnemonic: "",
			privateKey: "",
			hasAgreed: false,
			qrCode: ''
        }
    },
    computed: {
		passwordMessage() {
			return (this.passwordInvalid) ? "The password is incorrect" : ""
        },
        showRecoveryPhrase() {
			return Array.isArray(this.mnemonic) && this.mnemonic[0] !== ''
        }
    },
	methods: {
		nextStep() {
			this.state = this.state += 1
			if(this.state >= 3){
				this.close()
				this.state = 0
			}
		},
		handleCheckPassword() {
			this.checkPassword({password:this.password})
			.then((res) => {
				this.mnemonic = res.mnemonic.split(' ')
				this.privateKey = res.privateKey
				this.nextStep()
			})
			.catch(() => {
				this.passwordInvalid = true
			})
		},
		generateQrCode() {
			QRCode.toCanvas(this.mnemonic.join(' '), {width: 88, margin: 0})
			.then((res) => {
				this.$refs.qrCode.appendChild(res)
			})
			.catch((err) => {
				console.log(err)
			})
        }
	},
	watch: {
		password() {
			if(this.passwordInvalid){
				this.passwordInvalid = false
			}
		},
		state() {
			if(this.state === 2 && this.showRecoveryPhrase){
				this.generateQrCode()
			}
		}
	}
};
</script>
<template>
	<Popup :closeable="true" @onClose="close" :blur="false" class="recovery-popup" :isOpen="true">
		<div v-if="this.state === 0">
			<div class="popup-header">{{$t('Recovery')}}</div>
			<div class="popup-description">{{$t('Enter your password and continue to the next page to be able to see the recovery information.')}}</div>
			<div class="password-container">
				<InputBox 
				v-model="password"
				inputType="password"
				:placeholder="this.$t('Wallet password')"
				:showMessage="true"
				:borderColor="colors.btfsBlue500"
				:passwordPlaceholderColor="colors.gray500"
				:iconColor="colors.gray500"
				:isInvalid="passwordInvalid"
				:message="passwordMessage"
				/>
			</div>
			<div class="button-container" >
				<Button :cb="handleCheckPassword" :isDisabled="passwordInvalid">{{$t('Continue')}}</Button>
			</div>
		</div>
		<div v-if="this.state === 1">
			<div class="popup-header">{{$t('Important message')}}</div>
			<div class="popup-description popup-description-1">{{$t('The recovery information is unique and only you should have access. We encourage you to write these words down and keep them in a safe place. Avoid saving it in a computer or online service, and do not take a screenshot of it. If you lose it or share it with others, you may lose your assets.')}}</div>
			<div class="text-sm">
				<i18n path="Looking for more information? Read our FAQs" tag="span">
					<a href="https://www.bittorrent.com/token/bittorrent-file-system/faq/" target="_blank" rel="noreferrer noopener">{{ $t('FAQs') }}</a>
				</i18n>
			</div>
			<div class="text-sm tos-container">
				<bt-checkbox @change="(val) => {hasAgreed = val}"> 
					<template v-slot:right>
						{{$t('I agree to the terms and conditions of BTFS.')}}
					</template>
				</bt-checkbox>
			</div>
			<div class="button-container">
				<Button :cb="nextStep" :isDisabled="!hasAgreed">{{$t('Continue')}}</Button>
			</div>
		</div>
		<div v-if="this.state === 2">
			<div class="subtitle">
				{{$t('recovery phrase')}}
			</div>
			<div class="recovery-phrase-container">
				<div 
				v-if="!showRecoveryPhrase"
				class="light-container"
				>
					<p>{{$t('Not available')}}</p>
					<br/>
					<a href="https://docs.btfs.io/docs/faq" target="_blank">{{$t('Click here to learn more')}}</a>
				</div>
				<div
				v-if="showRecoveryPhrase"
				>
					<div class="recovery-right-container">
						<div class="recovery-answer-key-container" >
							<div v-for="(phrase, index) in mnemonic"
								class="recovery-answer-key"
								:key="index">{{phrase}}
							</div>
						</div>
					</div>
					<div class="button-container center copy-recovery-phrase">
						<Button type="copy" :copyValue="mnemonic.join(' ')">{{$t('Copy recovery phrase to clipboard')}}</Button>
					</div>
					<div class="line">
						<div class="or">OR</div>
					</div>
					<div class="qr-container">
						<div class="qr-code" ref="qrCode" ></div>
						<div class="qr-text-1">{{$t('Scan with Tronlink')}}</div>
						<div class="qr-text-2">{{$t('Check your surroundings and make sure no one is around!')}}</div>
					</div>
					<div class="line"/>
				</div>
			</div>
			<div class="subtitle">
				{{$t('private key')}}
			</div>
			<div class="private-key-container">
				<div class="light-container">
					{{privateKey}}
				</div>
				<div class="button-container center copy-private-key">
					<Button type="copy" :copyValue="privateKey">{{$t('Copy private key to clipboard')}}</Button>
				</div>
			</div>
		</div>
	</Popup>
</template>

