<script>

export default {
    name: 'modal',
    props: {
        show: {
            type: Boolean,
            default: false
        },
        showCloseButton: {
            type: Boolean,
            default: true
        },
        containerClass: {
            type: String,
            default: '',
        },
        // Modal will be fixed 100px from top instead of centered in the middle
        // Better for larger modals that may need to scroll
        fixed: {
            type: Boolean,
            default: false
        },
        fixedPadding: {
            type: Number,
            default: 0
        },
        escapeable: {
            type: Boolean,
            default: true
        }
    },
    components: {
    },
    methods: {
        close() {
            this.$emit('close');
        },
        updateActiveModalIndex(value) {
            this.$emit('updateActiveModalIndex', value);
        },
        escapeHandler() {
            if (this.escapeable) {
                this.close();
            }
        },
    },
    beforeDestroy() {
        // Removing class when watch() doesn't get called in IE/Edge
        if (document.body.classList.contains('no-scroll')) {
            document.body.classList.remove('no-scroll');
        }
    },
    computed: {
        fixedPaddingStyle() {
            return {
                'padding-top': `${this.fixedPadding}px`,
                'padding-bottom': `${this.fixedPadding}px`,
            };
        },
        escapableVisible() {
            return this.show;
        }
    },
    watch: {
        show(value) {
            if (value) {
                document.body.classList.add('no-scroll');
            } else {
                document.body.classList.remove('no-scroll');
            }
        }
    },
    created() {
        const escapeHandler = (e) => {
            if (e.key === 'Escape' && this.escapableVisible) {
                this.escapeHandler();
            }
        }

        document.addEventListener('keydown', escapeHandler);

        this.$once('hook:destroyed', () => {
            document.removeEventListener('keydown', escapeHandler);
        });
    }
};
</script>

<template>
    <transition name="modal">
        <div v-if="show" class="modal-mask" :class="{ fixed: fixed, centered: !fixed }" @click.stop="close">
            <div :style="fixedPaddingStyle">
                <div class="modal-container" :class="containerClass" @click.stop>
                    <slot></slot>
                    <div v-if="showCloseButton" id="auto-modal-close-btn" class="modal-close-btn button-icon--dark" @click="close">
                        <svg-icon icon="close" :hasFill="true" />
                    </div>
                </div>
            </div>
        </div>
    </transition>
</template>

