<script>
export default {
    name: 'Checkbox',
    props: {
        value: {
            type: Boolean,
            default: false
        },
        handleChange: {
            type: Function,
            default: function() {
                return;
            }
        },
        id: {
            type: String,
            default: null
        },
        isDisabled: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            inputVal: this.value
        };
    },
    methods: {
        change(value, event) {
            this.$emit('change', value, event);
        },
        click(event) {
            // Click does not provide the correct value since it is triggered before the
            // value change. Click can be used when you need to get a key modifier on the
            // click event (i.e shift-click)
            this.$emit('click', event);
        }
    },
    computed: {
        hasRightSlot() {
            return !!this.$slots['right'];
        },
        hasLeftSlot() {
            return !!this.$slots['left'];
        },
    },
    watch: {
        value(newVal) {
            this.inputVal = newVal;
        },
        inputVal(newVal) {
            this.handleChange(newVal);
        }
    }
};
</script>

<template>
    <div class="checkbox-container">
        <div class="left-section" v-if="hasLeftSlot" :class="{'gray-text': isDisabled}">
            <slot name="left" :class="{gray: isDisabled}"></slot>
        </div>
        <label class="checkbox-control">
            <input :id="id"
                type="checkbox"
                v-model="inputVal"
                @click.stop="click($event)"
                @change.stop="change(inputVal, $event)"
                :disabled="isDisabled">
            <div class="checkbox-indicator" :class="{isEnabled: !isDisabled}"></div>
        </label>
        <div class="right-section" v-if="hasRightSlot" :class="{'gray-text': isDisabled}">
            <slot name="right"></slot>
        </div>
    </div>
</template>

<style lang="scss" scoped>
@import '~@bittorrent/bt-assets/scss/colors';
@import '~@bittorrent/bt-assets/scss/variables';



.checkbox-container {
    align-items: center;
    transition: 2s;
    color: $gray-800;

    .checkbox-control {
        display: flex;
        position: relative;
        input[type='checkbox'] {
            position: absolute;
            box-sizing: border-box;
            z-index: -1;
            opacity: 0;
            margin: 0;
            padding: 0;
        }
        input[type='checkbox']:checked {
            ~ .checkbox-indicator {
                background-color: $btfs-blue-500-main;
                border: 2px solid $btfs-blue-500-main;
            }
            ~ .checkbox-indicator:after {
                display: block;
                box-sizing: border-box;
            }
        }
        .checkbox-indicator {
            height: 18px;
            width: 18px;
            background-color: transparent;
            border: 2px solid $gray-300;
            border-radius: 2px;
            box-sizing: border-box;
            &:after {
                content: '';
                position: absolute;
                display: none;
                left: 6px;
                top: 2px;
                width: 7px;
                height: 11px;
                border: solid $white;
                border-width: 0 2px 2px 0;
                transform: rotate(45deg);
            }
        }
        .isEnabled{
            cursor: pointer;
            border: 2px solid $btfs-blue-500-main;
        }
    }
    .right-section {
        margin-left: 5px;
    }
    .left-section {
        margin-left: 5px;
    }
    .gray-text {
        color:$gray-500;
    }
}
</style>