<script>
import ChartLine from '../components/ChartLine'
export default {
	name: 'HostScoreHistory',
	components: {
		ChartLine
	},
	props: {
		getHostScoreHistory: {
			type: Function,
			default: () => {}
		},
		hostScoreHistory: {
			type: Array,
			default: () => []
		},
	},
	data() {
		return {
			dateRange: [
				{
					name: '1D',
					numDays: 1
				},
				{
					name: '7D',
					numDays: 7
				},
				{
					name: '1M',
					numDays: 30
				}
			],
			selectedDateRange: 'first',
			dateRangeOptions : {
				first: {
					nativeName: this.$t('Today'),
					value: 1,
				},
				second: {
					nativeName: this.$t('Last 30 days'),
					value: 30,
				},
				third: {
					nativeName: this.$t('Last 60 days'),
					value: 60,
				},
			}
		}
	},
	methods:{

		handleHostScoreHistory(numDays) {
			this.selectedDateRange = Object.keys(this.dateRangeOptions).find(key => this.dateRangeOptions[key].value === numDays)

			let now = Date.now()
			let d = new Date();
			d.setDate(d.getDate() - numDays);
			let daysOffset = d.getTime()

			this.getHostScoreHistory({from: daysOffset, to: now, numDays:numDays})
		}

	},
	computed: {
		chartStyle() {
			return {
				height: `200px`,
				position: 'relative'
			}
		},
		chartData() {
			return this.hostScoreHistory.map((snapshot) => {
				let tooltipLines = []
				tooltipLines.push(`Uptime: ${snapshot.stat.uptime_score}/10`)
				tooltipLines.push(`Host age: ${snapshot.stat.age_score}/10`)
				tooltipLines.push(`Upload Speed: ${snapshot.stat.upload_speed_score}/10`)
				tooltipLines.push(`Download Speed: ${snapshot.stat.download_speed_score}/10`)
				tooltipLines.push(`Client Version: ${snapshot.stat.version_score}/10`)
				tooltipLines.push(`Host Score: ${snapshot.stat.score}/10`)
				return {
					x: snapshot.timestamp,
					y: snapshot.stat.score,
					tooltipLines: tooltipLines
				}
			})
		}
	},
	created() {
		this.handleHostScoreHistory(1)
	}
}


</script>

<template>
	<bt-card class="host-score-history">
		<div>
			<div class="label">{{$t('Host Score History')}}</div>
			<!-- <div class="date-range" v-for="(date, index) in dateRange" :key="date.name">
				<span
					class="date-range-selection"
					:class="{selected: index === selectedDateRangeIndex}"
					@click="() => {handleDateRangeClick(index)}"
				>
					{{date.name}}
				</span>
			</div> -->
			<div class="date-range">
				<bt-select :options = "dateRangeOptions" :valueProp="selectedDateRange" @option-updated="handleHostScoreHistory">
					<template v-slot:option-selected="optionSelected">
						<div class="date-range-selection">
							<svg-icon icon="calendar" class="icon"/>
							{{optionSelected.nativeName}}
						</div>
					</template>
				</bt-select>
			</div>
		</div>
		<div class="line-chart-container" ref="lineChartContainer">
			<ChartLine :styles="chartStyle" :data="chartData" :now="this.$t('Now')"/>
		</div>
	</bt-card>
</template>

