<script>
import SvgIcon from './svg-icon';

export default {
	name: 'Dropdown',
	components: {
		'svg-icon': SvgIcon
	},
	props: {
		placeholder: {
			type: String,
			required: true,
		},
		options: {
			type: Array,
			default: () => ['test', 'test2']
		},
		parentId: {
			type: String,
			required: true
		}
	},
    data() {
        return {
			selected: this.placeholder
        }
    },
	methods: {
		handleExpand() {
			this.$store.dispatch('updateDropdownSync', this.parentId)
        },
        handleOptionClick(option) {
			this.selected = option
			this.$store.dispatch('updateDropdownSync', null)
			this.$emit('selection', this.selected)
        }
	},
	computed:{
		isExpanded() {
			return (this.$store.state.wallet.dropdownSync === this.parentId ) ? true : false;
		}
	}
};
</script>

<template>
	<div class="dropdown" :class="{'active': isExpanded}">
		<div class="selected" @click="handleExpand" >
			<p>{{selected}}</p>
			<svg-icon icon="down-arrow" class="icon" :hasFill="true" :class="{'up-arrow': isExpanded}"/>
		</div>
		<div v-if="isExpanded" class="expanded-container">
			<div 
				class="option-container"
				v-for="(option, index) in options"
				:key="option"
				:class="{first: index === 0}"
				@click="handleOptionClick(option)"
			>
				{{option}}
			</div>
		</div>
	</div>
</template>

<style lang="scss" scoped>
.dropdown {
	border: 1px solid $btfs-blue-500-main;
	box-sizing: border-box;
	border-radius: 8px;
	color: $btfs-blue-800;
	width: fit-content;
	padding: 8px 16px;
	font-size: 16px;
	line-height: 24px;
	letter-spacing: 0.15px;
	position: relative;
	margin: 24px 0;
	min-width:152px;
	cursor: pointer;
	z-index: 1000;
	&.active {
		background: $btfs-blue-050;
	}

	.selected {
		display: flex;
		justify-content: space-between;
		font-weight: bold;

		.icon {
			margin: 0 0 0 14px;
			font-size: 0.5em;
			align-items: center;
			fill: $btfs-blue-800;

			&.up-arrow {
				transform: scale(-1);
			}
		}
	}

	.expanded-container {
		position: absolute;
		margin: 4px 0 0 0;
		top: 100%;
		left: 0;
		box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.2), 0px 3px 1px rgba(0, 0, 0, 0.12), 0px 2px 2px rgba(0, 0, 0, 0.14);
		border-radius: 4px;
		background-color: $gray-050;
		width: 188px;

		.option-container {
			padding: 8px 8px;
			margin: 8px 0;

			&.first {
				border-bottom: 1px solid $gray-300;
			}

			&:hover {
				background: $btfs-blue-050;
			}
		}
	}

}
</style>