import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
import HostAdd from './views/HostAdd.vue'
import HostSetup from './views/HostSetup.vue'
import HostDashboard from './views/HostDashboard.vue'
import Settings from './views/Settings.vue'
import WalletAdd from './views/WalletAdd.vue'
import WalletSetup from './views/WalletSetup.vue'
import WalletDashboard from './views/WalletDashboard.vue'
import WalletImport from './views/WalletImport.vue'
import btfsStore from './store';
import Vuex from 'vuex'
import VTooltip from 'v-tooltip'
import VueAnalytics from 'vue-analytics';
import axios from 'axios'
import VueAxios from 'vue-axios'
import Vuebar from 'vuebar';
import './scss/_tooltip.scss';
import '@bittorrent/bt-assets'
import '@bittorrent/bt-ui-modules'
import i18n from './i18n';

Vue.config.productionTip = false

Vue.use(VueRouter)
Vue.use(Vuex)
Vue.use(VTooltip)
Vue.use(Vuebar);

const routes = [
  { name:"HostAdd", path: '/host/add', component: HostAdd },
  { name:"HostSetup", path: '/host/setup', component: HostSetup },
  { name:"HostDashboard", path: '/host', component: HostDashboard },
  { name:"Settings", path: '/settings', component: Settings },
  { name:"WalletAdd", path: '/wallet/add', component: WalletAdd },
  { name:"WalletSetup", path: '/wallet/setup', component: WalletSetup },
  { name:"WalletDashboard", path: '/wallet', component: WalletDashboard },
  { name:"WalletImport", path: '/wallet/import', component: WalletImport },
]

const router = new VueRouter({
  routes // short for `routes: routes`
})
let port = (process.env.NODE_ENV === "development") ? 5001 : location.port
const store = new Vuex.Store({ strict: false });
btfsStore.install(store, { port: port });

Vue.use(VueAnalytics, {
    id: 'UA-747528-14',
    router
})

Vue.use(VueAxios, axios)

new Vue({
  router,
  store,
  i18n,
  render: h => h(App)
}).$mount('#app')


