<script>
import Button from '../components/Button.vue'
import colors from '../scss/_theme.scss';
import FolderSelectorModal from '../components/FolderSelectorModal.vue'


export default {
	name: 'EditHostFolder',
	components: {
		Button,
		FolderSelectorModal

	},
	props: {
		hostDirectoryPath: {
			type: String,
			default: null
		},
		checkOtherFolder: {
			type: Function,
			default: () => {}
		},
		getFolders: {
			type: Function,
			default: () => {}
		},
		getPartitions: {
			type: Function,
			default: () => {}
		},
	},
    data() {
        return {
			newHostDirectoryPath: this.hostDirectoryPath,
			colors,
			isOpen: false,
			storageLocationErrorMessage: '',
			isStorageLocationInvalid: false,
			currentHostDirectoryPath: this.hostDirectoryPath,
        }
    },
	methods: {
		toggleOpen() {
			this.newHostDirectoryPath = this.currentHostDirectoryPath
			this.isOpen = !this.isOpen
			this.$emit('isEditingFolder', this.isOpen)
		},
		handleUpdate(newHostDirectoryPath) {
			this.$emit('inputChange', newHostDirectoryPath)
			this.currentHostDirectoryPath = newHostDirectoryPath

			this.checkOtherFolder({path: newHostDirectoryPath})
			.then(() => {
				this.$emit('isFolderValid', true)

			})
			.catch((errMessage) => {
				if(this.currentHostDirectoryPath !== this.newHostDirectoryPath){
					this.storageLocationErrorMessage = errMessage;
					this.isStorageLocationInvalid = true
					this.$emit('isFolderValid', false)
				}
			})
			this.toggleOpen()

		},

	},
	computed: {
		storageLocationMessage() {
			if(this.isStorageLocationInvalid) {
				return this.storageLocationErrorMessage
			}
			else if (!this.isStorageLocationDifferent || this.newHostDirectoryPath === "") {
				return this.$t('Please manually input your new Host file path')
			}
			else{
				return this.storageLocationContinueMessage
			}
		},
		isStorageLocationDifferent() {
			return this.hostDirectoryPath !== this.newHostDirectoryPath
		},
	},
	watch:{
		newHostDirectoryPath() {
			this.isStorageLocationInvalid = false
		}
	}
};
</script>

<template>
	<div class="edit-host-folder">
		<div class="current">
			<div class="">
				<p class="subtitle">
					{{$t('Current host folder destination')}}
				</p>
				<b>
					{{currentHostDirectoryPath}}
				</b>
			</div>
			<div>
				<Button type="outline" :cb="toggleOpen"> 
					<div class="edit-button">
						{{$t('Edit')}} 
						<svg-icon icon="edit" :hasFill="true" class="icon"/>
					</div>
				</Button>
			</div>
		</div>
		<div v-if="isStorageLocationInvalid" class="error-message">
			{{storageLocationErrorMessage}}
		</div>
		<div class="folder-selector-container" :class="{hidden: !isOpen}">
			<FolderSelectorModal 
				v-if = "isOpen"
				:defaultFolder = "hostDirectoryPath" 
				:show = "isOpen" 
				@close = "(val) => {isOpen = false}"
				:getFolders = "getFolders"
				@selectPath = "handleUpdate"
				:getDrives = "getPartitions"
				:title = "this.$t('Select folder')"
				:folderDescription = "this.$t('Folder path')"
			/>
		</div>
	</div>
</template>

