<script>
export default {
    name: 'StepIndicator',
    props: {
        current: {
            type: Number,
            required: true
        },
        steps: {
            type: Array,
            required: true
        },
    },
    computed: {
        currentClamped() {
            // Range of step must be between 1 and max steps
            return Math.max(Math.min(this.current, this.steps.length), 1);
        },
        stepIndicatorStyle() {
            return {

            }
        }
    },
};
</script>

<template>

<div class="step-indicator-container" :style="stepIndicatorStyle">
    <ul class="step-list">
        <li v-for="(step, index) in steps"
            :key="index"
            :class="{ active: index + 1 === currentClamped, filled : index + 1 <= currentClamped }">
            <div class="step-list-inner">
                {{step}}
            </div>
        </li>
    </ul>
</div>

</template>

