<script>
import SlideBar from './SlideBar'

export default {
	name: 'SetStorageSize',
	components: {
		SlideBar,
	},
	props: {
		isDisabled: {
			type: Boolean,
			required: true,
			default:true
		},
		storageSpace: {
			type:Object,
			default:function() {
				return {
					minimum: {
						type: Number,
						required: false,
						default:0
					},
					used: {
						type:Number,
						required:true,
						default:0
					},
					cap: {
						type:Number,
						required:true,
						default:0
					},
					total: {
						type:Number,
						required:true,
						default:1000
					}
				}
			}
		}
	},
	data() {
		let data = this.initData()
		return {
			current: data,
			noSlideInput:this.storageSpace.cap,
			noSlideState:''
		}
		
	},
	methods: {
		handleSlideChange(slidePercent){
			let space = getStorageUsedAndRemaining(this.storageSpace.minimum, this.storageSpace.total, slidePercent)
			this.current.used = space.used
			this.current.available = space.available
			this.$emit('slideChange', this.current.used)
		},
		initData(){
			if(this.storageSpace.total < this.storageSpace.minimum){
				this.isDisabled = true
			}
			let initialSpaceUsedPercent = ((this.storageSpace.cap - this.storageSpace.minimum) / (this.storageSpace.total - this.storageSpace.minimum)).toFixed(3)
			initialSpaceUsedPercent = parseFloat(initialSpaceUsedPercent)
			let space = getStorageUsedAndRemaining(this.storageSpace.minimum, this.storageSpace.total, initialSpaceUsedPercent)
			if(initialSpaceUsedPercent < 0){
				initialSpaceUsedPercent = 0
			}
			else if(initialSpaceUsedPercent > 1){
				initialSpaceUsedPercent = 1
			}

			return {
				used:space.used,
				available:space.available,
				initialPercent:initialSpaceUsedPercent
			}
		}
	},
	computed:{
		earnings(){
			return gbToBTT(this.current.used)
		}
	},
	watch:{
		storageSpace : function(){
			let newData = this.initData()
			this.current = {
				...this.current,
				...newData
			}
		}
	}
};

function getStorageUsedAndRemaining(min, max, percent){
	let spaceUsed = (max - min) * percent + min
	let spaceAvailable = max - spaceUsed
	return {
		used:parseInt(spaceUsed.toFixed(0)),
		available:parseInt(spaceAvailable.toFixed(0))
	}
}

function gbToBTT(gb){
	let btt = gb * 8
	btt = parseInt(btt)
	return btt.toLocaleString()
}
</script>

<template>
	<div class="set-storage-size-container">
		<div class="storage">
			<p class="title">{{$t('Set Host Storage Size')}}</p>
			<div 
			class="storage-container">
				<div class="storage-space">
					<div class="storage-space-used" :class="{disabled:this.isDisabled}">
						{{this.current.used}} {{$t('GB used')}}
					</div>
					<div class="storage-space-available" :class="{disabled:this.isDisabled}">
						{{this.current.available}} {{$t('GB remaining')}}
					</div>
				</div>
				<SlideBar
					@slideChange="handleSlideChange" 
					:isDisabled="this.isDisabled"
					:initialPercent="this.current.initialPercent"
					/>
				<div>
					<p class="error" v-if="this.isDisabled">
						<img src="../assets/svg/error.svg"/>
						{{$t('32 GB is the minimum host storage commitment size')}}
					</p>
				</div>
			</div>
			
		</div>
		<div class="earnings">
			<p class="title">{{$t('Potential earnings per month')}}</p>
			<p class="btt">{{this.earnings}} BTT</p>
		</div>
	</div>
</template>

