
// consuming app should set the locale on load.
// can be used to hide DOM until app loads to avoid briefly showing app in English (fallback locale)
const state = {
    settings: {
        locale: '',
    },
};

const getters = {
    settings: (state) => state.settings,
    locale: (state) => state.settings.locale,
};

const mutations = {
    setSettings(state, settings) {
        state.settings = { ...state.settings, ...settings };
        localStorage.setItem('locale', state.settings.locale)
    },
};

const actions = {

    saveSettings({ commit }, settings) {
        commit('setSettings', settings);
    },
};

export default {
    state,
    getters,
    mutations,
    actions,
};