<script>
import ChartDoughnut from '../components/ChartDoughnut'

export default {
	name: 'HostScoreCard',
	components: {
		ChartDoughnut
	},
	props: {
		score: {
			type: Number,
			default: 0,
			required: true 
		},
		hostStats: {
			type:Object,
			default:function() {
				return {
					uptimeScore: {
						type:Number,
						required:true,
						default:0
					},
					ageScore: {
						type:Number,
						required:true,
						default:0
					},
						versionScore: {
						type:Number,
						required:true,
						default:0
					},
					uploadSpeedScore: {
						type:Number,
						required:true,
						default:0
					},
					downloadSpeedScore: {
						type:Number,
						required:true,
						default:0
					},
					uptimeWeight: {
						type:Number,
						required:true,
						default:0
					},
					ageWeight: {
						type:Number,
						required:true,
						default:0
					},
					versionWeight: {
						type:Number,
						required:true,
						default:0
					},
					uploadSpeedWeight: {
						type:Number,
						required:true,
						default:0
					},
					downloadSpeedWeight: {
						type:Number,
						required:true,
						default:0
					},
					lastUpdated: {
            type: String,
            required: true,
            default:''
          }
				}
			}
		},
	},
	data() {
		return {
		}
	},
	methods:{

	},
	computed: {
		scssVars() {
			return {
				'--uptimeScore': this.formattedHostStats.uptimeScore,
				'--ageScore': this.formattedHostStats.ageScore,
				'--uploadSpeedScore': this.formattedHostStats.uploadSpeedScore,
				'--downloadSpeedScore': this.formattedHostStats.downloadSpeedScore,
				'--versionScore': this.formattedHostStats.versionScore,
			}
		},
		formattedHostStats() {
			return {
				uptimeScore: this.hostStats.uptimeScore / 10,
				ageScore: this.hostStats.ageScore / 10,
				uploadSpeedScore: this.hostStats.uploadSpeedScore / 10,
				downloadSpeedScore: this.hostStats.downloadSpeedScore / 10,
				versionScore: this.hostStats.versionScore / 10,
				uptimeWeight: this.hostStats.uptimeWeight * 100,
				ageWeight: this.hostStats.ageWeight * 100,
				uploadSpeedWeight: this.hostStats.uploadSpeedWeight * 100,
				downloadSpeedWeight: this.hostStats.downloadSpeedWeight * 100,
				versionWeight: this.hostStats.versionWeight * 100,
				lastUpdated: new Date(this.hostStats.lastUpdated).toLocaleString()
			}
		}
	},
	created() {
		this.hostScoreTip = `${this.$t('This is an indicator of how reliable a host is, based on the following factors:')}<br/><br/>${this.$t('1. Uptime: Opted-in and enabled Host that is connected to the network; Please prevent interruptions from any network disconnection or client shut down, as this will reduce this value.')}<br/><br/>${this.$t('2. Host age: the historical time as a Host, manually opt-out as a Host will reduce this value.')}<br/><br/>${this.$t('3. Upload/download speed')}<br/><br/>${this.$t('4. Client version: Your client will be auto-updated to the latest node version, staying offline or disable this feature manually will reduce this value.')}`
	}
};


</script>

<template>
		<bt-card class="host-score-card" :style="scssVars">
			<div class="card-item-grid">
				<div class="card-item">
					<p class="title">{{$t('Host Score')}} <svg-icon icon="help-outline" class="help-icon" :fill="true" v-tooltip.bottom="{ content: hostScoreTip, classes: ['tooltip', 'Score'], hideOnTargetClick: false, autoHide: false}"/></p>
					<div class="doughnut-container">
						<ChartDoughnut :score="score"/>
					</div>
					<div class="last-updated">
						<p>{{$t('Last updated:')}}</p>
						<p>{{formattedHostStats.lastUpdated}}</p>
					</div>
				</div>
				<div class="card-item">
				<b>{{$t('Host score factors')}}</b>
				<div class="host-score-factor">

					<p class="label">{{$t('Uptime')}} ({{$t('% of score', {score: formattedHostStats.uptimeWeight})}}): <span :class="{red:hostStats.uptimeScore <= 2}">{{hostStats.uptimeScore}}/10</span></p>
					<div class="percent-bar uptime"/>

					<p class="label">{{$t('Host age')}} ({{$t('% of score', {score: formattedHostStats.ageWeight})}}): <span :class="{red:hostStats.ageScore <= 2}">{{hostStats.ageScore}}/10</span></p>
					<div class="percent-bar age"/>

					<p class="label">{{$t('Version')}} ({{$t('% of score', {score: formattedHostStats.versionWeight})}}): <span :class="{red:hostStats.versionScore <= 2}">{{hostStats.versionScore}}/10</span></p>
					<div class="percent-bar version"/>

					<p class="label">{{$t('Download')}} ({{$t('% of score', {score: formattedHostStats.downloadSpeedWeight})}}): <span :class="{red:hostStats.downloadSpeedScore <= 2}">{{hostStats.downloadSpeedScore}}/10</span></p>
					<div class="percent-bar down-speed"/>

					<p class="label">{{$t('Upload')}} ({{$t('% of score', {score: formattedHostStats.ageWeight})}}): <span :class="{red:hostStats.uploadSpeedScore <= 2}">{{hostStats.uploadSpeedScore}}/10</span></p>
					<div class="percent-bar up-speed"/>
				</div>
				</div>
			</div>
		</bt-card>
</template>

<style lang="scss" scoped>

.host-score-card {
	font-size: 14px;
	display: block;

	.card-item-grid {
		padding: 14px 0;
		
		.card-item {
			flex-direction: column;
			padding: 10px 24px;
			min-width: 200px;
		}
	}

	.title {
		font-size: 14px;
		font-weight: bold;
		font-stretch: normal;
		font-style: normal;
		line-height: 1.71;
		letter-spacing: 2px;
		text-transform: uppercase;
		display: inline-flex;
		align-items: center;

		.help-icon {
			margin: 0 0 0 5px;
		}
	}

	.doughnut-container {
		width: 150px;
		height: 150px;
		margin: 0 auto;
		position: relative;
		padding: 24px 0 10px 0;

	}

	.last-updated {
		font-size: 12px;
		color: $neutral-main;
		line-height: 1.5;
		margin: 18px 0 0 0;
	}

	.host-score-factor {

		margin: 4px 0 0 0;

		.label {
			font-size: 12px;
			font-weight: bold;
			margin: 16px 0 8px 0;

			.red {
				color: $accents-red-main;
			}
		}

		.percent-bar {
		width:100%;
		height: 8px;
		background: $primary-main-light;
		content: '';
		margin: 8px 0;
		position: relative;
		border-radius: 4px;
		overflow: hidden;

		&:after {
			display: block;
			height: 100%;
			background: $primary-main;
			content: '';
			border-radius: 4px;
		}

		&.uptime{
			&:after {
			width: calc(100% * var(--uptimeScore));
			}
		}
		&.age{
			&:after {
			width: calc(100% * var(--ageScore));
			}
		}
		&.up-speed{
			&:after {
			width: calc(100% * var(--uploadSpeedScore));
			}
		}
		&.down-speed{
			&:after {
			width: calc(100% * var(--downloadSpeedScore));
			}
		}
		&.version{
			&:after {
			width: calc(100% * var(--versionScore));
			}
		}
		}

	}
}



</style>