import Vue from 'vue';
import routes from './routes';
const { request } = routes;

const state = {
    value: {
        RepoPath:""
    },
    id: {
        ID:"",
        PublicKey:"",
        TronAddress: ""
    }
};
const mutations = {
    setRepoStat(s, stats) {
        Vue.set(s, 'value', stats);
    },
    setId(s, stats) {
        Vue.set(s, 'id', stats);
    },
    setVersion(s, stats) {
        Vue.set(s, 'version', stats);
    }
};
const getters = {
    repoStat(s) {
        return s.value;
    }
};
const actions = {
    metaRepoStat(context) {
        return request({
            endpoint: 'v1/repo/stat',
            method: 'POST',
            params: {
                human:true
            }
        }).then((stats) => {
            // console.log('repo/stats', JSON.parse(stats))
            context.commit('setRepoStat', JSON.parse(stats));
        });
    },
    metaId(context) {
        return request({
            endpoint: 'v1/id',
            method: 'POST',
        }).then((data) => {
            context.commit('setId', JSON.parse(data));
        });
    },
    metaVersion(context) {
        return request({
            endpoint: 'v1/version',
            method: 'POST',
        }).then((data) => {
            context.commit('setVersion', JSON.parse(data));
        });
    }
};

export default {
    state,
    getters,
    mutations,
    actions,
};