const mutations = {
    setToken(s, token) {
        Vue.set(s, 'token', token);
    }
}
function buildUrl({ protocol, domain, port, base, paramsList, endpoint, params }) {
    let queryParams = state.token ? `?t=${state.token}` : '?';
    if (paramsList) {

        paramsList.forEach((key) => {
            if (queryParams === '?') {
                queryParams += `${key}=${params[key]}`;
                return;
            }
            if(params[key] === null){
                return
            }
            queryParams += `&${key}=${params[key]}`;
        });
    }
    return `${protocol}://${domain}:${port}/${base}/${endpoint}${queryParams}`;
}
function request({ endpoint, type = '', method ='', body, params = {} }) {
    const usedType = (type || method).toLowerCase();
    let err;
    // if (usedType === 'post' && !state.token) {
    //     err = new Error('token value required for any non-GET request');
    //     err.name = `request: endpoint ${endpoint}:`;
    //     throw err;
    // }
    /* validate endpoint & request type */
    const info = state.endpoints[endpoint];
    if (!info || !info[usedType]) {
        err = new Error(`Api method <${usedType}> doesn't exist for endpoint <${endpoint}>`);
        err.name = 'request';
        throw err;
    }
    let paramsList;
    /* validate query params */
    if (info[usedType].params) {
        paramsList = Object.keys(info[usedType].params);
        let newParamsList = []
        paramsList.forEach((key, i) => {

            // programatically exclude query parameter if value is null
            if(params[key] !== null){
                newParamsList.push(key)
            }

            /* insert default param where applicable,
            for an example see endpoint "exchange/transactions" */
            if (!params[key] && info[usedType].defaultParams[key]) {
                params[key] = info[usedType].defaultParams[key];
            }

            if (typeof params[key] !== typeof info[usedType].params[key]() && newParamsList.includes(key)) {
                err = new Error(`Invalid param type <${typeof params[key]}> for @${key} <${typeof info[usedType].params[key]()}>`);
                err.name = `request: endpoint ${endpoint}:${usedType}`;
                throw err;
            }
        });

        paramsList = newParamsList

    }
    /* validate request body */
    if (info[usedType].body) {
        if (typeof body !== typeof info[usedType].body()) {
            err = new Error(`Invalid body type <${typeof body}> expected @body <${typeof info[usedType].body()}>`);
            err.name = `request: endpoint ${endpoint}:${usedType}`;
            throw err;
        }
    }
    /* build url */
    const { protocol, domain, port, base } = state;
    const url = buildUrl({ protocol, domain, port, base, endpoint, paramsList, params });
    /* set request headers & potential request body */
    const fetchOptions = {
        method: usedType.toUpperCase(),
        headers: {
            //TODO: remove content type when not necessary?
            'Content-Type': info[usedType].contentType,
        }
    };
    if (body) {
        fetchOptions.body = body;
    }
    return new Promise(async (resolve, reject) => {
        try {
            const response = await fetch(url, fetchOptions);
            if (response.status === 403) {
                throw '403';
            }
            const text = await response.text();
            resolve(text);
        } catch (err) {
            reject(err);
        }
    });
}

export default { state, mutations, request };