<script>
import Button from '../components/Button.vue'
import Checkbox from '../components/Checkbox.vue'
import Textbox from '../components/Textbox.vue'
import SetStorageSize from '../components/SetStorageSize.vue'

export default {
  name: 'HostSetup',
  components: {
    Button,
    Checkbox,
    Textbox,
    SetStorageSize,
  },
  props: {
    storageSpace: {
      type:Object,
      default:function() {
        return {
          minimum: {
            type: Number,
            required: false,
            default:0
          },
          used: {
            type:Number,
            required:true,
            default:0
          },
          cap: {
            type:Number,
            required:true,
            default:0
          },
          total: {
            type:Number,
            required:true,
            default:1000
          }
        }
      }
    },
    storageAnnounce: {
      type: Function,
      default: () => {}
    },
    setInitializationFlag: {
      type: Function,
      default: () => {}
    },
    updateHostSetupValues: {
      type: Function,
      default: () => {}
    },
    chooseOtherFolder: {
      type: Function,
      default: () => {}
    },
    hostSetupValues: {
      type: Object,
    }
  },
  data() {
    return {
        steps:['1', '2', '3'],
        //start at step 2, hiding TOS page for the time being
        current:2,
        hasAgreed:false,
        scrolledToBottom:false,
        selectedStorageValue: this.storageSpace.cap
    }
  },
  computed: {
    isDisabled() {
      return this.selectedStorageValue < this.storageSpace.minimum
    }
  },
  methods: {
    handleScrolled(arg) {
      this.scrolledToBottom = arg;
    },
    handleCommitClick() {
      this.updateHostSetupValues({size: this.selectedStorageValue})
      this.nextStep()
    },
    handleSetupComplete() {
      let {path, size} = this.hostSetupValues

      this.newFolderPromise(path, size)
      .then(() => {
        this.storageAnnounce(size)
      })

      this.setInitializationFlag()
      .then(() => {
        this.$router.push('/host')
      })
    },
    newFolderPromise(path, size) {
      return new Promise((resolve) => {

        if(path === null) {
          return resolve()
        }
        else {
          return this.chooseOtherFolder({path: path, size: size, suppressNotification: true})
          .then(() => {
            return resolve()
          })
        }

      })
    },
    nextStep() {
      this.current += 1
    },
    prevStep() {
      this.current -= 1
    }
  },
  watch:{
    storageSpace : function(){
      this.selectedStorageValue = this.storageSpace.cap
    }
  },
  created() {
    this.storageAnnounce(32)
  }
}
</script>

<template>
  <div class="host-setup">

      <!-- TODO: remove old TOS page -->
      <div class="host-setup-1" v-if="false">
        <p class="header">Begin Hosting</p>
        <p class="description">
          Please read, scroll all the way down, and agree to our terms for hosting before clicking next.
        </p>
        <Textbox @scrolled="handleScrolled">
          <p>
            BitTorrent File System (BTFS) is both a protocol and network implementation that provides a content-addressable, peer-to-peer mechanism for storing and sharing digital content in a decentralized file system.
          </p>
          <br/>        
          <p>
            BTFS represents the first scalable implementation of a decentralized storage system that is compatible with blockchain DApps. Coupled with BTT, the long term vision for BTFS is to provide an incentivized file sharing ecosystem. BTFS represents the first scalable implementation of a decentralized storage system that is compatible with blockchain DApps. Coupled with BTT, the long term vision for BTFS is to provide an incentivized file sharing ecosystem.
          </p>
          <br/>
          <p>
            Learn more here.
          </p>
        </Textbox>
        <div class="checkbox-container">
           <Checkbox @change="hasAgreed = $event" :isDisabled="!scrolledToBottom">
            <template v-slot:right>
               <span>I agree to the terms and conditions of BTFS.</span>
            </template>
           </Checkbox> 
        </div>
        <div class="button-container">
            <Button :isDisabled="!hasAgreed || !scrolledToBottom" >Next</Button>
        </div>
      </div>

      <div class="host-setup-2" v-if="current === 2">
        <p class="header">{{$t('Host storage size')}}</p>
        <p class="description">
          {{$t('32 GB is the minimum host storage commitment size. Try moving the slider below to see how much more you can earn by increasing your commitment size.')}}
        </p>
        <div class="storage-size-container">
          <SetStorageSize 
            :storageSpace="storageSpace" 
            :isDisabled="false" 
            @slideChange="selectedStorageValue = $event"/>
        </div>
        <div class="button-container">
            <Button :isDisabled="isDisabled" :cb="handleCommitClick">{{$t('Commit Storage')}}</Button>
        </div>
      </div>

      <div class="host-setup-3" v-if="current === 3">
        <p class="header">{{$t('How to be a good host')}}</p>
        <div class="description">
          <ul class="list">
            <li>
              {{$t('Leave your computer and BTFS running at all times.')}}
            </li>
            <li>
              {{$t('Make sure your computer is plugged in a power source and connected to the internet.')}}
            </li>
            <li>
              {{$t('Do not manipulate the host folder on your computer.')}}
            </li>
            <li>
              {{$t('Keep calm and earn BTT!')}}
            </li>
          </ul>
        </div >
        <div class="button-container">
            <Button :cb="handleSetupComplete">{{$t('Agree & Host')}}</Button>
        </div>
      </div>

  </div>
</template>

