import Vue from 'vue';
import routes from './routes';
const { request } = routes;

const state = {
    info: {
        storage_price_ask: 7680,
        storage_price_default: 7680,
        customized_pricing: false
    },
    value: {
        host_stats: {
            online: false,
            uptime: 0,
            score: 0,
            storage_used: 0,
            storage_cap: 0,
            storage_disk_total:0,
            storage_disk_available:0,
            uptime_score: 0,
            age_score: 0,
            version_score: 0,
            speed_score: 0,
            uptime_weight: 0,
            age_weight: 0,
            version_weight: 0,
            speed_weight: 0


        },
        renter_stats: {

        }
    },
    contracts: {
        "active_contract_num":0,
        "compensation_paid":0,
        "compensation_outstanding":0,
        "first_contract_start":"0001-01-01T00:00:00Z",
        "last_contract_end":"0001-01-01T00:00:00Z",
        "role":0
    },
    contractsList: {
        "contracts":[]
    },
    setup: {
        path: null,
        size: null
    },
    isSynced: false,
    isSyncedLocal: false,
    isImporting: false,
    proposedStorageAvailable: null,
    isStorageLoading: true,
    statsList: [],
};
const mutations = {
    setInfo(s, stats) {
        Vue.set(s, 'info', stats);
    },
    setStatsInfo(s, stats) {
        Vue.set(s, 'value', stats);
    },
    setStatsList(s, list) {
        Vue.set(s, 'statsList', list);
    },
    setPathList(s, list) {
        Vue.set(s, 'pathList', list);
    },
    setPathPartitions(s, partitions) {
        Vue.set(s, 'partitionsData', partitions);
    },
    setContractsInfo(s, stats) {
        let formattedStats = stats
        formattedStats.compensation_outstanding = formattedStats.compensation_outstanding/1000000
        formattedStats.compensation_paid = formattedStats.compensation_paid/1000000
        Vue.set(s, 'contracts', formattedStats);
    },
    setContractsList(s, stats) {
        Vue.set(s, 'contractsList', stats);
    },
    setSetupValues(s, options) {
        Vue.set(s, 'setup', {...s.setup, ...options})
    },
    setIsSynced(s, val) {
        Vue.set(s, 'isSynced', val)
    },
    setIsSyncedLocal(s, val) {
        Vue.set(s, 'isSyncedLocal', val)
    },
    setIsStorageLoading(s, val) {
        Vue.set(s, 'isStorageLoading', val)
    },
    setIsImporting(s, val) {
        Vue.set(s, 'isImporting', val)
    },
    setProposedStorageAvailable(s, val) {
        Vue.set(s, 'proposedStorageAvailable', val)
    }
};
const getters = {
    statsInfo(s) {
        return s.value;
    },
    contractInfo(s) {
        return s.value;
    },
    isCustomizedPricing(s) {
        return s.info.customized_pricing
    },
    storagePriceAsk(s) {
        return s.info.storage_price_ask
    },
    storagePriceDefault(s) {
        return s.info.storage_price_default
    }
};
const actions = {
    storageInfo(context) {
        return request({
            endpoint: 'v1/storage/info',
            method: 'POST',
        }).then((stats) => {
            let data = JSON.parse(stats)

            //convert btt/day/GB to btt/month/TB
            let price = Math.round(data.storage_price_ask/1000000*30*1024)
            let priceDefault = Math.round(data.storage_price_default/1000000*30*1024)

            data.storage_price_ask = price
            data.storage_price_default = priceDefault
            data.customized_pricing = (data.customized_pricing) ? true : false
            context.commit('setInfo', data);
        });
    },
    storagePathList(context, options) {
        let regexTrailingPathElement = /([\/\\])[^\/\\]+[\/\\]?$/  //eslint-disable-line
        let regexDriveLetter = /^[a-zA-Z]:/ //eslint-disable-line
        let trailingPathElement = options.path.match(regexTrailingPathElement)

        let driveLetter = options.path.match(regexDriveLetter)
        let root = (driveLetter) ? driveLetter : ''

        // get appropriate slash type ('\' for windows, '/' for mac and linux)
        // to append to base path if base path is root
        let slashType = (trailingPathElement === null) ? '' : trailingPathElement[1]
        let basePath = (slashType === '') ? (driveLetter) ? `${driveLetter}\\`: '/' : options.path

        let parentPath = (slashType === '') ? undefined : basePath.replace(regexTrailingPathElement,'')
        let top = false
        if(parentPath === root) {
            parentPath = (driveLetter) ? `${driveLetter}\\`: '/'
        }
        if(slashType === '' || (options.path === getRoot(basePath))) {
            top = true
        }


        return request({
            endpoint: 'v1/storage/path/list',
            method: 'POST',
            params: {
                arg: encodeURIComponent(basePath)
            }
        }).then((stats) => {
            let data = JSON.parse(stats)
            let folderData = {
                subfolders: data.Strings.map((subfolder) => {
                    let subPath = basePath.endsWith(slashType) ? `${subfolder}` : `${slashType}${subfolder}`
                    subPath = basePath + subPath
                    return {
                        name: subfolder,
                        read: true,
                        write: true,
                        path: subPath,
                    }
                }),
                subfiles: [],
                meta: {
                    parent: parentPath,
                    top: top,
                    item_path: basePath,
                    drive: {
                      name: "meta.drive.name",
                      root: getRoot(basePath)
                    }
                }
            }
            return folderData
        });
    },
    storagePathPartitions() {
        return request({
            endpoint: 'v1/storage/path/volumes',
            method: 'POST'
        })
        .then((res) => {
            let data = JSON.parse(res)
            let regexDriveLetterNoTrailingSlash = /^[a-zA-Z]:(?!\\)/ //eslint-disable-line
            let partitionsData = data.map((x) => {

                let isDriveLetterNoTrailingSlash = x.name.match(regexDriveLetterNoTrailingSlash)
                let name = (isDriveLetterNoTrailingSlash) ? `${x.name}\\` : x.name
                let mountPoint = (isDriveLetterNoTrailingSlash) ? `${x.mount_point}\\` : x.mount_point
                if(name === "Macintosh HD") {
                    mountPoint = "/"
                }
                return {
                    name: name,
                    path: mountPoint,
                    root: mountPoint
                }
            })
            // filter /private/var/vm - macOS swap and sleep image files
            // filter other unneeded mac mounts
            let pathBlacklist = ['/private/var/vm', '/home', '/net', '/dev', '/volumes/recovery']
            partitionsData = partitionsData.filter(item => {
                let isBlacklisted = false
                pathBlacklist.forEach((blacklistString) => {
                    if(item.path.toLowerCase().startsWith(blacklistString)){
                        isBlacklisted = true
                    }
                })
                return !isBlacklisted
            })
            partitionsData = {drives : partitionsData}
            return partitionsData
        })
    },
    storageStatsInfo(context) {
        let l = context.rootState.config.host.initialized ? false : true
        let reqObj = {
            endpoint: 'v1/storage/stats/info',
            method: 'POST',
        }
        reqObj.params = {
            'l': l
        }
        return request(reqObj)
        .then((stats) => {
            let data = JSON.parse(stats)
            if(data.host_stats){
                context.commit('setStatsInfo', data)

                //if not synced and storageStatsInfo success
                if(!l) {
                    context.commit('setIsSyncedLocal', true)
                }
            }
            context.commit('setIsStorageLoading', false)
        })
    },
    storageStatsList(context, options) {
        let from = (options.from/1000).toFixed(0)
        let to = (options.to/1000).toFixed(0)
        return request({
            endpoint: 'v1/storage/stats/list',
            method: 'POST',
            params: {
                arg:`${from}&arg=${to}`
            }
        })
        .then((stats) => {
            let data = JSON.parse(stats)
            data.forEach((x, index, currArray) => {
                currArray[index].timestamp = x.timestamp * 1000
            }, data)

            let filterDateTracker = []
            let filteredData = data.filter((x) => {
                let d = new Date(x.timestamp)
                d = d.toLocaleDateString()

                if(!filterDateTracker.includes(d)){
                    filterDateTracker.push(d)
                    return x
                }

            })
            let slicedData = filteredData.slice(0,options.numDays)
            context.commit('setStatsList', slicedData)
        })
    },
    storageStatsSync() {
        return request({
            endpoint: 'v1/storage/stats/sync',
            method: 'POST',
        }).
        then((res) => {
            //return sync success or fail
            //empty response is a success
            return (res === "") ? true : false 

        })
    },
    storageAnnounce(context, options) {
        return request({
            endpoint: 'v1/storage/announce',
            method: 'POST',
            params: {
                'host-storage-price': null,
                'host-storage-max': options['hostStorageMax'],
                'scp': null
            }
        }).then(() => {
            // console.log('announcing ->', options['hostStorageMax'], options['hostStorageMax']*Math.pow(1000, 3))
            return context.dispatch('storageStatsInfo')
        })
    },
    storageAnnounceDisableCustomizedPricing(context) {
        return request({
            endpoint: 'v1/storage/announce',
            method: 'POST',
            params: {
                'host-storage-price': null,
                'host-storage-max': null,
                'scp': false
            }
        }).then(() => {
            return context.dispatch('storageInfo')
        })
    },
    storageAnnouncePrice(context, options) {
        let price = Math.round(options['hostStoragePrice']*1000000/30/1024)
        return request({
            endpoint: 'v1/storage/announce',
            method: 'POST',
            params: {
                'host-storage-price': price,
                'host-storage-max': null,
                'scp': null
            }
        }).then(() => {
            return context.dispatch('storageInfo')
        })
    },
    storageContractsInfo(context) {
        return request({
            endpoint: 'v1/storage/contracts/stat',
            method: 'POST',
            params: {
                role: 'host'
            }
        }).then((stats) => {
            // console.log('storage/contract/stat\n', JSON.parse(stats))
            context.commit('setContractsInfo', JSON.parse(stats));
        });
    },
    storageContractsSync() {
        return request({
            endpoint: 'v1/storage/contracts/sync',
            method: 'POST',
            params: {
                role: 'host'
            }
        })
        .then((res) => {
            //return sync success or fail
            //empty response is a success
            return (res === "") ? true : false 
        })
    },
    storageContractsList(context) {
        return request({
            endpoint: 'v1/storage/contracts/list',
            method: 'POST',
            params: {
                role: 'host',
                order:"escrow_time,asc",
                status:"active",
                size:20,
            }
        }).then((stats) => {
            context.commit('setContractsList', JSON.parse(stats));
        });
    },
    appReset() {

        return new Promise((resolve) => {

            let interval = setInterval(() => {

                request({
                    endpoint: 'v1/storage/path/status',
                    method: 'POST',

                })
                .then(() => {
                    clearInterval(interval)
                    return resolve()
                })
                .catch(() => {
                })

            }, 5000)
        })

    },
    storagePath(context, options) {
        if(options.suppressNotification !== true){
            context.dispatch('notificationInProgress', 'host')
        }
        else{
            context.commit('setIsImporting', true)
        }
        return request({
            endpoint: 'v1/storage/path',
            method: 'POST',
            params: {
                arg: `${options.path}&arg=${options.size}`
            }
        })
        .then(() => {
            return handleReset(context, options.suppressNotification)
        })
        .catch(() => {
            // hack to handle btfs empty response
            // btfs should return 200 before it resets
            return handleReset(context, options.suppressNotification)
        })

        function handleReset(context, suppressNotification) {
            return context.dispatch('appReset')
            .then(() => {
                context.dispatch('metaRepoStat')

                if(suppressNotification !== true){
                    context.dispatch('notificationComplete', 'host')
                }
                else{
                    context.commit('setIsImporting', false)
                }
            })
        }
    },
    storagePathCapacity(context, options) {
        return request({
            endpoint: 'v1/storage/path/capacity',
            method: 'POST',
            params: {
                arg: encodeURIComponent(options.path)
            }
        })
        .then((stats) => {
            let data = JSON.parse(stats)
            let freeSpaceGB = bToGB(data.FreeSpace)
            if(data.Type === "error") {
                //{"Message":"no such file or directory","Code":0,"Type":"error"}
                return Promise.reject(data.Message)

            }
            else if(freeSpaceGB < 32 || !data.Valid ) {
                // if not valid or .btfs dir already exists
                let errorMessage = 'You do not have enough space on this hard drive.'
                return Promise.reject(errorMessage)

            }

            context.dispatch('updateProposedStorageAvailable', data.FreeSpace)
            return Promise.resolve('[storagePathCapacity] pass')
        })
        .catch((e) => {
            return Promise.reject(e)
        })
    },
    updateHostSetupValues(context, options) {
        context.commit('setSetupValues', options)
    },
    updateIsSynced(context, val) {
        context.commit('setIsSynced', val)
    },
    updateProposedStorageAvailable(context, val) {
        context.commit('setProposedStorageAvailable', val)
    }
};

function bToGB(b) {
  let gb = b/Math.pow(1000, 3)
  gb = Math.floor(gb)
  return gb
}

function getRoot(path) {
    //mac: /volumes/drive-name
    //linux: /media/drive-name

    let isExternalDriveRegex = /^(\/(media|Volumes|home|net|dev))/ //eslint-disable-line
    let regexDriveLetter = /^[a-zA-Z]:/ //eslint-disable-line

    let isExternalDrive = path.match(isExternalDriveRegex)

    if(isExternalDrive) {
        return path
    }

    let driveLetter = path.match(regexDriveLetter)
    
    return (driveLetter) ? `${driveLetter}\\`: '/'
}

export default {
    state,
    getters,
    mutations,
    actions,
};