<script>
import { mapGetters } from 'vuex'

export default {
  name: 'Banner',
  props: {
  },
  data() {
    return {
      bannerData: [],
      index: 0,
      showBanner: false
    }
  },
  computed: mapGetters({
    locale: 'locale'
  }),
  methods: {
    next() {
      this.index = (this.index + 1) % (this.bannerData.length)
    },
    previous() {
      this.index = (this.index - 1 < 0) ? this.bannerData.length - 1 : this.index - 1
    },
    toggleBanner() {
      this.showBanner = !this.showBanner
      this.$emit('showBanner', this.showBanner)

    },
    loadBannerData() {
      let bannerXML = (process.env.NODE_ENV === "development") ? '/banner.xml' : '/hostui/banner.xml'
      return this.axios.get(bannerXML).then((response) => {
        let parser = new DOMParser();
        let xmlDoc = parser.parseFromString(response.data,"text/xml");
        let bannerList = xmlDoc.getElementsByTagName("content")
        this.bannerData = []
        bannerList.forEach((content)=> {
          let htmlContent = content.childNodes[1].nodeValue.trim()
          this.bannerData.push(this.$t(htmlContent))
        })
      })
      .catch(() => {

      })
    }
  },
  created() {
    this.loadBannerData()
    .then(() => {
      this.toggleBanner()
    })

  },
  watch:{
    locale(){
      this.loadBannerData()
    }
  }
};
</script>

<template>
  <div class="banner" v-if="bannerData.length > 0 && showBanner === true">
    <div class="arrow-container" :class="{'hidden': bannerData.length === 1}">
      <div class="previous" @click="previous">
        <svg-icon icon="arrow-back" class="arrow-icon back" :hasFill="true"/>
       {{$t('Previous')}}
      </div>
      <div class="next" @click="next">
        {{$t('Next')}}
        <svg-icon icon="arrow-forward" class="arrow-icon forward" :hasFill="true"/>
      </div>
    </div>
    <div class="content" v-html="bannerData[index]">
    </div>
    <svg-icon icon="close" class="close-icon" :hasFill="true" @click="toggleBanner"/>
  </div>
</template>

<style lang="scss" scoped>

.banner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 48px;
  background: $primary-blue-dark;
  color: $neutral-white;
  flex-shrink: 0;
  padding: 0 24px;

  .arrow-container {
    display: flex;

    .previous, .next {
      display: flex;
      align-items: center;
      font-size: 14px;
      line-height: 16px;
      cursor : pointer;

      .arrow-icon {
        fill: $neutral-white;

        &.back {
          margin: 0 6px 0 0;
        }

        &.forward {
          margin: 0 0 0 6px;
        }
      }
    }

    &.hidden {
      visibility: hidden;
    }

    .next {
      margin: 0 0 0 24px;
    }
  } 

  .close-icon {
    fill: $neutral-white;
    font-size: 20px;
    cursor: pointer;
  }

  .content {
    /deep/ a {
      color: $accents-yellow-medium;
      margin: 0 0 0 16px;
    }
  } 
  
}

</style>