import Vue from 'vue';
import routes from './routes';
import utils from '../../utils'
const { request } = routes;

const state = {
    balance: {
        BtfsWalletBalance: NaN,
        BttWalletBalance: NaN
    },
    transactions: [],
  keys: {
    PrivateKey: '',
    Mnemonic:[]
  },
  dropdownSync:null,
  auth: null,
  isSpeedWalletRunning: false,
  speedWalletKey: ''
};
const mutations = {
    setBalance(s, balance) {
        let formattedBalance = {
            BtfsWalletBalance: balance.BtfsWalletBalance / 1000000,
            BttWalletBalance: balance.BttWalletBalance / 1000000
        }
        Vue.set(s, 'balance', formattedBalance)
    },
    setKeys(s, options) {
        Vue.set(s, 'keys', {PrivateKey: options.PrivateKey, Mnemonic: options.Mnemonic});
    },
    setTransactions(s, transactions) {
        let formattedTransactions = transactions.map((x, index) => {
        let d = new Date(x.time_create)

            return {
                id: index,
                created: d,
                amount: x.amount/1000000,
                type: 'Deposit',
                status: x.status,
                from: x.from,
                to: x.to

            }
        })
        formattedTransactions.sort(function(a,b){
          // Turn your strings into dates, and then subtract them
          // to get a value that is either negative, positive, or zero.
          return new Date(b.created) - new Date(a.created);
        });

        Vue.set(s, 'transactions', formattedTransactions);
    },
    setDropdownSync(s, val) {
        Vue.set(s, 'dropdownSync', val);
    },
    setAuth(s, val) {
        Vue.set(s, 'auth', val);
    },
    setSpeedWalletKey(s, speedWalletKey) {
        Vue.set(s, 'speedWalletKey', speedWalletKey)
        if(speedWalletKey !== null){
            Vue.set(s, 'isSpeedWalletRunning', true)
        }
        else {
            Vue.set(s, 'isSpeedWalletRunning', false)
        }

    }
};
const getters = {
    balance(s) {
        return s.balance
    },
    recoveryPhrase(s) {
        return s.recoveryPhrase;
    },
    transactions(s) {
        return s.transactions
    },
    dropdownSync(s) {
        return s.dropdownSync
    },
    auth(s) {
        return s.auth
    }
};
const actions = {
    walletBalance(context) {
        return request({
            endpoint: 'v1/wallet/balance',
            method: 'POST',
            
        }).then((res) => {
            let data = JSON.parse(res)
            context.commit('setBalance', data);
        });
    },
    walletPassword(context, options) {
        return request({
            endpoint: 'v1/wallet/password',
            method: 'POST',
            params: {
                arg: options.password,
            }
            
        })
    },
    walletKeys(context) {
        return request({
            endpoint: 'v1/wallet/keys',
            method: 'POST',
        }).then((res) => {
            let data = JSON.parse(res)
            let mnemonic = data.Mnemonic
            let formattedMnemonic = (mnemonic === "") ? [] : mnemonic.split(' ')
            let formattedKeys = {
                PrivateKey: data.PrivateKey,
                Mnemonic: formattedMnemonic
            }
            context.commit('setKeys', formattedKeys);
        });
    },
    walletTransactions(context) {
        return request({
            endpoint: 'v1/wallet/transactions',
            method: 'POST',
            
        }).then((res) => {
            let data = JSON.parse(res)
            context.commit('setTransactions', data);
        });
    },
    walletDeposit(context, options) {
        return request({
            endpoint: 'v1/wallet/deposit',
            method: 'POST',
            params: {
                arg: options.amount*1000000,
                a:'',
                p: context.state.auth
            }
        }).then((res) => {
            context.dispatch('walletTransactions')
            context.dispatch('walletBalance')
            let data = JSON.parse(res)
            if ("Type" in data) {
                if (data.Type === "error") {
                    return 'failed'
                }
            }
            return 'success'
        });
    },
    walletWithdraw(context, options) {
        return request({
            endpoint: 'v1/wallet/withdraw',
            method: 'POST',
            params: {
                arg: options.amount*1000000,
                p: context.state.auth
            }
        }).then((res) => {
            context.dispatch('walletTransactions')
            context.dispatch('walletBalance')
            let data = JSON.parse(res)
            if ("Type" in data) {
                if (data.Type === "error") {
                    return 'failed'
                }
            }
            return 'success'
        })
    },
    walletTransfer(context, options) {
        let amount = options.amount*1000000
        let address = encodeURIComponent(options.address)
        return request({
            endpoint: 'v1/wallet/transfer',
            method: 'POST',
            params: {
                arg: `${address}&arg=${amount}`,
                p: context.state.auth
            }
        }).then((res) => {
            context.dispatch('walletTransactions')
            context.dispatch('walletBalance')
            let data = JSON.parse(res)
            if ("Type" in data) {
                if (data.Type === "error") {
                    return 'failed'
                }
            }
            return 'success'
        })
    },
    walletCheckPassword(context, options) {
        return request({
            endpoint: 'v1/wallet/keys',
            method: 'POST',
        }).then((res) => {
            let data = JSON.parse(res)
            let mnemonic = utils.decrypt_with_aes(options.password, data.Mnemonic)
            let privKey = utils.decrypt_with_aes(options.password, data.PrivateKey)
            return {mnemonic: mnemonic, privateKey: privKey}
        })
        .catch(() => {
            return Promise.reject()
        })
    },
    walletImport(context, options) {
        let params = (options.type === 'p') ? {p:encodeURIComponent(options.value), m:''} : {p:'', m:options.value.replace(/\s+/g, '+').toLowerCase()}
        return request({
            endpoint: 'v1/wallet/import',
            method: 'POST',
            params: params
        }).then((res) => {
            if(res !== ''){
                let data = JSON.parse(res)
                if ("Type" in data) {
                    if (data.Type === "error") {
                        return Promise.reject()
                    }
                }
            }
            return context.dispatch('appReset')
            
        })
        .catch(() => {
            return Promise.reject()
        })
    },
    updateDropdownSync(context, options) {
        var dropdownSync = context.state.dropdownSync
        if( dropdownSync === options){
            context.commit('setDropdownSync', null)
        }
        else {
            context.commit('setDropdownSync', options)
        }
    },
    walletAuth(context, options) {
        context.commit('setAuth', options)
    },
    walletDiscovery(context, options) {
        return request({
            endpoint: 'v1/wallet/discovery',
            method: 'POST',
            params: options
        }).then((res) => {
            let data = JSON.parse(res)
            let key = null
            // If data.Key DNE -> speed not running
            // If data.Key == 'CAISAA==' -> wrong speed wallet password
            if(!data.Key) {
                context.commit('setSpeedWalletKey', key)
                return Promise.reject('speed not running')
            }
            else if(data.Key && data.Key === 'CAISAA==' && options.p !== ''){
                context.commit('setSpeedWalletKey', '')
                return Promise.reject('invalid speed password')
            }
            else{
                key = data.Key
                context.commit('setSpeedWalletKey', key)
            }
        })
    },
    walletGenerateKey(context) {
        return request({
            endpoint: 'v1/wallet/generate_key',
            method: 'POST',
        }).then((res) => {
            let data = JSON.parse(res)
            let mnemonic = data.Mnemonic
            let formattedMnemonic = (mnemonic === "") ? [] : mnemonic.split(' ')
            let formattedKeys = {
                PrivateKey: data.PrivateKey,
                Mnemonic: formattedMnemonic
            }
            context.commit('setKeys', formattedKeys);
        });
    },
    walletInit(context, options) {
        let password = options.password
        let privateKey = encodeURIComponent(context.state.keys.PrivateKey)
        let mnemonic = context.state.keys.Mnemonic.join(' ')
        let privateKeyCipher = encodeURIComponent(utils.encrypt_with_aes(password, privateKey))
        let mnemonicCipher = encodeURIComponent(utils.encrypt_with_aes(password, mnemonic))

        let arg = `${privateKey}&arg=${privateKeyCipher}&arg=${mnemonicCipher}`

        return request({
            endpoint: 'v1/wallet/init',
            method: 'POST',
            params: {
                arg:arg
            }
        }).then(() => {
            return context.dispatch('appReset')
        })
        .catch((e) => {
            return Promise.reject(e)
        })

    }
};

export default {
    state,
    getters,
    mutations,
    actions,
};